import { FC, MouseEvent, useMemo } from 'react';
import classNames from 'classnames';

// Components
import { MenuItem } from '../../../Menu';
import Typography from '../../../Typography';
import Icon, { IconProps } from '../../../Icon';

// Types
import { TestMetadata } from 'types';

// Styles
import './SideNavItem.scss';

export interface SideNavItemProps {
  /** name of item */
  name: string;
  /** text to display */
  text: string;
  /** whether item is active */
  isActive?: boolean;
  /** whether item should change style when hovered */
  isHoverable?: boolean;
  /** Can choose to only show item for specific sizes */
  visibleOn?: {
    size: 'slim' | 'small' | 'medium' | 'large' | 'wide';
    direction: 'up' | 'down';
  };
  /** Icon to display */
  iconProps?: IconProps;
  /** click event */
  onClick?: (e: MouseEvent<HTMLLIElement>) => void;
  /** classname */
  className?: string;
  /** Sets test id */
  testMetadata?: {
    link?: TestMetadata;
    icon?: TestMetadata;
  };
}

const SideNavItem: FC<SideNavItemProps> = ({
  name,
  text,
  isActive = false,
  isHoverable = true,
  visibleOn,
  iconProps,
  onClick,
  className,
  children,
  testMetadata,
}) => {
  const SideNavItemClassNames = useMemo(
    () =>
      classNames(
        'sb-cp-side-nav-item',
        visibleOn &&
          `sb-cp-side-nav-item__visible-on-${visibleOn.size}-${visibleOn.direction}`,
        className,
      ),
    [className, visibleOn],
  );

  return (
    <MenuItem
      key={name}
      isActive={isActive}
      className={SideNavItemClassNames}
      onClick={onClick}
    >
      {children ? (
        children
      ) : (
        <span
          className={classNames('sb-cp-side-nav-item__container', {
            'sb-cp-side-nav-item__container_is-selected': isActive,
          })}
        >
          <a className="sb-cp-side-nav-item__link" {...testMetadata?.link}>
            {iconProps && (
              <Icon
                {...iconProps}
                {...testMetadata?.icon}
                className={classNames('sb-cp-side-nav-item__icon', {
                  'sb-cp-side-nav-item__icon_is-selected': isActive,
                  'sb-cp-side-nav-item__icon_is-hoverable': isHoverable,
                })}
              />
            )}
            <Typography variant="T05" className="sb-cp-side-nav-item__text">
              {text}
            </Typography>
          </a>
        </span>
      )}
    </MenuItem>
  );
};

export default SideNavItem;
