import React, {
  ComponentProps,
  createRef,
  ReactElement,
  useCallback,
  useMemo,
  useState,
} from 'react';
import classNames from 'classnames';

// Components
import SearchResultsTemplate from './SearchResultsTemplate/SearchResultsTemplate';
import Search, { SearchProps } from '../../../Search';
import Input from '../../../Input';

// Styles
import './SearchTypeaheadTemplate.scss';

// Hooks
import { useClickOutside } from '@dtci/triton/hooks';

// Types
import { BadgeProps } from '../../../Badge';

export type SearchTypeaheadTemplateProps<T extends SearchResultBase> = {
  /** Container class name */
  className?: string;
  /** The data to fill search results list */
  searchResults?: T[];
  /** Event to fire when search changes */
  onChange: (query: string) => void;
  /** Event to fire when result selected */
  onSelect: (selectedResult: T) => void;
  /** Event to fire when result selected */
  onViewMoreClick?: () => void;
  /** Search/Input value */
  value: string;
  /** Additional input props for Search, based on the Search Component */
  searchProps?: Partial<SearchProps>;
  /** Additional input props for Input, based on the inputComponent */
  inputProps?: ComponentProps<typeof Input>;
  /** Additional badge props */
  badgeProps?: BadgeProps;
  /** Renders a loader */
  isLoading?: boolean;
};

export type SearchResultBase = {
  name: string;
  id: string;
  metadata?: string[];
};

type SearchTypeahead = <T extends SearchResultBase>(
  p: SearchTypeaheadTemplateProps<T>,
) => ReactElement<SearchTypeaheadTemplateProps<T>>;

const SearchTypeaheadTemplate: SearchTypeahead = ({
  searchResults = [],
  className,
  onChange,
  onSelect,
  onViewMoreClick,
  value,
  inputProps,
  badgeProps,
  isLoading,
}) => {
  const [isFocused, setIsFocused] = useState(false);
  const searchRef = createRef<HTMLDivElement>();

  const searchInputProps = useMemo(
    () => ({
      id: 'searchInput',
      autoComplete: 'off',
      'data-testid': 'searchTypeaheadInput',
      onFocus: () => {
        setIsFocused(true);
      },
      ...inputProps,
      onKeyUp: (e: React.KeyboardEvent<HTMLInputElement>) => {
        // If the user presses enter, we want to close the search results
        if (e.key == 'Enter' || e.keyCode == 13) {
          setIsFocused(false);
        }
        inputProps?.onKeyUp?.(e);
      },
    }),
    [inputProps],
  );

  const handleChange = useCallback(
    (value: string): void => {
      onChange(value);
      if (value.length >= 3) setIsFocused(true);
      else setIsFocused(false);
    },
    [onChange, setIsFocused],
  );

  // We check to see if the user has acted outside the general search area
  // (replaces onBlur, which fires before onClick on elements inside the area)
  useClickOutside(
    () => {
      setIsFocused(false);
    },
    searchRef,
    [setIsFocused],
  );

  const onSelectHandler: typeof onSelect = (selectedResult): void => {
    setIsFocused(false);
    onSelect(selectedResult);
  };

  return (
    <div
      className={classNames('sb-cp-search-typeahead-container', className)}
      ref={searchRef}
    >
      <Search
        inputProps={searchInputProps}
        width="auto"
        value={value}
        onChange={handleChange}
        hasIcon={false}
        showCancelBtn={false}
        className={classNames('sb-cp-search-typeahead')}
      />
      {isFocused && (
        <SearchResultsTemplate
          results={searchResults}
          onSelect={onSelectHandler}
          onViewMoreClick={() => {
            setIsFocused(false);
            onViewMoreClick?.();
          }}
          headerLabel="Top Results"
          footerLabel="View All Search Results"
          badgeProps={badgeProps}
          isLoading={isLoading}
        />
      )}
    </div>
  );
};

export default SearchTypeaheadTemplate;
