import {
  FC,
  SyntheticEvent,
  useCallback,
  useEffect,
  useMemo,
  useRef,
  useState,
} from 'react';
import classNames from 'classnames';

// Components
import {
  DataUtilityWrapper,
  DataUtilityBar,
} from '../Templates/DataUtilityBar';
import PopoverOverlayTemplate from '../Templates/PopoverOverlayTemplate';
import StandardTable, {
  DataProps,
  MasterDetailProps,
  StandardTableCommonProps,
} from '../StandardTable';
import Button, { ButtonProps } from '../../Button';
import PopoverButton from '../../PopoverButton';
import MenuList from '../../MenuList';
import Typography from '../../Typography';

// Utils
import { sortDirectionToColumnStateSort } from './entityGrid.utils';

// Types
import { Column } from '../StandardTable/types';
import {
  ActionState,
  ActionType,
  DataUtilityWrapperConfig,
  Option,
  TestMetadata,
} from '../../types';
import {
  ColumnApi,
  ColumnState,
  FilterChangedEvent,
  GridApi,
  GridReadyEvent,
} from 'ag-grid-community';

// Styles
import './EntityGrid.scss';

export type CustomGridAction = ButtonProps & {
  onClick: (e: SyntheticEvent, gridApi?: GridApi) => void;
};

export interface EntityGridProps {
  // Triggers when grid api has loaded
  onGridReady?: (params: GridReadyEvent) => void;
  // Filter has been modified and applied.
  onFilterChanged?: (event: FilterChangedEvent) => void;
  // Title of the grid
  title?: string;
  // Entity Name
  entityName?: string;
  // Utility Bar Config
  utility: Pick<DataUtilityWrapperConfig, 'dataDefinition'>;
  // Custom Buttons
  customActions?: CustomGridAction[];
  // Table config
  table: Pick<
    StandardTableCommonProps,
    | 'options'
    | 'selectable'
    | 'dataGridApiRef'
    | 'dataGridColumnApiRef'
    | 'onGridReady'
    | 'onFirstDataRendered'
    | 'moveableColumns'
  > &
    DataProps &
    MasterDetailProps;
  /** Class name for bar */
  className?: string;
  // Portal for popups
  portal?: HTMLElement | null;
  // Show export options
  exportable?: boolean;
  // Export options available
  exportOptions?: Option[];
  // Trigger export
  onExport?: (type: string, gridApi?: GridApi) => void;
  // Disabled treatment for buttons while loading
  isLoading?: boolean;
  // Export treatment on button
  isExporting?: boolean;
  // is data still fetching
  isFetching?: boolean;
  // message to display if no results are found
  noResultsMessage?: string;
  // Additional items in more dropdown
  moreActions?: Option[];
  // Enabled bulk options
  bulkActions?: Option[];
  // Trigger more action
  onMoreActionSelected?: (selected: Option) => void;
  // Trigger bulk action
  onBulkActionSelected?: (
    action: Option,
    selected: Record<string, unknown>[],
    gridApi?: GridApi,
  ) => void;
  // Select ids
  ids?: {
    bulk?: string;
    sort?: string;
    display?: string;
    export?: string;
  };
  // Test metadata
  testMetadata?: {
    wrapper?: TestMetadata;
    table?: TestMetadata;
  };
}

const EntityGrid: FC<EntityGridProps> = ({
  ids,
  utility,
  className,
  table,
  portal,
  title,
  exportable,
  customActions,
  onExport,
  moreActions,
  onMoreActionSelected,
  onBulkActionSelected,
  testMetadata,
  onGridReady,
  onFilterChanged,
  exportOptions,
  entityName,
  bulkActions,
  isLoading,
  isExporting,
  isFetching,
  noResultsMessage,
}) => {
  const [selected, setSelected] = useState(0);
  const [displayCount, setDisplayCount] = useState<number>(0);
  const tableRef = useRef<GridApi | null>(null);
  const columnRef = useRef<ColumnApi | null>(null);

  const utilityBarClassNames = useMemo(
    () => classNames('sb-cp-entity-list', className),
    [className],
  );

  const onActionStateChange = useCallback(
    (type: ActionType, state: ActionState[]) => {
      const model: ColumnState[] = [];

      state.forEach((colState) => {
        colState.actionType === 'Sort' &&
          model.push({
            colId: colState.dataKey,
            sort: colState.direction
              ? sortDirectionToColumnStateSort[colState.direction]
              : undefined,
          });
        colState.actionType === 'Display' &&
          model.push({
            colId: colState.dataKey,
            hide: colState.hidden,
          });
      });

      tableRef.current?.refreshServerSide({
        purge: true,
      });

      columnRef.current?.applyColumnState({
        state: model,
        defaultState: {
          sort: null,
        },
      });
    },
    [],
  );

  const columns: Column[] = useMemo(() => {
    return utility.dataDefinition.map((definition) => {
      return {
        ...definition.column,
        definition: {
          ...definition.column?.definition,
          ...(definition.column?.filter === false && {
            menuTabs: [],
            filter: false,
          }),
          ...(definition.displayDefinition?.visible === false && {
            colId: definition.dataKey,
            hide: true,
          }),
          field: definition.field,
        },
      };
    });
  }, [utility.dataDefinition]);

  const handleExport = useCallback(
    (option: Option) => {
      if (tableRef.current) {
        onExport?.(option.value, tableRef.current);
      }
    },
    [onExport],
  );

  const onSelectionChanged = useCallback(
    (event) => {
      if (
        table?.selectable &&
        bulkActions !== undefined &&
        event.type === 'selectionChanged'
      ) {
        const api = event.api;
        const selected = api.getSelectedRows();
        setSelected(selected.length);
      }
    },
    [table, bulkActions],
  );

  const handleFilterChanged = useCallback(
    (event: FilterChangedEvent) => {
      if (onFilterChanged) {
        onFilterChanged(event);
      }

      if (table.mode === 'server') {
        return;
      }

      setDisplayCount(event.api.getDisplayedRowCount());
    },
    [onFilterChanged, table.mode],
  );

  const handleBulkAction = useCallback(
    (option) => {
      if (tableRef.current) {
        onBulkActionSelected?.(
          option,
          tableRef.current.getSelectedRows(),
          tableRef.current,
        );
      }
    },
    [onBulkActionSelected],
  );

  /**
   * Calculates the displayed records total count based on the current table.mode.
   * - For server mode: the total count is provided by the parent component through `totalCount` prop.
   * - For client mode: this component resolves the total count internally relying on ag-grid api method `getDisplayedRowCount()`.
   */
  useEffect(() => {
    const count =
      table.mode === 'server' && !isFetching
        ? table.totalCount || 0
        : table.rowData?.length || 0;

    setDisplayCount(count);
  }, [table.rowData, table.mode, table.totalCount, isFetching]);

  return (
    <DataUtilityWrapper
      className={utilityBarClassNames}
      onActionStateChange={onActionStateChange}
      dataUtilityWrapperConfig={{
        ...utility,
        ...(exportable && {
          exportOptions: exportOptions,
        }),
      }}
      onExportOptionSelected={exportable ? handleExport : undefined}
      isLoading={isLoading}
      isExporting={isExporting}
      testMetadata={testMetadata?.wrapper}
    >
      <DataUtilityBar
        entityName={entityName}
        titleIconProps={{
          iconType: 'fa',
          iconName: 'fa-list',
          size: 'smaller',
          faPackage: 'fas',
          altText: 'List Icon',
          title: 'List Icon',
        }}
        titleLabel={title}
        totalCount={displayCount}
        containerProps={{
          padding: ['normal', 'none', 'normal', 'none'],
        }}
        moreActions={moreActions}
        onMoreActionSelected={onMoreActionSelected}
        ids={ids}
        testMetadata={{
          resultsBadge: {
            'data-testid': 'entity-grid-results-badge',
          },
        }}
      >
        {selected > 0 && bulkActions !== undefined && (
          <>
            <Typography variant="T15">({selected}) items selected</Typography>
            <PopoverButton
              buttonText="Bulk"
              buttonProps={{
                styleType: 'promoted',
              }}
            >
              <PopoverOverlayTemplate
                title="Bulk Actions"
                size="small"
                elevated={false}
                flush
              >
                <MenuList
                  id={ids?.bulk}
                  options={bulkActions}
                  onChange={handleBulkAction}
                  showSelected={false}
                />
              </PopoverOverlayTemplate>
            </PopoverButton>
          </>
        )}
        {customActions?.map((action, index) => {
          return (
            <Button
              key={index}
              {...action}
              sizeType="smaller"
              onClick={(e: SyntheticEvent) => {
                if (tableRef.current) {
                  action?.onClick(e, tableRef.current);
                }
              }}
            />
          );
        })}
      </DataUtilityBar>
      <StandardTable
        {...table}
        onSelectionChanged={onSelectionChanged}
        onFilterChanged={handleFilterChanged}
        portal={portal}
        columns={columns}
        dataGridApiRef={tableRef}
        dataGridColumnApiRef={columnRef}
        filterable={true}
        testMetadata={{
          container: testMetadata?.table,
        }}
        onGridReady={onGridReady}
        noResultsMessage={noResultsMessage}
        isFetching={isFetching}
      />
    </DataUtilityWrapper>
  );
};

export default EntityGrid;
