import {
  KeyboardEvent,
  useMemo,
  useCallback,
  ReactNode,
  ReactElement,
} from 'react';
import classNames from 'classnames';
import isEmpty from 'lodash/isEmpty';
import isNull from 'lodash/isNull';

// Components
import Input, { InputProps } from '../Input';
import Search, { SearchProps } from '../Search';

// Styles
import './AutoComplete.scss';

// Types
import { TestMetadata } from 'types';

export interface AutoCompleteProps<T> {
  /** Specify the input component to enter data, can be input, search or a custom component */
  inputComponent?: 'input' | 'search' | ReactNode;
  insetComponent?: ReactNode;
  /**  Class name */
  className?: string;
  /** Input Value */
  value: string;
  /** Event to fire when input changes */
  onChange: (query: string) => void;
  /** Additional input props for Input, based on the Input Component */
  inputProps?: Partial<InputProps>;
  /** Additional input props for Search, based on the Search Component */
  searchProps?: Partial<SearchProps>;
  /** The data to fill autocomplete options with */
  data: T[] | null;
  /** Minimum characters required before results are shown */
  minChars?: number;
  /** Renders a loader */
  isLoading?: boolean;
  /**  Will add hover states to results */
  isInteractive?: boolean;
  /** will show view results item */
  isShowingViewResults?: boolean;
  /**  Will add hover states to results */
  onOpenResults?: (value: string) => void;
  /** Render options with a custom react element */
  renderDataFunc: (data: T, index?: number) => JSX.Element;
  /** Filter data using this function, by default no filter */
  filterDataFunc?: (data: T) => boolean;
  /** Event fired when a key is pressed */
  onKeyPress?: (event: KeyboardEvent<HTMLInputElement>) => void;
  // Test IDs for sections of AutoComplete
  testMetadata?: {
    autoComplete?: TestMetadata;
    searchIcon?: TestMetadata;
  };
}

type AutoComplete = <T>(
  p: AutoCompleteProps<T>,
) => ReactElement<AutoCompleteProps<T>>;

const AutoComplete: AutoComplete = ({
  className,
  inputProps,
  searchProps,
  onOpenResults,
  isLoading,
  isShowingViewResults = 'true',
  testMetadata = {
    autoComplete: { 'data-testid': 'autocomplete' },
    searchIcon: { 'data-testid': 'searchIcon' },
  },
  onChange,
  value,
  inputComponent = 'input',
  insetComponent,
  data,
  minChars = 0,
  isInteractive = true,
  renderDataFunc,
  filterDataFunc = () => true,
  onKeyPress = () => {
    // noop
  },
  ...otherProps
}) => {
  const autocompleteClassnames = useMemo(
    () => classNames('sb-autocomplete', className),
    [className],
  );
  const resultsClassNames = useMemo(
    () =>
      classNames(
        'sb-autocomplete__results',
        {
          'sb-autocomplete__results_interactive': isInteractive,
        },
        className,
      ),
    [className, isInteractive],
  );
  const filteredData = useMemo(
    () => data && data.filter((itm) => filterDataFunc(itm)),
    [data, filterDataFunc],
  );
  const filteredDataElements = useCallback(
    (itm, index) => renderDataFunc(itm, index),
    [renderDataFunc],
  );
  const handleChange = useCallback(
    (evt) => onChange(evt.target.value),
    [onChange],
  );
  const handleOpenResults = useCallback(
    () => onOpenResults && onOpenResults(value),
    [value, onOpenResults],
  );
  const minEntered = value.length >= minChars;

  return (
    <div
      {...otherProps}
      {...testMetadata?.autoComplete}
      className={autocompleteClassnames}
    >
      {inputComponent === 'input' && (
        <Input
          role="input"
          onKeyPress={onKeyPress}
          value={value}
          onChange={handleChange}
          {...inputProps}
        />
      )}
      {inputComponent === 'search' && (
        <Search
          onKeyPress={onKeyPress}
          value={value}
          onChange={onChange}
          inputProps={inputProps}
          {...searchProps}
        />
      )}
      {typeof inputComponent !== 'string' ? inputComponent : null}
      {insetComponent}
      {(value || !isEmpty(filteredData)) && (
        <ul className={resultsClassNames}>
          {isShowingViewResults && value.length > 0 && (
            <li
              role="item"
              onClick={handleOpenResults}
              className="sb-autocomplete__echo"
            >
              <i
                {...testMetadata?.searchIcon}
                className="fas fa-search sb-autocomplete__echo__search-icon"
              />
              <span className="sb-autocomplete__echo__search-value">
                &quot;<strong>{value}</strong>&quot;
              </span>
            </li>
          )}
          {minEntered && (
            <>
              {filteredData && filteredData.map(filteredDataElements)}
              {!isNull(data) && isEmpty(filteredData) && (
                <li className="sb-autocomplete__results_msg">
                  No results found
                </li>
              )}
              {isLoading && isNull(data) && (
                <li className="sb-autocomplete__results_msg">Loading...</li>
              )}
            </>
          )}
        </ul>
      )}
    </div>
  );
};

export default AutoComplete;
