import { useContext } from 'react';

// Components
import SpellbookBasicCanvas from '../../../Canvases/BasicCanvas';

// Context
import { PageGeneratorContext } from '../..';

// Types
import { CanvasTemplateProps } from '../../types';

// Styles
import './BodyCanvas.scss';

const BodyCanvas: React.FC<CanvasTemplateProps> = ({ sectionNames }) => {
  const context = useContext(PageGeneratorContext);
  if (context === undefined) {
    throw new Error('PageGeneratorContext is not defined');
  }

  return (
    <SpellbookBasicCanvas
      testMetadata={{
        standardSection: {
          'data-testid': 'PageGenerator_FormCanvas',
        },
      }}
    >
      <>{context.utils.renderChildren(sectionNames?.['body'])}</>
    </SpellbookBasicCanvas>
  );
};

export default BodyCanvas;
