import { FC, useMemo } from 'react';
import classNames from 'classnames';

// Components
import Badge, { BadgeProps } from '../Badge';
import Button from '../Button';

// Styles
import './Pillbox.scss';

export type PillboxProps = BadgeProps;

const Pillbox: FC<PillboxProps> = ({
  children,
  className,
  showClose = true,
  shape = 'pill',
  size = 'small',
  testMetadata,
  width = 'auto',
  onClose,
  ...otherProps
}) => {
  const pillboxClassNames = useMemo(
    () => classNames(showClose ? 'sb-pillbox__badge' : '', className),
    [showClose, className],
  );
  return (
    <Badge
      {...otherProps}
      shape={shape}
      size={size}
      width={width}
      className={pillboxClassNames}
      {...testMetadata}
    >
      {children}
      {showClose && (
        <Button
          iconOnly
          iconType="fa-times"
          labelText="Remove"
          styleType="demoted"
          sizeType="small"
          className="sb-pillbox__remove"
          onClick={onClose}
        />
      )}
    </Badge>
  );
};

export default Pillbox;
