import { FC } from 'react';

// Components
import Avatar, { AvatarProps } from '../Avatar';
import BadgedContainer, { BadgedContainerBadgeProps } from '../BadgedContainer';
import Card from '../Card';
import Container from '../Container';
import Typography from '../Typography';

// Hooks
import { useMinimalCardClasses } from './useMinimalCardClasses';

// Types
import { TestMetadata } from 'types';

// Styles
import './PersonCard.scss';

export interface ComponentsClassNames {
  card?: string;
  avatar?: string;
  name?: string;
  title?: string;
  container?: string;
}

export interface PersonCardProps {
  /** Badges that are positioned on the container */
  badges?: BadgedContainerBadgeProps | BadgedContainerBadgeProps[];
  /** Avatar source string */
  avatarSrc?: string;
  /** Avatar Props */
  avatarProps?: AvatarProps;
  /** Accessibility label (do not set if name appears with text) */
  accessibilityLabel?: string;
  /** Person name string */
  name?: string;
  /** Person role string */
  title?: string;
  /** Object to add custom classes to the components */
  componentsClassNames?: ComponentsClassNames;
  /** Determines if the whole card is an interactive object*/
  isInteractive?: boolean;
  /** Determines if the card in a active state */
  isActive?: boolean;
  /** Event to fire when the card is clicked */
  onClick?: () => void;
  /** Determines what kind of background style has the card */
  cardStyle?: 'backed' | 'floating';
  /** Test IDs for component sections*/
  testMetadata?: {
    personCard?: TestMetadata;
    personAvatar?: TestMetadata;
  };
  /** Whether it is or not a minimal Variant*/
  isMinimalVariant?: boolean;
}

const PersonCard: FC<PersonCardProps> = ({
  badges = [],
  avatarSrc,
  accessibilityLabel,
  name,
  title,
  componentsClassNames = {},
  children,
  testMetadata,
  isMinimalVariant = false,
  avatarProps = {},
  ...otherProps
}) => {
  const { cardClassName, containerClassName, titleClassName, nameClassName } =
    useMinimalCardClasses(componentsClassNames, isMinimalVariant);

  return (
    <Card
      orientation="inline"
      className={cardClassName}
      {...testMetadata?.personCard}
      {...otherProps}
    >
      {!isMinimalVariant && (
        <BadgedContainer badges={badges}>
          <Avatar
            imgSrc={avatarSrc}
            accessibilityLabel={accessibilityLabel}
            radiusType="circle"
            size="medium"
            className={componentsClassNames.avatar}
            {...testMetadata?.personAvatar}
            {...avatarProps}
          />
        </BadgedContainer>
      )}
      <Container padding="normal" className={containerClassName}>
        {!!name && (
          <Typography
            variant={isMinimalVariant ? 'T20' : 'T70'}
            className={nameClassName}
          >
            {name}
          </Typography>
        )}
        {!!title && (
          <Typography
            variant={isMinimalVariant ? 'T10' : 'T30'}
            className={titleClassName}
          >
            {title}
          </Typography>
        )}
        {children}
      </Container>
    </Card>
  );
};

export default PersonCard;
