import React, { FC, useMemo, useState } from 'react';
import classNames from 'classnames';

// Components
import Container from '../../../Container';
import Typography from '../../../Typography';
import Icon from '../../../Icon';
import Button from '../../../Button';

// Styles
import './TopNavTemplate.scss';

// Types
import { TopNavItemType, ActionItemType } from './types';
import TopNavItem from './TopNavItem';

export interface TopNavProps {
  /** Nav Bar Title */
  title?: string;
  /** Nav Bar Font Color */
  fontColor?: 'light' | 'dark';
  /** Nav Bar Color Selected from Domain*/
  navbarColor?: 'default' | 'missioncontrol' | 'admin' | 'incubation';
  /** Array of navigation items to list in the nav area */
  navItems?: TopNavItemType[];
  /** Array of action items to list in the action area (max of three) */
  actionItems?:
    | [ActionItemType, ActionItemType, ActionItemType]
    | [ActionItemType, ActionItemType]
    | [ActionItemType];
  /** Callback function to be called when a nav item is clicked */
  onNavClick?: (navItem?: TopNavItemType) => void;
  //** CoPilot Action */
  copilotAction?: () => void;
}

const TopNavTemplate: FC<TopNavProps> = ({
  title,
  navbarColor = 'default',
  fontColor = 'light',
  navItems,
  actionItems,
  onNavClick,
  copilotAction,
}) => {
  const [activeTab, setActiveTab] = useState(
    navItems?.length ? 'sb-cp-nav-item_' + navItems[0].title : '',
  );

  const headerStyles = useMemo(() => {
    return classNames('sb-cp-top-nav-template', {
      'sb-cp-top-nav-template__default': navbarColor === 'default',
      'sb-cp-top-nav-template__missioncontrol':
        navbarColor === 'missioncontrol',
      'sb-cp-top-nav-template__admin': navbarColor === 'admin',
      'sb-cp-top-nav-template__incubation': navbarColor === 'incubation',
    });
  }, [navbarColor]);

  const titleStyles = useMemo(() => {
    return classNames('sb-cp-nav-title', {
      'sb-cp-nav-title__font-light': fontColor === 'light',
      'sb-cp-nav-title__font-dark': fontColor === 'dark',
    });
  }, [fontColor]);

  const navItemStyles = useMemo(() => {
    return classNames('sb-cp-nav-items-row', {
      'sb-cp-nav-items-row__gradient-light': fontColor === 'light',
      'sb-cp-nav-items-row__gradient-dark': fontColor === 'dark',
    });
  }, [fontColor]);

  const areaStyles = useMemo(() => {
    return classNames('sb-cp-action-area', {
      'sb-cp-action-area__area-light': fontColor === 'light',
      'sb-cp-action-area__area-dark': fontColor === 'dark',
    });
  }, [fontColor]);

  const handleNavItemClick = (navItem: string): void => {
    const navItemTitle = navItem.split('_')[1];
    const returnItem = navItems?.find((item) => item.title === navItemTitle);
    setActiveTab(navItem);
    if (onNavClick) {
      onNavClick(returnItem);
    }
  };

  const handleCoPilotClick = (): void => {
    if (copilotAction) copilotAction();
  };

  return (
    <Container
      className={headerStyles}
      style={{ background: `${navbarColor}` }}
    >
      <Typography className={titleStyles} variant="T20">
        {title}
      </Typography>
      {navItems?.length ? (
        <Container className={navItemStyles}>
          {navItems?.map((navItem) => (
            <TopNavItem
              key={navItem.title.toUpperCase()}
              title={navItem.title}
              fontColor={fontColor}
              activeTab={activeTab}
              handleNavItemClick={handleNavItemClick}
            />
          ))}
        </Container>
      ) : (
        <div />
      )}
      <Container className={areaStyles}>
        {actionItems?.map((actionItem) => (
          <Button
            className="sb-cp-action-item"
            key={actionItem.iconName}
            data-testid={'fa-' + actionItem.iconName}
            iconType={'fa-' + actionItem.iconName}
            iconOnly={true}
            onClick={actionItem.onClick}
            sizeType="small"
            styleType="link"
          />
        ))}

        {actionItems?.length && <div className="sb-cp-action-divider" />}
        <Button
          data-testid="copilot-click"
          onClick={handleCoPilotClick}
          iconType="fa-sidebar-flip"
          className="sb-cp-copilot sb-cp-action-item"
          sizeType="small"
          styleType="link"
        >
          <Icon
            className="sb-cp-action-item"
            iconType="fa"
            faPackage="fa-thin"
            iconName="fa-chevron-right"
            size="small"
          />
        </Button>
      </Container>
    </Container>
  );
};

export default TopNavTemplate;
