import { FC, ReactNode, useMemo } from 'react';
import classNames from 'classnames';

// Styles
import './ContentBlock.scss';

// Types
import { TestMetadata } from 'types';

export interface ContentBlockProps {
  children: ReactNode;
  /** Class name for content block container */
  className?: string;
  // Test IDs for sections of ContentBlock
  testMetadata?: TestMetadata;
}

const ContentBlock: FC<ContentBlockProps> = ({
  children,
  className,
  testMetadata,
  ...otherProps
}) => {
  const contentBlockClassNames = useMemo(
    () => classNames('sb-content-block', className),
    [className],
  );

  return (
    <div {...otherProps} className={contentBlockClassNames} {...testMetadata}>
      {children}
    </div>
  );
};

export default ContentBlock;
