import { FC } from 'react';
import { ColumnApi, RowNode } from 'ag-grid-community';

// Styles
import './GroupRowInnerRenderer.scss';
import Badge from '../../../Badge';

// Group row renderers are not typed in ag-grid (uses any). So we type the pieces we care about only.
export type GroupRowInnerRendererProps = {
  columnApi: ColumnApi;
  node: RowNode;
  value: string;
};

const GroupRowInnerRenderer: FC<GroupRowInnerRendererProps> = ({
  node,
  value,
}) => {
  return (
    <div className="sb-datagrid_group-row" data-testid="DataGrid_Group_Row">
      <span
        className="sb-datagrid_group-row_label"
        data-testid="DataGrid_Group_Label"
      >
        {value}
      </span>

      <Badge
        shape="pill"
        size="small"
        className="sb-datagrid_group-row_count"
        testMetadata={{
          'data-testid': 'DataGrid_Group_Count',
        }}
      >
        {node?.allChildrenCount}
      </Badge>
    </div>
  );
};

export default GroupRowInnerRenderer;
