import { FC, useCallback, useContext, useMemo, useState } from 'react';
import classNames from 'classnames';
import get from 'lodash/get';
import { getSavingState } from './utils';

// Components
import Typography from '../Typography';
import Badge from '../Badge';
import PopoverButton from '../PopoverButton';
import MenuList from '../MenuList';
import { TreeBrowserContext } from './TreeBrowser';
import Button from '../Button';
import Spinner from '../Spinner';
import PopoverOverlay from '../PopoverOverlay';

// Styles
import './TreeHeader.scss';

// Types
import { TreeEntity } from './types';

export interface TreeNodeHeaderProps {
  /* Node */
  node: TreeEntity;
  /* Current Level */
  level: number;
  /* Current Path */
  path: string;
}

const TreeNodeHeader: FC<TreeNodeHeaderProps> = ({ node, path, level }) => {
  const {
    lengthKey,
    actions,
    isSaving,
    onClickItem,
    onCreate,
    updateTree,
    levels,
    idKey,
    primaryKey,
    statuses,
    statusKey,
  } = useContext(TreeBrowserContext);
  const [isPopoverButtonOpen, setIsPopoverButtonOpen] = useState(false);

  const id = get(node, idKey),
    name = get(node, primaryKey),
    displayLevel = levels[level] || level,
    count = get(node, lengthKey),
    status = get(node, statusKey),
    badge = get(statuses, status),
    isNew = node?.new,
    isGroup = level < levels.length - 2,
    nodeActions = node?.actions,
    disabled = node.disabled;

  const treeHeaderClassNames = useMemo(
    () =>
      classNames('sb-treebrowser__header', {
        'sb-treebrowser__node-new': isNew,
      }),
    [isNew],
  );

  const onClick = useCallback(() => {
    if (disabled || isNew) return;
    onClickItem?.(id || name, node, displayLevel);
  }, [name, id, onClickItem, displayLevel, disabled, node, isNew]);

  const isCreatable = onCreate && node?.creatable;

  const savingState = getSavingState(id || name, isSaving);

  return (
    <div className={treeHeaderClassNames} data-testid="data-header">
      <div className="sb-treebrowser__header-interactive" onClick={onClick}>
        <Typography
          className={classNames('sb-treebrowser__header-name', {
            'sb-treebrowser__header-name-disabled': disabled,
            'sb-treebrowser__header-name-new': isNew,
          })}
          variant="T15"
          data-testid="node-title"
        >
          <span
            className={classNames(
              `sb-treebrowser__header-trigger${disabled ? '-disabled' : ''}`,
            )}
            title={name}
          >
            {name}
          </span>
          {!!count && (
            <Badge
              className="sb-treebrowser__header-badge sb-treebrowser__header-count"
              color="base"
              light
              shape="pill"
              size="small"
            >
              {count}
            </Badge>
          )}
          {status && (
            <Badge
              className="sb-treebrowser__header-badge sb-treebrowser__header-status"
              color={badge || 'primary'}
              light
              shape="pill"
              size="small"
              title={status}
            >
              {status}
            </Badge>
          )}
        </Typography>
      </div>
      <div className="sb-treebrowser__header-actions">
        {isNew && (
          <Spinner
            size="small"
            testMetadata={{ 'data-testid': 'loading-header' }}
          />
        )}
        {isCreatable && !isNew && (
          <Button
            iconOnly
            iconType="fa-plus"
            iconPackage="fas"
            className={classNames(
              'sb-treebrowser__header-add',
              'sb-treebrowser__header-action',
            )}
            onClick={() => {
              updateTree(id || name, node, level, path, isGroup);
            }}
            styleType="standard"
            data-testid="add-icon"
            sizeType="smaller"
            alt={isSaving ? 'Creating' : 'Create'}
            disabled={disabled}
            {...savingState}
          />
        )}
        {nodeActions && !isNew && (
          <PopoverButton
            buttonProps={{
              styleType: 'standard',
              className: classNames(
                'sb-treebrowser__option',
                'sb-treebrowser__header-action',
              ),
              testMetadata: { 'data-testid': 'node-options-button' },
              disabled: disabled || savingState.disabled,
            }}
            handleToggle={(state, event) => {
              event?.stopPropagation();
              setIsPopoverButtonOpen(state);
            }}
            isOpen={isPopoverButtonOpen}
          >
            <PopoverOverlay flush elevated={false}>
              <MenuList
                showSelected={false}
                options={nodeActions.map((action) => {
                  const actionName = actions?.[action]?.label || action;
                  return {
                    label: actionName,
                    value: action,
                  };
                })}
                onChange={(selected) => {
                  actions?.[selected.value].onClick(selected.value, id || name);
                  setIsPopoverButtonOpen(false);
                }}
              />
            </PopoverOverlay>
          </PopoverButton>
        )}
      </div>
    </div>
  );
};

export default TreeNodeHeader;
