// Types
import { CellRendererFunc, MasterColumnCellRenderedParam } from '../types';
import { ICellRendererParams } from 'ag-grid-community';

// Styles
import './IDFieldRenderer.scss';

// Components
import MasterDetailCell from '../MasterDetailCell/MasterDetailCell';

// Utils
import { getCellValue } from './utils';

const IdFieldRenderedCell: React.FC<{
  params: ICellRendererParams;
}> = ({ params }) => {
  const cellValue = getCellValue(params);

  if (!cellValue) {
    console.log('Return Null');
    return null;
  }

  return (
    <div
      title={cellValue}
      className="sb-standard-table_cell-id"
      data-testid="StandardTable_Field_ID"
    >
      {cellValue}
    </div>
  );
};

const IDFieldRenderer: CellRendererFunc = (
  params: ICellRendererParams & MasterColumnCellRenderedParam,
) => {
  if (params.isMasterColumn) {
    return (
      <MasterDetailCell
        gridAPI={params.api}
        node={params.node}
        onMasterDetailCellMouseOver={params.onMasterDetailCellMouseOver}
      >
        <IdFieldRenderedCell params={params} />
      </MasterDetailCell>
    );
  }

  return <IdFieldRenderedCell params={params} />;
};

export default IDFieldRenderer;
