import {
  ChangeEvent,
  ComponentProps,
  FC,
  KeyboardEvent,
  SyntheticEvent,
  useEffect,
  useMemo,
  useRef,
  useState,
} from 'react';
import classNames from 'classnames';

// Components
import Button from '../Button';
import Input from '../Input';

// Styles
import './Search.scss';

// Types
import { TestMetadata } from 'types';

export interface SearchProps {
  /** Text for aria-label and placeholder */
  labelText?: string;
  /** Class name for search container */
  className?: string;
  /** Determine if search field should blend against dark nav styling */
  inNavDark?: boolean;
  /** Determine if search field should blend against light nav styling */
  inNavLight?: boolean;
  /** Determines if search field has an icon */
  hasIcon?: boolean;
  /** Icon class */
  iconClass?: string;
  /** Additional input props for Input, based on the inputComponent */
  inputProps?: ComponentProps<typeof Input>;
  /** value of search */
  value: string;
  /** show or hide a cancel button */
  showCancelBtn?: boolean;
  /** show an expandable search bar */
  expandable?: boolean;
  /** onChange to change search */
  onChange: (value: string) => void;
  /** onClear if parent component would like to do anything else after the clear */
  onClear?: () => void;
  /** onCancel if parent component would like to do anything else after canceling */
  onCancel?: () => void;
  /** function to be called when input is focused */
  onFocus?: (e: SyntheticEvent) => void;
  onKeyPress?: (e: KeyboardEvent<HTMLInputElement>) => void;
  /** option to default focus the input on load */
  isFocusOnLoad?: boolean;
  /** Width of the Input */
  width?: 'small' | 'medium' | 'large' | 'auto';
  // Test IDs for component sections
  testMetadata?: {
    search?: TestMetadata;
    searchButton?: TestMetadata;
    searchInput?: TestMetadata;
    searchIcon?: TestMetadata;
    clearButton?: TestMetadata;
  };
}

const Search: FC<SearchProps> = ({
  labelText = 'Search',
  hasIcon = true,
  iconClass = 'fa-search',
  inNavDark,
  inNavLight,
  value,
  showCancelBtn = true,
  expandable = false,
  onChange,
  // noop
  onClear = () => {
    // noop
  },
  onCancel,
  onKeyPress,
  onFocus = () => {
    // noop
  },
  className,
  inputProps = {},
  isFocusOnLoad = false,
  width,
  testMetadata = {
    search: { 'data-testid': 'search' },
    searchInput: { 'data-testid': 'searchInput' },
    searchButton: { 'data-testid': 'searchButton' },
    searchIcon: { 'data-testid': 'searchIcon' },
    clearButton: { 'data-testid': 'clearButton' },
  },
  ...otherProps
}) => {
  const searchClassNames = useMemo(
    () =>
      classNames(
        'sb-search',
        {
          'sb-search_in-nav': inNavDark || inNavLight,
          'sb-search_in-nav_dark': inNavDark,
          'sb-search_in-nav_light': inNavLight,
          'sb-search_with_icon': hasIcon,
        },
        className,
      ),
    [hasIcon, inNavDark, inNavLight, className],
  );

  const [expandableSearch, setExpandableSearch] = useState(expandable);
  const inputRef = useRef<HTMLInputElement>(null);

  useEffect(() => {
    if (isFocusOnLoad && inputRef.current) {
      inputRef.current?.focus();
    }
  }, [inputRef, isFocusOnLoad]);

  const handleChange = (e: ChangeEvent<HTMLInputElement>): void => {
    onChange(e.currentTarget.value);
  };

  const handleClear = (e: SyntheticEvent): void => {
    if (inputRef && inputRef.current) {
      inputRef.current.focus();
    }
    // clear input value
    onChange('');
    onClear();
    e.preventDefault();
    e.stopPropagation();
  };

  const handleCancel = (): void => {
    if (onCancel) {
      onCancel();
    }
    // clear input value
    onChange('');
    onClear();
  };

  const handleExpandeSearch = (): void => {
    setExpandableSearch((prev) => !prev);
  };

  const inputClassName = useMemo(
    () => classNames('sb-search__field__input', inputProps.className),
    [inputProps.className],
  );

  return (
    <div {...otherProps} className={searchClassNames} {...testMetadata?.search}>
      {expandableSearch ? (
        <Button
          type="button"
          iconOnly
          sizeType="small"
          styleType="promoted"
          iconType={iconClass}
          onClick={handleExpandeSearch}
          className="sb-search__button"
          {...testMetadata?.searchButton}
        />
      ) : (
        <>
          <div className="sb-search__input-container">
            <Input
              labelText={labelText}
              placeholder={labelText}
              onChange={handleChange}
              onKeyPress={onKeyPress}
              inputRef={inputRef}
              value={value}
              onFocus={onFocus}
              className={inputClassName}
              autoFocus={expandable}
              onBlur={() => expandable && handleExpandeSearch()}
              width={width}
              {...testMetadata?.searchInput}
              {...inputProps}
            />

            {hasIcon && (
              <i
                className={`fas ${iconClass} sb-search__icon`}
                {...testMetadata?.searchIcon}
              />
            )}

            {value.length > 0 && (
              <Button
                type="button"
                iconOnly
                styleType="demoted"
                iconType="fa-times-circle"
                labelText="Clear"
                className={classNames('sb-search__clear', {
                  'sb-search__clear--shrink': showCancelBtn,
                })}
                onMouseDown={handleClear}
                onClick={handleClear}
                {...testMetadata?.clearButton}
              />
            )}
          </div>

          {showCancelBtn && value.length > 0 && (
            <Button
              type="button"
              styleType="demoted"
              sizeType="small"
              className="sb-search__cancel"
              onClick={handleCancel}
            >
              Cancel
            </Button>
          )}
        </>
      )}
    </div>
  );
};

export default Search;
