import { useMemo } from 'react';
import classNames from 'classnames';

// Components

// Types
import { TestMetadata } from 'types';

// Styles
import './SideNavRendition.scss';

export interface SideNavRenditionProps {
  /** Additional class */
  className?: string;
  // Test IDs for SideNavRendition
  testMetadata?: TestMetadata;
}

const SideNavRendition: React.FC<SideNavRenditionProps> = ({
  children,
  className,
  testMetadata,
  ...otherProps
}) => {
  const SideNavClassNames = useMemo(
    () => classNames('sb-side-nav-rendition', className),
    [className],
  );

  return (
    <div {...otherProps} className={SideNavClassNames} {...testMetadata}>
      {children}
    </div>
  );
};

export default SideNavRendition;
