import { FC } from 'react';

// Components
import Button from '../Button';
import Status from './Status';

// Types
import { TestMetadata } from 'types';

export interface UnknownErrorProps {
  statusCode?: number;
  appName?: string;
  handleRefresh: () => void;
  // Test IDs for component sections
  testMetadata?: {
    unknownError?: TestMetadata;
    refreshPageButton?: TestMetadata;
  };
}

const UnknownError: FC<UnknownErrorProps> = ({
  statusCode,
  appName,
  testMetadata,
  handleRefresh,
}) => {
  return (
    <>
      <h2 {...testMetadata?.unknownError}>Sorry, something went wrong.</h2>
      <p>
        An unexpected error has occurred, and your request couldnt be
        completed. For any production support related issues or access request,
        please submit using{' '}
        <a
          href="https://issues.dmed.technology/report/ep"
          target="_blank"
          rel="noopener noreferrer"
        >
          the DMED Issues tool
        </a>
        {appName ? `, then enter "${appName}" as the app name` : ''}.
      </p>
      {statusCode && <Status statusCode={statusCode} />}
      <Button
        className="sb-error-template_action-button"
        labelText="Refresh"
        iconType="fa-sync"
        colorType="primary"
        onClick={handleRefresh}
        {...testMetadata?.refreshPageButton}
      >
        Refresh page
      </Button>
      <p className="sb-error-template_back-message">
        Keep seeing this message? Please come back and try again later.
      </p>
    </>
  );
};

export default UnknownError;
