import { ComponentProps, FC } from 'react';
import classNames from 'classnames';

// Components
import Button from '../Button';

// Styles
import './NotificationButton.scss';

// Types
import { TestMetadata } from 'types';

export type NotificationButtonProps = ComponentProps<typeof Button> & {
  // Test IDs for component sections
  testMetadata?: TestMetadata;
};

const NotificationButton: FC<NotificationButtonProps> = ({
  className,
  testMetadata,
  ...props
}) => {
  return (
    <Button
      {...props}
      className={classNames('sb-notification-button', className)}
      sizeType="small"
      iconOnly
      colorType="primary"
      iconType="fa-bullhorn"
      {...testMetadata}
    />
  );
};

export default NotificationButton;
