import {
  useContext,
  useState,
  useEffect,
  useCallback,
  FC,
  ReactNode,
} from 'react';
import classNames from 'classnames';

// Context
import { ControlSetContext } from '.';

// Styles
import './Control.scss';

// Types
import { TestMetadata } from 'types';

export interface ControlProps {
  /** Content */
  children?: ReactNode;
  /** Classnames */
  className?: string;
  /** Set the control color: primary (default), secondary */
  colorType?: 'primary' | 'secondary';
  /** Control ID */
  controlId?: string;
  /** Set default selected state */
  defaultSelected?: boolean;
  /** Event to fire when toggled */
  onToggle?: (selected: boolean) => void;
  // Test IDs for component sections
  testMetadata?: TestMetadata;
}

const Control: FC<ControlProps> = ({
  children,
  className,
  colorType = 'primary',
  controlId,
  defaultSelected = false,
  onToggle,
  testMetadata,
  ...otherProps
}) => {
  const { selectedControlId, requireSelection, onControlSelect } =
    useContext(ControlSetContext);

  const [selected, setSelected] = useState(defaultSelected);

  useEffect(() => {
    if (defaultSelected) {
      controlId && onControlSelect && onControlSelect(controlId);
    }
  }, [defaultSelected, onControlSelect, controlId]);

  useEffect(() => {
    if (selected && selectedControlId && selectedControlId !== controlId) {
      setSelected(false);
      if (onToggle) {
        onToggle(false);
      }
    }
    if (selectedControlId === controlId && selectedControlId) setSelected(true);
  }, [selectedControlId, selected, controlId, onToggle]);

  const handleClick = useCallback(() => {
    const isCurrentlySelectedAndRequired =
      requireSelection && selectedControlId === controlId && selected;
    if (!isCurrentlySelectedAndRequired) {
      const newSelected = !selected;
      setSelected(newSelected);
      if (onToggle) {
        onToggle(newSelected);
      }
      newSelected && controlId && onControlSelect && onControlSelect(controlId);
    }
  }, [
    onToggle,
    selected,
    requireSelection,
    selectedControlId,
    controlId,
    onControlSelect,
  ]);

  return (
    <div
      className={classNames('sb-control', className, {
        'sb-control_primary': colorType === 'primary',
        'sb-control_secondary': colorType === 'secondary',
        'sb-control_selected': selected,
        'sb-control_unselected': !selected,
      })}
      onClick={handleClick}
      {...testMetadata}
      {...otherProps}
    >
      {children}
    </div>
  );
};

export default Control;
