import { FC, useCallback, useMemo } from 'react';
import classNames from 'classnames';

// Components
import DataUtilityBarActions from './DataUtilityBarActions';
import DataUtilityBarSummary from './DataUtilityBarSummary';
import Icon, { IconProps } from '../../../Icon';
import UtilityBar from '../../../UtilityBar';

// Types
import { ContainerProps } from '../../../Container';
import { TestMetadata, Option } from '../../../types';

export interface DataUtilityBarProps {
  /** Select ID's */
  ids?: {
    sort?: string;
    display?: string;
    export?: string;
    moreActions?: string;
  };
  /** Icon to show to left of summary */
  titleIconProps?: IconProps;
  /** Label to show in summary */
  titleLabel?: string;
  /** Visible count shown in the data */
  visibleCount?: number;
  /** Total count shown in the data */
  totalCount?: number;
  /** Any additional menu options */
  moreActions?: Option[];
  /** Gets called when a "more action" action is selected */
  onMoreActionSelected?: (selected: Option) => void;
  /** Class name for bar */
  className?: string;
  /** Container props to use in each container */
  containerProps?: Omit<ContainerProps, 'children'>;
  /** Test meta data for the bar sections */
  testMetadata?: {
    utilityBar?: TestMetadata;
    container?: TestMetadata;
    titleLabel?: TestMetadata;
    loader?: TestMetadata;
    resultsBadge?: TestMetadata;
  };
  /** Name of the entity */
  entityName?: string;
}

const DataUtilityBar: FC<DataUtilityBarProps> = ({
  ids,
  visibleCount,
  totalCount = 0,
  className,
  titleIconProps,
  titleLabel,
  moreActions,
  onMoreActionSelected,
  containerProps = {
    padding: 'loose',
  },
  children,
  testMetadata,
  entityName,
}) => {
  const utilityBarClassNames = useMemo(
    () => classNames('sb-cp-data-utility-bar', className),
    [className],
  );

  const titleIconComp = useMemo(
    () => (titleIconProps ? <Icon {...titleIconProps} /> : null),
    [titleIconProps],
  );

  const handleMoreActionSelected = useCallback(
    (value: Option) => {
      onMoreActionSelected && onMoreActionSelected(value);
    },
    [onMoreActionSelected],
  );

  return (
    <UtilityBar
      leftSection={
        <DataUtilityBarSummary
          className="sb-cp-data-utility-bar__left-region"
          visibleCount={visibleCount}
          totalCount={totalCount}
          icon={titleIconComp}
          titleLabel={titleLabel}
          containerProps={containerProps}
          testMetadata={testMetadata}
          entityName={entityName}
        />
      }
      rightSection={
        <DataUtilityBarActions
          className="sb-cp-data-utility-bar__right-region"
          moreActions={moreActions}
          onMoreActionSelected={handleMoreActionSelected}
          containerProps={containerProps}
          ids={ids}
        >
          {children}
        </DataUtilityBarActions>
      }
      className={utilityBarClassNames}
      testMetadata={testMetadata?.utilityBar}
    />
  );
};

export default DataUtilityBar;
