import { useMemo, useCallback, FC } from 'react';
import classNames from 'classnames';

// Components
import Avatar, { AvatarProps } from '../Avatar/Avatar';

// Styles
import './AvatarGroup.scss';

// Types
import { TestMetadata } from 'types';

export interface AvatarGroupProps {
  /* Max visible avatars */
  maxVisible?: number;
  /* Avartars elements*/
  avatars: JSX.Element[];
  /* Props for count text avatar */
  avatarCountProps?: AvatarProps;
  /* Spacing type */
  spacing?: 'none' | 'stack' | 'small' | 'medium' | 'large';
  /**Custom classes */
  className?: string;
  // Test IDs for sections of AvatarGroup
  testMetadata?: TestMetadata;
}

const AvatarGroup: FC<AvatarGroupProps> = ({
  maxVisible,
  spacing = 'stack',
  avatars,
  avatarCountProps,
  testMetadata,
  className,
}) => {
  const hideNumber = useMemo(() => {
    if (maxVisible || maxVisible === 0) {
      if (avatars.length > maxVisible) {
        return avatars.length - maxVisible;
      } else {
        return undefined;
      }
    }
  }, [avatars.length, maxVisible]);

  const ovelapStyles = useCallback(
    (index) => {
      return { zIndex: avatars.length - index };
    },
    [avatars],
  );

  return (
    <div
      className={classNames(
        'sb-avatar-group',
        {
          'sb-avatar-group_stacked': spacing === 'stack',
          'sb-avatar-group_small': spacing === 'small',
          'sb-avatar-group_medium': spacing === 'medium',
          'sb-avatar-group_large': spacing === 'large',
        },
        className,
      )}
      {...testMetadata}
    >
      {
        // NOTE: if `maxVisible` is undefined it'll not exclude any elements
        avatars.slice(0, maxVisible).map((avatar, index) => (
          <div
            key={index}
            className="sb-avatar-group__avatar-container"
            style={spacing === 'stack' ? ovelapStyles(index) : {}}
          >
            {avatar}
          </div>
        ))
      }
      {hideNumber && (
        <div className="sb-avatar-group__avatar-container">
          <Avatar
            textMode
            text={`+${hideNumber}`}
            className="sb-avatar-group_text"
            {...avatarCountProps}
          />
        </div>
      )}
    </div>
  );
};

export default AvatarGroup;
