import { FC, useState, useMemo, useCallback, useEffect } from 'react';
import classNames from 'classnames';

// Components
import Skeleton from '../Skeleton';

// Styles
import './ContentGrid.scss';
import './ContentGridSkeleton.scss';

// Types
import { TestMetadata } from 'types';

export interface ContentGridSkeletonProps {
  size: 'smaller' | 'small' | 'medium' | 'large' | 'larger';
  width: number;
  itemCount?: number;
  // Test IDs for component sections
  testMetadata?: TestMetadata;
}

const ContentGridSkeleton: FC<ContentGridSkeletonProps> = ({
  size,
  width,
  itemCount,
  testMetadata = { 'data-testid': 'ContentGridSkeleton_Skeleton' },
}) => {
  const [skeletonsArray, setSkeletonsArray] = useState([0]);

  const skeletonsWidth = useMemo(() => {
    switch (size) {
      case 'smaller':
        return 100;
      case 'small':
        return 180;
      case 'medium':
        return 290;
      case 'large':
        return 350;
      case 'larger':
        return 550;
    }
  }, [size]);

  const actualWidth = useCallback(
    (number) => {
      return number * skeletonsWidth + (number - 1) * 16;
    },
    [skeletonsWidth],
  );

  useEffect(() => {
    for (let index = 1; actualWidth(index) <= width; index++) {
      if (actualWidth(index + 1) > width) {
        setSkeletonsArray(
          Array.from({ length: itemCount ?? index * 2 }, (v, i) => i),
        );
      }
    }
  }, [width, actualWidth, setSkeletonsArray, itemCount]);

  return (
    <>
      {skeletonsArray?.map((i, index) => (
        <Skeleton
          key={`keyy${index}`}
          className={classNames(
            'sb-content-grid',
            'sb-content-grid-item-skeleton',
            `sb-content-grid_${size}`,
          )}
          {...testMetadata}
        />
      ))}
    </>
  );
};

export default ContentGridSkeleton;
