import { FC } from 'react';

// Components
import BadgedContainer from '../BadgedContainer';
import Button from '../Button';
import Notification from '../Notification';
import { Toast, ToastContainer } from '../Toast';

// Styles
import './CodeBox.scss';

// Types
import { TestMetadata } from 'types';

export interface CodeBoxProps {
  code: string;
  // Test IDs for sections of CodeBox
  testMetadata?: {
    copyButton?: TestMetadata;
    code?: TestMetadata;
  };
}

const CodeBox: FC<CodeBoxProps> = ({
  code,
  testMetadata = {
    copyButton: { 'data-testid': 'copyButton' },
    code: { 'data-testid': 'code' },
  },
}) => {
  const onCopyClick = (): void => {
    navigator.clipboard.writeText(code);

    Toast({
      position: 'top-right',
      closeOnClick: true,
      animate: true,
      containerId: 'code-box-container',
      content: (
        <Notification
          iconTypePrimary="fa-check-circle"
          role="status"
          notifType="success"
          light
          hideClose
        >
          <div>Copied code to clipboard!</div>
        </Notification>
      ),
    });
  };

  return (
    <>
      <BadgedContainer
        className="sb-code-box__container"
        badges={{
          component: (
            <Button
              labelText="Copy"
              sizeType="smaller"
              onClick={onCopyClick}
              {...testMetadata?.copyButton}
            >
              Copy
            </Button>
          ),
          position: 'bottom-right',
        }}
      >
        <pre>
          <code className="sb-code-box" {...testMetadata?.code}>
            {code}
          </code>
        </pre>
      </BadgedContainer>
      <ToastContainer containerId="code-box-container" enableMultiContainer />
    </>
  );
};

export default CodeBox;
