import { ComponentProps, ReactNode, useCallback, useContext } from 'react';
import { FormProvider, useForm, Mode } from 'react-hook-form';
import { AnyObjectSchema } from 'yup';
import { DeepPartial, SubmitHandler } from 'react-hook-form';
import { yupResolver } from '@hookform/resolvers/yup';

// Components
import Container, { ContainerProps } from '../Container';

// Types
import { TestMetadata } from 'types';

// Analytics
import {
  AnalyticsContext,
  handleAnalyticsEvent,
  TAnalyticsEventData,
} from '@dtci/dash/analytics';

export interface FormProps<I extends Record<string, unknown>> {
  /** Yup validation method for validation resolver */
  validationSchema?: AnyObjectSchema;
  /** This function will receive the form data if form validation is successful */
  onSubmit: SubmitHandler<I>;
  /** Default vaules for the compoents */
  defaultValues?: DeepPartial<I>;
  /** Component to register */
  children?: ReactNode;
  /** Container props - no children */
  containerProps?: Omit<ContainerProps, 'children'>;
  /** Props spread to form including test ids */
  formProps?: ComponentProps<'form'> & TestMetadata;
  /** Additional event information to pass to analytics tracking */
  analyticsEventData?: TAnalyticsEventData;
  /** Additional Add to change mode for validation */
  validationMode?: Exclude<Mode, 'onTouched' | 'all'>;
  /** Additional Add to check when revalidation occurs */
  reValidationMode?: Exclude<Mode, 'onTouched' | 'all'>;
}

const Form = <I extends Record<string, unknown>>({
  validationSchema,
  defaultValues,
  validationMode,
  reValidationMode,
  onSubmit,
  children,
  containerProps,
  formProps,
  analyticsEventData,
}: FormProps<I>): JSX.Element => {
  const methods = useForm<I>({
    resolver: validationSchema ? yupResolver(validationSchema) : undefined,
    defaultValues,
    mode: validationMode || 'onSubmit',
    reValidateMode: reValidationMode || 'onSubmit',
  });

  const analyticsContext = useContext(AnalyticsContext);

  const handleOnSubmit = useCallback(
    async (e) => {
      await onSubmit(e);
      handleAnalyticsEvent({
        analyticsContext,
        analyticsEventData,
        componentType: 'Form',
        actionName: 'submit',
        label: formProps?.['aria-label'] || formProps?.className || '',
        value: [methods.getValues()],
      });
    },
    [analyticsContext, analyticsEventData, formProps, methods, onSubmit],
  );

  return (
    <Container {...containerProps}>
      <FormProvider {...methods}>
        <form
          onSubmit={(e) => {
            methods.handleSubmit(handleOnSubmit)(e);
          }}
          {...formProps}
        >
          {children}
        </form>
      </FormProvider>
    </Container>
  );
};

export default Form;
