import { isArray } from 'lodash';
import { ReactElement, ReactNode } from 'react';
import { SpacingOptions } from '../types';
import {
  CanvasObject,
  Layout,
  LayoutObject,
  PageSchema,
  AreaObject,
} from './types';

export const isLayout = (layout: Layout): layout is LayoutObject => {
  return (layout as LayoutObject).type !== undefined;
};

export const parsePageSchema = (page: string | PageSchema): PageSchema => {
  let schema = page;
  try {
    schema = typeof page === 'string' ? (JSON.parse(page) as PageSchema) : page;
  } catch (e) {
    throw Error('PageGenerator: Unable to load page config');
  }

  if (schema.canvas && !validateSchema(schema.canvas)) {
    throw Error(
      'PageGenerator: Duplicate key used for one or more page areas. Page section naming must be globally unique.',
    );
  }

  return schema;
};

export const validateSchema = (canvas: CanvasObject): boolean => {
  const keys = collectKeys(canvas);

  // A set can only contain unique values, so this is a performant way of checking for duplicates
  return new Set(keys).size === keys.length;
};

export const collectKeys = (canvas: CanvasObject): string[] => {
  const schemaKeys: string[] = [];

  const areas = canvas.areas;

  if (isArray(areas)) {
    areas.forEach((section: AreaObject) => {
      if (section.name) {
        schemaKeys.push(section.name);
      }
      if (section.canvas) {
        schemaKeys.push(...collectKeys(section.canvas));
      }
    });
  }
  return schemaKeys;
};

export const getContainerPadding = (section: AreaObject): SpacingOptions[] => {
  if (section?.container && section?.container?.padding) {
    const container = section?.container;
    if (typeof container.padding === 'string') {
      return [
        container.padding,
        container.padding,
        container.padding,
        container.padding,
      ];
    } else {
      const padding = container.padding;
      return [
        padding?.top || 'normal',
        padding?.right || 'loose',
        padding?.bottom || 'normal',
        padding?.left || 'loose',
      ];
    }
  } else if (section?.canvas?.template) {
    return ['none', 'none', 'none', 'none'];
  }

  return ['normal', 'loose', 'normal', 'loose'];
};

export const getChildren = (
  children: Array<Exclude<ReactNode, boolean | null | undefined>>,
  name: string,
): Array<Exclude<ReactNode, boolean | null | undefined>> => {
  return children.filter(
    (child: ReactNode) => (child as ReactElement)?.props?.name === name,
  );
};
