import { CellRendererFunc } from '../StandardTable/types';
import { IRowNode } from 'ag-grid-community';
import ButtonGroup from '../../ButtonGroup';
import Button from '../../Button';

const OptionsCellRenderer: CellRendererFunc<{
  onDeleteRow: (node: IRowNode) => void;
  onEditRow: (node: IRowNode) => void;
  deletable?: boolean;
}> = ({ api, node, onDeleteRow, onEditRow, deletable }) => {
  // NOTE: not wrapping in `useMemo` because `api.getEditingCell()` won't be
  // called on updates
  const isCurrentRowEditing = api
    .getEditingCells()
    .some((cell) => cell.rowIndex === node.rowIndex);

  return (
    <ButtonGroup className="sb-cp-standard-table__options-group">
      {deletable && (
        <Button
          className="sb-cp-standard-table__option"
          iconType="fa-trash"
          iconPackage="fa-light"
          sizeType="smaller"
          labelText="Delete"
          iconOnly
          type="button"
          onClick={() => {
            onDeleteRow(node);
          }}
        />
      )}
      {!isCurrentRowEditing && (
        <Button
          className="sb-cp-standard-table__option"
          iconType="fa-edit"
          iconPackage="fa-light"
          sizeType="smaller"
          labelText="Edit"
          iconOnly
          type="button"
          onClick={() => {
            onEditRow(node);
          }}
        />
      )}
    </ButtonGroup>
  );
};

export default OptionsCellRenderer;
