import React, { FC, useMemo } from 'react';
import classNames from 'classnames';

// Styles
import './TopNavItem.scss';

interface TopNavItemProps {
  title: string;
  fontColor?: 'light' | 'dark';
  activeTab: string;
  handleNavItemClick: (navItem: string) => void;
}

const TopNavItem: FC<TopNavItemProps> = ({
  title,
  fontColor = 'light',
  activeTab,
  handleNavItemClick,
}) => {
  const activeStyle = useMemo(
    () =>
      classNames({
        'sb-cp-nav-item__active-light': fontColor === 'light',
        'sb-cp-nav-item__active-dark': fontColor === 'dark',
      }),
    [fontColor],
  );

  return (
    <div
      onClick={(clicked) => handleNavItemClick(clicked.currentTarget.id)}
      id={'sb-cp-nav-item_' + title}
      className={
        activeTab == 'sb-cp-nav-item_' + title
          ? `sb-cp-nav-item ${activeStyle}`
          : `sb-cp-nav-item`
      }
      key={title.toUpperCase()}
    >
      <p>{title}</p>
    </div>
  );
};

export default TopNavItem;
