import {
  KeyboardEvent,
  forwardRef,
  useImperativeHandle,
  useState,
  useRef,
} from 'react';

// Components
import PopoverOverlay from '../../../Templates/PopoverOverlayTemplate';
import Input from '../../../../Input';

// Types
import { IDoesFilterPassParams, IFilterParams } from 'ag-grid-community';
import { GridModel } from '../../types';
import isArray from 'lodash/isArray';

const DataGridTextFilter = forwardRef((props: IFilterParams, ref) => {
  // Controlled value of the input
  const [input, setInput] = useState<string>('');

  // Actual applied value of the filter. We use ref here to guarantee that when `isFilterActive` is called We get the current value.
  const activeFilterRef = useRef<string>('');

  // Required to communicate with ag-grid
  useImperativeHandle(ref, () => ({
    // Is the filter currently applied
    isFilterActive() {
      return activeFilterRef.current.length > 0;
    },

    // Does the row pass the filter criteria
    doesFilterPass(params: IDoesFilterPassParams) {
      const column = props?.colDef?.field;
      if (column && typeof props?.colDef?.valueGetter === 'function') {
        const value = props.colDef.valueGetter({
          ...props,
          data: params.data,
          getValue: (field) => params.data[field],
          node: params.node,
        });
        return (isArray(value) ? value : [value]).some((singleValue) =>
          singleValue
            .toLowerCase()
            .includes(activeFilterRef.current.toLowerCase()),
        );
      }
      if (column && params.data?.[column]) {
        return params.data[column]
          .toLowerCase()
          .includes(activeFilterRef.current.toLowerCase());
      }
      return false;
    },

    // Save Model
    getModel() {
      if (!activeFilterRef.current) {
        return undefined;
      }

      return {
        state: activeFilterRef.current,
      };
    },

    // Restore Model
    setModel(model: GridModel<string>) {
      if (model == null) {
        setInput('');
        activeFilterRef.current = '';
      } else {
        setInput(model.state);
        activeFilterRef.current = model.state;
      }
    },
  }));

  // Set active filter on apply
  const onApply = (): void => {
    if (input.length > 0) {
      activeFilterRef.current = input;
    } else {
      activeFilterRef.current = '';
    }
    props.filterChangedCallback();
    props.api.hidePopupMenu();
  };

  // Set active filter on enter
  const onKeyPress = (e: KeyboardEvent<HTMLInputElement>): void => {
    e.key === 'Enter' && onApply();
  };

  // Clear input text
  const onClear = (): void => {
    setInput('');
  };

  // Close popup and restore input text to set filter
  const onCancel = (): void => {
    input !== activeFilterRef.current && setInput(activeFilterRef.current);
    props.api.hidePopupMenu();
  };

  return (
    <PopoverOverlay
      title="Filter"
      size="grid"
      onApply={onApply}
      onCancel={onCancel}
      onClear={onClear}
      isClearEnabled={input.length > 0}
      testMetaData={{
        cancel: {
          'data-testid': 'StandardTable_Filter_Cancel',
        },
        apply: {
          'data-testid': 'StandardTable_Filter_Apply',
        },
        clear: {
          'data-testid': 'StandardTable_Filter_Clear',
        },
      }}
    >
      <Input
        data-testid="input-filter"
        value={input}
        onChange={(e) => setInput(e.target.value)}
        onKeyPress={onKeyPress}
        type="text"
        width="auto"
        testMetadata={{
          'data-testid': 'StandardTable_Filter_Text',
        }}
      />
    </PopoverOverlay>
  );
});

export default DataGridTextFilter;
