import { UniqueIdentifier } from '@dnd-kit/core';

export function getColor(id: UniqueIdentifier): string | undefined {
  switch (String(id)[0]) {
    case 'A':
      return '#4ac4ac';
    case 'B':
      return '#ffda6c';
    case 'C':
      return '#00bcd4';
    case 'D':
      return '#ef769f';
  }

  return undefined;
}
