import get from 'lodash/get';

// Types
import { CellRendererFunc, PrimaryColumn } from '../types';
import { ICellRendererParams } from 'ag-grid-community';

// Styles
import './PrimaryFieldRenderer.scss';

// Components
import MasterDetailCell from '../MasterDetailCell/MasterDetailCell';

// Utils
import { getCellValue } from './utils';

const PrimaryFieldRenderedCell: React.FC<{
  params: ICellRendererParams & PrimaryColumn;
}> = ({ params }) => {
  const demotedValue =
    typeof params.secondaryField == 'string'
      ? get(params.data, params.secondaryField)
      : typeof params.secondaryField == 'function' &&
        params.secondaryField(params.data);

  const cellValue = getCellValue(params);

  if (!cellValue) {
    return null;
  }

  return (
    <div
      title={cellValue}
      className="sb-standard-table_cell-primary"
      data-testid="StandardTable_Field_Primary"
    >
      <div title={cellValue} className="sb-standard-table_cell-primary_text">
        {cellValue}
      </div>

      {demotedValue && (
        <div
          title={demotedValue}
          className="sb-standard-table_cell-primary_demoted-text"
        >
          {demotedValue}
        </div>
      )}
    </div>
  );
};

const PrimaryFieldRenderer: CellRendererFunc<PrimaryColumn> = (params) => {
  if (params.isMasterColumn) {
    return (
      <MasterDetailCell
        gridAPI={params.api}
        node={params.node}
        onMasterDetailCellMouseOver={params.onMasterDetailCellMouseOver}
      >
        <PrimaryFieldRenderedCell params={params} />
      </MasterDetailCell>
    );
  }

  return <PrimaryFieldRenderedCell params={params} />;
};

export default PrimaryFieldRenderer;
