import { FC } from 'react';
import classNames from 'classnames';

// Components
import Button from '../Button';
import Notification from '../Notification';

// Styles
import './SessionExpirationWarning.scss';

// Types
import { TestMetadata } from 'types';

export interface SessionExpirationWarningProps {
  /** Class name for session expiration warning */
  className?: string;
  /** show warning notification and message (2 minutes until session expiration) */
  firstWarning?: boolean;
  /** show error notification and message (1 minute until session expiration) */
  lastWarning?: boolean;
  /** show expiration message (session expired) */
  expired?: boolean;
  /** page is being refreshed, disable buttons */
  isRefreshing?: boolean;
  /** event handler for refresh button */
  clearSessionAndReload?: () => void;
  // Test IDs for component sections
  testMetadata?: {
    sessionExpirationWarning?: TestMetadata;
    refreshButton?: TestMetadata;
  };
}

const SessionExpirationWarning: FC<SessionExpirationWarningProps> = ({
  children,
  firstWarning,
  lastWarning,
  expired,
  isRefreshing,
  clearSessionAndReload,
  className,
  testMetadata = {
    refreshButton: { 'data-testid': 'refreshButton' },
  },
  ...otherProps
}) => {
  const sessionExpirationWarningClassNames = classNames(
    'sb-session-expiration-warning',
    className,
  );

  return (
    <div {...otherProps} className={sessionExpirationWarningClassNames}>
      {(firstWarning || lastWarning || expired) && (
        <Notification
          light
          hideClose
          notifType={lastWarning || expired ? 'error' : 'warning'}
          iconTypePrimary="fa-exclamation-triangle"
          {...testMetadata?.sessionExpirationWarning}
        >
          <div>
            <h2 className="sb-session-expiration-warning-header">
              {expired
                ? 'Your MyID session has expired.'
                : 'Your MyID session is about to expire.'}
            </h2>
            <p className="sb-session-expiration-warning-text">
              Refresh the page to renew your session.
            </p>
            <Button
              sizeType="small"
              colorType={lastWarning || expired ? 'error' : 'warning'}
              className="sb-session-expiration-warning-button"
              onClick={clearSessionAndReload}
              disabled={isRefreshing}
              {...testMetadata?.refreshButton}
            >
              {isRefreshing ? 'Refreshing...' : 'Refresh Now'}
            </Button>
          </div>
        </Notification>
      )}
      {children}
    </div>
  );
};

export default SessionExpirationWarning;
