import { useCallback, useEffect, useRef } from 'react';

// Types
import { CellRendererFunc, MasterColumnCellRenderedParam } from '../types';
import { ICellRendererParams } from 'ag-grid-community';

// Components
import MasterDetailCell from '../MasterDetailCell/MasterDetailCell';
import ViewMore from '../../../ViewMore';

// Utils
import { getCellValue } from './utils';
import {
  createAutoHeightRowResizeObservable,
  isExpandedAutoHeightRow,
} from '../../../DataGrid/rowHeight.utils';

// Styles
import './ExpandableFieldRenderer.scss';

const ExpandableFieldCellRenderer: React.FC<{
  params: ICellRendererParams;
}> = ({ params }) => {
  const unsubscribeResizeObservableRef =
    useRef<ReturnType<typeof createAutoHeightRowResizeObservable>>();
  const cellValue = getCellValue(params);

  useEffect(() => {
    return () => {
      if (unsubscribeResizeObservableRef.current) {
        unsubscribeResizeObservableRef.current();
      }
    };
  });

  const onViewMoreChangedHandler = useCallback(
    (expanded) => {
      unsubscribeResizeObservableRef.current =
        createAutoHeightRowResizeObservable(
          params.api,
          params.node,
          params.eParentOfValue, // Ag Grid Cell Wrapper element.
          expanded,
        );
    },
    [params],
  );

  if (!cellValue) {
    return null;
  }

  return (
    <div
      title={cellValue}
      className="sb-cp-standard-table_cell-expandable"
      data-testid="StandardTable_Field_Expandable"
    >
      <ViewMore
        text={cellValue || ''}
        truncateAtLine={1}
        onViewMoreChanged={onViewMoreChangedHandler}
        expandedByDefault={
          !!params.node.id &&
          isExpandedAutoHeightRow(params.api, params.node.id)
        }
      />
    </div>
  );
};

const ExpandableFieldRenderer: CellRendererFunc = (
  params: ICellRendererParams & MasterColumnCellRenderedParam,
) => {
  if (params.isMasterColumn) {
    return (
      <MasterDetailCell
        gridAPI={params.api}
        node={params.node}
        onMasterDetailCellMouseOver={params.onMasterDetailCellMouseOver}
      >
        <ExpandableFieldCellRenderer params={params} />
      </MasterDetailCell>
    );
  }

  return <ExpandableFieldCellRenderer params={params} />;
};

export default ExpandableFieldRenderer;
