import classNames from 'classnames';
import { FC, ReactNode } from 'react';

// Components
import { BodySection, HeaderSection } from '../../../Sections';
import {
  StandardLayout,
  HeaderPanel,
  BodyPanel,
} from '../../../StandardLayout';
import { Menu } from '../../../Menu';

// Types
import { TestMetadata } from 'types';

// Styles
import './SideNavTemplate.scss';

export interface SideNavTemplateProps {
  /** header menu items */
  actions?: ReactNode;
  /** classname */
  className?: string;
  // Test IDs for SideNavTemplate
  testMetadata?: {
    base?: TestMetadata;
    header?: TestMetadata;
    headerMenu?: TestMetadata;
    body?: TestMetadata;
    bodyMenu?: TestMetadata;
  };
}

const SideNavTemplate: FC<SideNavTemplateProps> = ({
  actions,
  children,
  className,
  testMetadata,
}) => {
  return (
    <StandardLayout
      className={classNames('sb-cp-side-nav', className)}
      {...testMetadata?.base}
    >
      {actions && (
        <HeaderPanel>
          <HeaderSection
            padding="none"
            className="sb-cp-side-nav__header"
            {...testMetadata?.header}
          >
            <Menu
              className="sb-cp-side-nav__header-menu"
              {...testMetadata?.headerMenu}
            >
              {actions}
            </Menu>
          </HeaderSection>
        </HeaderPanel>
      )}
      <BodyPanel>
        <BodySection padding="none" {...testMetadata?.body}>
          <Menu
            className="sb-cp-side-nav__body-menu"
            {...testMetadata?.bodyMenu}
          >
            {children}
          </Menu>
        </BodySection>
      </BodyPanel>
    </StandardLayout>
  );
};

export default SideNavTemplate;
