import {
  DetailedHTMLProps,
  FC,
  forwardRef,
  HTMLProps,
  LiHTMLAttributes,
  ReactElement,
  useContext,
  useMemo,
} from 'react';
import classNames from 'classnames';

// Components
import Badge from '../Badge';
import Icon from '../Icon';

// Styles
import './Step.scss';

// Context
import { StepsContext } from './Steps';

// Types
import { TestMetadata } from 'types';

export interface StepProps
  extends DetailedHTMLProps<LiHTMLAttributes<HTMLLIElement>, HTMLLIElement> {
  /** index of the Step */
  index: number;
  /** Classname of Step */
  className?: string;
  /** Indicator text */
  indicator?: ReactElement | number | string;
  /** Test data IDs for Step */
  testMetadata?: TestMetadata;
}

const Step = forwardRef<HTMLLIElement, StepProps>(
  ({ index, children, className, indicator, ...otherProps }, ref) => {
    const { connected, currentStep, mode, orientation } =
      useContext(StepsContext);

    const state = useMemo(() => {
      if (currentStep === index) return 'in-progress';
      if (currentStep > index) return 'completed';
      return 'unstarted';
    }, [currentStep, index]);

    const textComponent: JSX.Element = (
      <div
        className={classNames(
          `sb-step_${connected ? 'connected' : 'unconnected'}_text_${state}`,
        )}
      >
        {children}
      </div>
    );

    const iconComponent: JSX.Element = (
      <div className="sb-step-badge">
        {state === 'completed' ? (
          <Icon iconType="fa" iconName="fa-check" faPackage="fas" />
        ) : (
          indicator
        )}
      </div>
    );

    const connectedStep = {
      text: textComponent,
      number: iconComponent,
      both: (
        <>
          {iconComponent}
          {textComponent}
        </>
      ),
    };

    return (
      <li
        ref={ref}
        className={classNames(
          'sb-step',
          {
            'sb-step_connected': connected,
            'sb-step_unconnected': !connected,
            'sb-step_completed': state === 'completed',
            'sb-step_vertical': orientation === 'vertical',
          },
          className,
        )}
        {...otherProps}
      >
        {connected ? (
          <Badge
            shape={mode === 'number' ? 'circle' : 'pill'}
            className={classNames(`sb-step-badge_${state}`, {
              'sb-step_both': mode === 'both',
            })}
          >
            {connectedStep[mode]}
          </Badge>
        ) : (
          <div
            className={classNames({
              'sb-step_both': mode === 'both',
            })}
          >
            {mode !== 'text' && (
              <Badge
                shape="circle"
                className={classNames(`sb-step-badge_${state}`)}
              >
                {iconComponent}
              </Badge>
            )}
            {mode !== 'number' && textComponent}
          </div>
        )}
      </li>
    );
  },
);

Step.displayName = 'Step';
export default Step as FC<HTMLProps<HTMLLIElement> & StepProps>;
