import { FC, useMemo } from 'react';

// Styles
import './JourneySummary.scss';

// Types
import { JourneyItemType } from './types';

// Components
import Container from '../../../Container';
import Typography from '../../../Typography';
import Icon from '../../../Icon/Icon';
import JourneyRow from './DisplayTypes/TypeRow/TypeRow';
import classNames from 'classnames';
import Badge from '../../../Badge';

export interface JourneySummaryProps {
  /** Journey ID */
  journeyId: string;
  /** Title */
  title: string;
  //** Template Name */
  journeyTemplateName?: string;
  //** Precent Completed */
  percentCompleted?: number;
  //**Journey Values */
  journeyValues?: JourneyItemType[];
  //** Custom Classes for parent container */
  className?: string;
}

const JourneySummary: FC<JourneySummaryProps> = ({
  journeyId,
  title,
  journeyTemplateName,
  percentCompleted,
  journeyValues,
  className,
}): JSX.Element => {
  const parentClasses = useMemo(
    () => classNames('sb-cp-journey-summary-parent-layout', className),
    [className],
  );

  return (
    <Container className={parentClasses}>
      <Container>
        <Typography
          className="sb-cp-journey-summary-light-font"
          variant="T15"
          truncate={true}
        >
          Journey ID: {journeyId}
        </Typography>
        <div className="sb-cp-journey-summary-title-row">
          <Typography variant="T50" truncate>
            {title}
          </Typography>
          {journeyTemplateName && (
            <div className="sb-cp-journey-summary-icon-layout">
              <Badge size="small" color="base" light>
                {journeyTemplateName}
              </Badge>
            </div>
          )}
        </div>
        <div className="sb-cp-journey-summary-stepper-row">
          <Typography variant="T60">{percentCompleted}%</Typography>
        </div>
      </Container>
      <Container className="sb-cp-journey-summary-grid-layout">
        <Container className="sb-cp-journey-summary-columns">
          {journeyValues?.map((item) => {
            return <JourneyRow key={Math.random()} item={item} />;
          })}
        </Container>
      </Container>
    </Container>
  );
};

export default JourneySummary;
