import { FC, useEffect, useState } from 'react';

// Components
import Button from '../../../Button';
import Container from '../../../Container';
import InputGroup from '../../../InputGroup';
import Select from '../../../Select';
import SearchTypeaheadTemplate from '../SearchTypeaheadTemplate';

// Styles
import './SearchBar.scss';

// Types
import { ValueType } from 'react-select';

// Data
import { SearchResultBase } from '../SearchTypeaheadTemplate/SearchTypeaheadTemplate';

export interface SearchOption {
  value: string;
  label: string;
}

export interface EntityOption extends SearchOption {
  attributeOptions: SearchOption[];
}

export interface SearchBarProps {
  entityOptions: EntityOption[];
  onSearch: (
    entity: ValueType<SearchOption, false>,
    query: string,
    attribute: ValueType<SearchOption, false>,
  ) => void;
  onChange?: (
    entity: ValueType<SearchOption, false>,
    query: string,
    attribute: ValueType<SearchOption, false>,
  ) => void;
  onSelect?: (selected: SearchResultBase) => void;
  searchResults?: SearchResultBase[];
  onViewMoreClick?: SearchBarProps['onSearch'];
  placeholder?: string;
  isLoading?: boolean;
  initialQuery?: string;
  portal?: HTMLElement;
}

const SearchBar: FC<SearchBarProps> = ({
  entityOptions,
  onChange,
  onSearch,
  onSelect,
  searchResults,
  onViewMoreClick,
  placeholder = 'Enter at least 3 character to search',
  isLoading = false,
  initialQuery = '',
  portal,
}) => {
  const [vertical, setVertical] = useState(false);
  const [query, setQuery] = useState<string>(initialQuery);
  const [searchEntity, setEntity] = useState<ValueType<EntityOption, false>>(
    entityOptions[0],
  );
  const [searchAttribute, setSearchAttribute] = useState<
    ValueType<SearchOption, false>
  >(searchEntity?.attributeOptions[0]);

  useEffect(() => {
    setQuery(initialQuery);
  }, [initialQuery]);

  useEffect(() => {
    const handleResize = (): void => {
      const match = window.matchMedia('(max-width: 767px)').matches;
      setVertical(match);
    };
    handleResize();
    window.addEventListener('resize', handleResize);
    return () => window.removeEventListener('resize', handleResize);
  }, []);

  return (
    <Container
      padding={['normal', 'none', 'normal', 'none']}
      className="sb-cp-searchbar"
    >
      <InputGroup className="sb-cp-searchbar__input-group" vertical={vertical}>
        <Select
          aria-label="Search Entity"
          options={entityOptions}
          defaultValue={searchEntity}
          useMenuPortal
          menuPortalTarget={
            portal ?? (typeof window !== 'undefined' ? document.body : null)
          }
          className="sb-cp-searchbar__select"
          isDisabled={entityOptions.length <= 1}
          onChange={(value) => {
            setEntity(value);
            setSearchAttribute(value?.attributeOptions[0]);
            onChange?.(value, query, value?.attributeOptions[0]);
            setQuery('');
          }}
          blurInputOnSelect
        />
        <SearchTypeaheadTemplate
          className="sb-cp-searchbar__query"
          data-testid="search-input"
          value={query}
          onChange={(value) => {
            onChange?.(searchEntity, value, searchAttribute);
            setQuery(value);
          }}
          inputProps={{
            placeholder,
            onKeyUp: (e) => {
              if (e.key == 'Enter' || e.keyCode == 13) {
                const target = e.target as HTMLInputElement;
                onSearch?.(searchEntity, target.value, searchAttribute);
                setQuery(target.value);
              }
            },
          }}
          searchResults={searchResults}
          onSelect={(selected) => onSelect?.(selected)}
          onViewMoreClick={() =>
            onViewMoreClick?.(searchEntity, query, searchAttribute)
          }
          isLoading={isLoading}
        />
        <Select
          aria-label="Search Attribute"
          value={searchAttribute}
          options={searchEntity?.attributeOptions}
          useMenuPortal
          menuPortalTarget={
            portal ?? (typeof window !== 'undefined' ? document.body : null)
          }
          className="sb-cp-searchbar__select"
          isDisabled={
            searchEntity?.attributeOptions &&
            searchEntity.attributeOptions?.length <= 1
          }
          onChange={(value) => {
            onChange?.(searchEntity, query, value);
            setSearchAttribute(value);
          }}
          blurInputOnSelect
        />
      </InputGroup>
      <Button
        sizeType="small"
        labelText="Search"
        colorType="primary"
        data-testid="search-button"
        onClick={() => {
          onSearch(searchEntity, query, searchAttribute);
        }}
        disabled={query.trim().length === 0}
      >
        Search
      </Button>
    </Container>
  );
};

export default SearchBar;
