import { FC, useCallback, useMemo, useRef, useState } from 'react';
import { GridApi, IRowNode } from 'ag-grid-community';
import Typography from '../../Typography';
import Button from '../../Button';
import Container from '../../Container';
import ConfirmationModal from '../../ConfirmationModal';
import StandardTable, {
  MasterDetailProps,
  StandardTableCommonProps,
  StandardTableProps,
} from '../StandardTable';
import { Column } from '../StandardTable/types';
import OptionsCellRenderer from './OptionsCellRenderer';
import './EditableStandardTable.scss';
import classNames from 'classnames';

export type EditableStandardTableProps = StandardTableCommonProps &
  MasterDetailProps & {
    className?: string;
    title?: string;
    creatable?: boolean;
    deletable?: boolean;
    rowData: StandardTableProps['rowData'];
    onRowDeleted?: (node: IRowNode) => void;
    renderDeleteConfirmationModalTitle?: (node: IRowNode) => string;
    renderDeleteConfirmationModalText?: (node: IRowNode) => string;
  };
const EditableStandardTable: FC<EditableStandardTableProps> = ({
  columns,
  className,
  creatable,
  deletable,
  title,
  rowData,
  onRowDeleted,
  renderDeleteConfirmationModalTitle = () => 'Delete Record?',
  renderDeleteConfirmationModalText = () =>
    'Are you sure you want to delete this record?',
  ...standardTableProps
}) => {
  const [rowNodeToDelete, setRowNodeToDelete] = useState<IRowNode | null>(null);
  const dataGridApi = useRef<GridApi>(null);

  const firstEditableColKey = useMemo(
    () => columns.find((col) => col.definition?.editable)?.definition?.field,
    [columns],
  );
  if (!firstEditableColKey) {
    throw new Error('Identifiable editable column not found');
  }

  const handleAddRowClick = useCallback(async () => {
    if (!dataGridApi.current) {
      throw new Error('Data grid has not been set');
    }

    const result = dataGridApi.current.applyTransaction({
      add: [{}],
    });

    const addedRow = result?.add?.[0];
    if (typeof addedRow?.rowIndex !== 'number') {
      throw new Error('Something went wrong when attempting to add a row');
    }

    // NOTE: serFocusedCell and startEditingCell will fail if called immediately,
    // so we wait for a bit
    await new Promise((res) => setTimeout(res, 1));

    dataGridApi.current.setFocusedCell(addedRow.rowIndex, firstEditableColKey);
    dataGridApi.current.startEditingCell({
      rowIndex: addedRow.rowIndex,
      colKey: firstEditableColKey,
    });
  }, [firstEditableColKey]);

  const handleEditRow = useCallback(
    (node: IRowNode) => {
      if (node.rowIndex === null) {
        throw new Error('rowIndex not available');
      }
      if (!dataGridApi.current) {
        throw new Error('Data grid has not been set');
      }

      dataGridApi.current.startEditingCell({
        rowIndex: node.rowIndex,
        colKey: firstEditableColKey,
      });
    },
    [firstEditableColKey],
  );

  const columnsWithOptions = useMemo(() => {
    const optionsCol: Column = {
      type: 'custom',
      definition: {
        field: 'options',
        headerName: 'Options',
        cellRenderer: OptionsCellRenderer,
        cellRendererParams: {
          deletable,
          onDeleteRow: setRowNodeToDelete,
          onEditRow: handleEditRow,
        },
      },
    };

    return [...columns, optionsCol];
  }, [columns, deletable, handleEditRow]);

  return (
    <>
      <div className={classNames('sb-editable-standard-stable', className)}>
        {(creatable || title) && (
          <Container
            padding={['tight', 'loose', 'tight', 'loose']}
            className="sb-cp-editable-standard-table__header"
          >
            <div>{title && <Typography variant="T30">{title}</Typography>}</div>
            {creatable && (
              <Button
                sizeType="smaller"
                colorType="primary"
                type="button"
                onClick={handleAddRowClick}
              >
                Add
              </Button>
            )}
          </Container>
        )}
        <StandardTable
          gridClassName="sb-cp-editable-standard-table__grid"
          mode="client"
          autoHeight
          dataGridApiRef={dataGridApi}
          columns={columnsWithOptions}
          rowData={rowData}
          editType="fullRow"
          onRowEditingStarted={({ api, node }) => {
            api.refreshCells({
              columns: ['options'],
              rowNodes: [node],
              force: true,
            });
          }}
          onRowEditingStopped={({ api, node }) => {
            api.refreshCells({
              columns: ['options'],
              rowNodes: [node],
              force: true,
            });
          }}
          {...standardTableProps}
        />
      </div>
      {rowNodeToDelete && (
        <ConfirmationModal
          title={renderDeleteConfirmationModalTitle(rowNodeToDelete)}
          text={renderDeleteConfirmationModalText(rowNodeToDelete)}
          onConfirm={() => {
            if (!dataGridApi.current) {
              throw new Error('Data grid has not been set');
            }

            onRowDeleted?.(rowNodeToDelete);
            dataGridApi.current.applyTransaction({
              remove: [rowNodeToDelete.data],
            });
            setRowNodeToDelete(null);
          }}
          onClose={() => {
            setRowNodeToDelete(null);
          }}
        />
      )}
    </>
  );
};

export default EditableStandardTable;
