import { useMemo } from 'react';
import classNames from 'classnames';

// Components
import Container from '../../../Container';

// Types
import { SpacingOptions, TestMetadata } from 'types';

// Styles
import './SideNavSection.scss';

export interface SideNavSectionProps {
  /** Controls the padding: none, normal (default), tight, loose*/
  padding?: SpacingOptions | SpacingOptions[];
  /** Additional class */
  className?: string;
  // Test IDs for HeaderSection
  testMetadata?: TestMetadata;
}

const SideNavSection: React.FC<SideNavSectionProps> = ({
  padding = 'none',
  children,
  className,
  testMetadata,
  ...otherProps
}) => {
  const SideNavSectionClassNames = useMemo(
    () => classNames('sb-side-nav-section', className),
    [className],
  );

  return (
    <Container
      {...otherProps}
      padding={padding}
      className={SideNavSectionClassNames}
      {...testMetadata}
    >
      {children}
    </Container>
  );
};

export default SideNavSection;
