import React from 'react';

// Components
import Badge from '../../../Badge';

// Styles
import './ResultsBadge.scss';

// Types
import { TestMetadata } from '../../../types';

export interface ResultsBadgeProps {
  /** Count to show in the badge */
  visibleCount?: number;
  /** Count to show in the badge */
  totalCount?: number;
  /** What to label the number */
  entityName?: string;
  /** TestMetadata for badge */
  testMetadata?: TestMetadata;
}

const ResultsBadge: React.FC<ResultsBadgeProps> = ({
  visibleCount,
  totalCount = 0,
  entityName = 'Items',
  testMetadata,
}) => (
  <Badge
    className="sb-cp-results-badge"
    color="base"
    shape="pill"
    size="small"
    light
    {...testMetadata}
  >
    {`${
      visibleCount ? visibleCount.toLocaleString() + ' of ' : ''
    }${totalCount.toLocaleString()} ${entityName}`}
  </Badge>
);

export default ResultsBadge;
