import { ComponentProps, FC } from 'react';

// Components
import Button from '../Button';

// Types
import { TestMetadata } from 'types';

export type CancelButtonProps = ComponentProps<typeof Button> & {
  testMetadata?: TestMetadata;
};

const CancelButton: FC<CancelButtonProps> = ({
  children = 'Cancel',
  isFetching,
  styleType = 'demoted',
  sizeType = 'small',
  testMetadata,
  ...other
}) => (
  <Button
    type="button"
    className="sb-cancel-button sb-action-button"
    styleType={styleType}
    disabled={isFetching}
    isFetching={isFetching}
    sizeType={sizeType}
    {...testMetadata}
    {...other}
  >
    {children}
  </Button>
);

export default CancelButton;
