import { ReactElement } from 'react';

// Components
import Badge, { BadgeProps } from '../../../../Badge';
import Typography from '../../../../Typography';

// Types
import { SearchResultBase } from '../SearchTypeaheadTemplate';

// Styles
import './SearchResultRowTemplate.scss';

export type SearchResultRowTemplateProps<T extends SearchResultBase> = {
  /** The data to fill search result row */
  item: T;
  /** Event to fire when result selected */
  onSelect: (selectedResult: T) => void;
  /** Additional badge props */
  badgeProps?: BadgeProps;
};

export type SearchResultRow = <T extends SearchResultBase>(
  p: SearchResultRowTemplateProps<T>,
) => ReactElement<SearchResultRowTemplateProps<T>>;

const SearchResultRowTemplate: SearchResultRow = ({
  item,
  onSelect,
  badgeProps,
}) => {
  return (
    <div
      data-testid="searchResultRow"
      onClick={() => onSelect(item)}
      className="sb-cp-search-result-row"
    >
      <Typography variant="T15">{item.name}</Typography>
      <div className="sb-cp-search-result-metadata">
        {item?.metadata?.map((meta) => (
          <Badge
            light
            key={meta}
            color="primary"
            size="small"
            className="sb-cp-search-result-badge"
            {...badgeProps}
          >
            {meta}
          </Badge>
        ))}
      </div>
    </div>
  );
};

export default SearchResultRowTemplate;
