import {
  createContext,
  FC,
  useCallback,
  useEffect,
  useMemo,
  useRef,
  useState,
} from 'react';
import classNames from 'classnames';
import { isEqual, set, get } from 'lodash';

// Components
import TreeHeader from './TreeHeader';
import TreeContainer from './TreeContainer';
import TreeEmptyMessage from './TreeEmptyMessage';

// Styles
import './TreeBrowser.scss';

// Types
import { TreeAction, TreeContext, TreeEntity } from './types';
import { BadgeTypes, TestMetadata } from '../types';

export interface TreeBrowserProps {
  /* Tree Data */
  data: TreeEntity;
  /* Key of the display identifier (ie. name) of an entity in the object */
  primaryKey?: string;
  /* Key of the status identifier of an entity in the object */
  statusKey?: string;
  /* Key of the count identifier of an entity in the object */
  lengthKey?: string;
  /* Key of the ID identifier of an entity in the object (what you'll get back from callback functions) */
  idKey?: string;
  /* Entity Heirarchy Labels (Singular) - ie. ['Series', 'Season', 'Episode'] */
  levels?: string[];
  /* Map of status values to badge colors */
  statuses?: Record<string, BadgeTypes>;
  /* Map of action types to callback functions */
  actions?: Record<string, TreeAction>;
  /* Class Name */
  className?: string;
  /* Callback Function for the lowest level of the tree */
  onClickItem?: (
    id: string | number,
    node: TreeEntity,
    level: string | number,
  ) => void;
  testMetadata?: TestMetadata;
  /* Handle Creation */
  onCreate?: (
    parentId: string | number,
    data: TreeEntity,
    level: number | string,
  ) => Promise<TreeEntity | void>;
  /* Currently selected */
  activeItemId?: string | number;
  /* Hides header of Tree Browser */
  hideHeader?: boolean;
}

export const TreeBrowserContext = createContext<
  TreeContext | Record<string, never>
>({});

const TreeBrowser: FC<TreeBrowserProps> = ({
  lengthKey = 'count',
  primaryKey = 'name',
  statusKey = 'status',
  idKey = 'id',
  levels = ['Entity', 'Item'],
  data,
  className,
  actions,
  statuses,
  onClickItem,
  testMetadata,
  onCreate,
  activeItemId,
  hideHeader = false,
}) => {
  const [toggleAll, setToggleAll] = useState(true);
  const treeData = useRef(data);
  const [isSaving, setIsSaving] = useState('');
  const [, setRerender] = useState(0);

  const treeBrowserClassNames = useMemo(
    () => classNames(`sb-treebrowser`, className),
    [className],
  );

  const addTreeData = useCallback((path, value) => {
    if (!path) {
      treeData.current.data?.push(value);
    } else {
      const current = get(treeData.current, path);
      set(treeData.current, path, {
        ...current,
        data: [...current.data, value],
      });
    }
  }, []);

  const updateTreeData = useCallback((path, value) => {
    const current = path ? get(treeData.current, path) : treeData.current;
    if (current) {
      const newData = {
        ...current,
        data: current?.data?.map((item: TreeEntity) => {
          if (item.new) {
            return {
              ...item,
              ...value,
              new: false,
            };
          }
          return item;
        }),
      };

      if (path) {
        set(treeData.current, path, newData);
      } else {
        treeData.current = newData;
      }
    }
  }, []);

  const updateTree = useCallback(
    async (id, node, level, path, group) => {
      addTreeData(path, {
        new: true,
        [primaryKey]: 'Creating...',
        ...(group && { data: [] }),
      });
      await setIsSaving(id);
      const newItem = await onCreate?.(id, node, level);
      updateTreeData(path, {
        [primaryKey]: `New ${
          levels?.[level + 1] ? levels?.[level + 1] : 'Item'
        }`,
        ...newItem,
      });
      setIsSaving('');
    },
    [addTreeData, onCreate, levels, primaryKey, updateTreeData],
  );

  useEffect(() => {
    if (!isEqual(treeData.current, data)) {
      treeData.current = data;
      setRerender((i) => i + 1);
    }
  }, [data]);

  return (
    <div className={treeBrowserClassNames} {...testMetadata}>
      <TreeBrowserContext.Provider
        value={{
          updateTree: updateTree,
          onCreate: onCreate,
          primaryKey: primaryKey,
          lengthKey: lengthKey,
          idKey: idKey,
          statusKey: statusKey,
          isSaving: isSaving,
          levels: levels,
          statuses: statuses,
          actions: actions,
          onClickItem: onClickItem,
          setToggleAll: setToggleAll,
          toggleAll: toggleAll,
          activeItemId,
        }}
      >
        {!hideHeader && (
          <TreeHeader node={treeData.current} level={0} path="" />
        )}
        {treeData.current?.data?.length ? (
          <div className="sb-treebrowser__tree-data">
            <TreeContainer data={treeData.current.data} path="data" />
          </div>
        ) : (
          <TreeEmptyMessage level={1} />
        )}
      </TreeBrowserContext.Provider>
    </div>
  );
};

export default TreeBrowser;
