import React, { useState } from 'react';

// Components
import MenuList from '../../../MenuList';
import PopoverOverlayTemplate from '../PopoverOverlayTemplate';
import Button from '../../../Button';
import Popover from '../../../Popover';

// Styles
import './ActionButton.scss';

// Types
import { Option, TestMetadata } from '../../../types';

export interface ActionListButtonProps {
  selectId?: string;
  overlayTitle?: string;
  actionList: Option[];
  testMetadata?: TestMetadata;
  isFetching?: boolean;
  isDisabled?: boolean;
  buttonLabel?: string;
  buttonText?: string;
  buttonIcon?: string;
  onActionSelected: (selected: Option) => void;
}

const ActionListButton: React.FC<ActionListButtonProps> = ({
  selectId,
  overlayTitle,
  actionList,
  isFetching,
  isDisabled,
  onActionSelected,
  buttonIcon = 'fa-solid fa-ellipsis',
  buttonText,
  buttonLabel,
  testMetadata,
}) => {
  const [open, setOpen] = useState<boolean>(false);

  return (
    <Popover
      visible={open}
      popoverContent={
        <PopoverOverlayTemplate title={overlayTitle} flush elevated={false}>
          <MenuList
            id={selectId}
            options={actionList}
            onChange={(selected: Option) => {
              setOpen(false);
              onActionSelected(selected);
            }}
            showSelected={false}
          />
        </PopoverOverlayTemplate>
      }
      className="sb-cp-action-button__popover"
      tippyProps={{
        className: 'sb-cp-action-box',
        arrow: true,
        interactive: true,
        theme: 'light-border',
        placement: 'bottom-end',
        onClickOutside: () => setOpen(false),
      }}
    >
      <Button
        {...testMetadata}
        styleType="standard"
        iconType={buttonIcon}
        iconPlacement={buttonText ? 'after' : undefined}
        iconOnly={!buttonText}
        sizeType="smaller"
        onClick={() => setOpen(!open)}
        type="button"
        disabled={isDisabled || isFetching}
        isFetching={isFetching}
        labelText={buttonLabel}
        className="sb-cp-action-button__button"
      >
        {buttonText}
      </Button>
    </Popover>
  );
};

export default ActionListButton;
