import { FC, useContext } from 'react';
import { TreeBrowserContext } from './TreeBrowser';

// Components
import TreeLeaf from './TreeLeaf';
import TreeNode from './TreeNode';

// Types
import { TreeEntity } from './types';

export interface TreeContainerProps {
  /* Tree Data */
  data?: TreeEntity;
  /* Current level of the tree rendering */
  level?: number;
  path: string;
}

const TreeContainer: FC<TreeContainerProps> = ({ data, level = 1, path }) => {
  const { idKey } = useContext(TreeBrowserContext);
  return (
    <>
      {data?.map((node: TreeEntity, index: number) => {
        if (node?.data) {
          return (
            <TreeNode
              key={node?.[idKey] || index}
              node={node}
              level={level}
              path={`${path}[${index}]`}
            />
          );
        } else {
          return (
            <TreeLeaf key={node?.[idKey] || index} node={node} level={level} />
          );
        }
      })}
    </>
  );
};

export default TreeContainer;
