import { FC } from 'react';

// Components
import Button from '../Button';
import Status from './Status';

// Types
import { TestMetadata } from 'types';

export interface UnauthorizedProps {
  statusCode: number;
  appName?: string;
  detailedMessage?: string;
  requestText: string;
  issuesToolLink?: string;
  handleRefresh?: () => void;
  // Test IDs for component sections
  testMetadata?: {
    unauthorized?: TestMetadata;
    refreshPageButton?: TestMetadata;
  };
}

const Unauthorized: FC<UnauthorizedProps> = ({
  statusCode,
  appName,
  detailedMessage,
  testMetadata,
  requestText,
  issuesToolLink = 'https://issues.dmed.technology/report/ep',
  handleRefresh,
}) => {
  return (
    <>
      <h2 {...testMetadata?.unauthorized}>
        Sorry, you are not authorized to perform that action
        {appName ? ` in ${appName}` : ''}.
      </h2>
      {detailedMessage && <p>{detailedMessage}</p>}
      <p>
        {requestText}, go to{' '}
        <a href={issuesToolLink} target="_blank" rel="noopener noreferrer">
          the DMED Issues tool
        </a>
        {appName ? `, then enter "${appName}" as the app name` : ''}.
      </p>
      {statusCode && <Status statusCode={statusCode} />}
      {handleRefresh && (
        <Button
          className="sb-error-template_action-button"
          labelText="Refresh"
          iconType="fa-sync"
          colorType="primary"
          onClick={handleRefresh}
          {...testMetadata?.refreshPageButton}
        >
          Refresh page
        </Button>
      )}
    </>
  );
};

export default Unauthorized;
