import { FC } from 'react';

// Components
import Button from '../Button';
import Status from './Status';

// Types
import { TestMetadata } from 'types';

export interface InvalidUserProps {
  statusCode: number;
  appName?: string;
  handleRefresh?: () => void;
  // Test IDs for component sections
  testMetadata?: {
    invalidUser?: TestMetadata;
    refreshPageButton?: TestMetadata;
  };
}

const InvalidUser: FC<InvalidUserProps> = ({
  statusCode,
  appName,
  testMetadata,
  handleRefresh,
}) => {
  return (
    <>
      <h2 {...testMetadata?.invalidUser}>
        Sorry, you don&apos;t yet have access {appName ? ` to ${appName}` : ''}.
      </h2>
      <p>
        For any production support related issues or access request, please
        submit using{' '}
        <a
          href="https://issues.dmed.technology/report/ep"
          target="_blank"
          rel="noopener noreferrer"
        >
          the DMED Issues tool
        </a>
        {appName ? `, then enter "${appName}" as the app name` : ''}.
      </p>
      {statusCode && <Status statusCode={statusCode} />}
      {handleRefresh && (
        <Button
          className="sb-error-template_action-button"
          labelText="Refresh"
          iconType="fa-sync"
          colorType="primary"
          onClick={handleRefresh}
          {...testMetadata?.refreshPageButton}
        >
          Refresh page
        </Button>
      )}
    </>
  );
};

export default InvalidUser;
