import { FC } from 'react';
import classNames from 'classnames';

// Components
import Link, { LinkProps } from '../../../Link';
import Popover from '../../../Popover';
import Icon, { IconProps } from '../../../Icon';
import Typography from '../../../Typography';

// Types
import { TippyProps } from '@tippyjs/react';
import { TestMetadata } from 'types';

// Styles
import './PopoverTemplateItem.scss';

interface PopoverTemplateItemLinkProps {
  /** text to display in link */
  label?: string;
  /** different styles for the first level of the popover */
  isFirstLevel?: boolean;
  /** Custom class name(s) for component container */
  className?: string;
  /** Icon to show in the left side of the item */
  leftIconProps?: IconProps;
  /** Icon to show in the right side of the item */
  rightIconProps?: IconProps;
  /** Disables the Popover Template Item */
  disabled?: boolean;
}

const PopoverTemplateItemLink: FC<PopoverTemplateItemLinkProps> = ({
  label,
  className,
  isFirstLevel = false,
  leftIconProps,
  rightIconProps,
  disabled,
  ...otherProps
}) => {
  return (
    <Link
      className={classNames(
        'sb-cp-popover-template-item-link',
        isFirstLevel && 'sb-cp-popover-template-item-link__first-level',
        disabled && 'sb-cp-popover-template-item-link_disabled',
        className,
      )}
      disabled={disabled}
      {...otherProps}
    >
      {leftIconProps && (
        <span className="sb-cp-popover-template-item-link__left-icon">
          <Icon {...leftIconProps} />
        </span>
      )}
      <div className="sb-cp-popover-template-item-link__right">
        <Typography variant="T10">{label}</Typography>
        {rightIconProps && (
          <span className="sb-cp-popover-template-item-link__right-icon">
            <Icon {...rightIconProps} />
          </span>
        )}
      </div>
    </Link>
  );
};

export interface PopoverTemplateItemProps
  extends Omit<LinkProps, 'testMetadata'>,
    PopoverTemplateItemLinkProps {
  /** Items in the popover */
  items?: PopoverTemplateItemProps[];
  /** Props to be passed to the instanced Tippy component */
  tippyProps?: TippyProps;
  /** Custom class name(s) to be passed to the instanced Tippy component */
  tippyClassName?: string;
  // Test IDs for component sections
  testMetadata?: {
    PopoverTemplateItem?: TestMetadata;
    PopoverTemplateItemLink?: TestMetadata;
  };
}

const PopoverTemplateItem: FC<PopoverTemplateItemProps> = ({
  label,
  items,
  tippyProps,
  tippyClassName,
  testMetadata,
  ...otherProps
}) => {
  return items ? (
    <Popover
      popoverContent={items}
      tippyProps={{ ...tippyProps }}
      className={classNames(
        'sb-cp-popover-template-item__popover-wrapper',
        tippyClassName,
      )}
      {...testMetadata?.PopoverTemplateItem}
    >
      <PopoverTemplateItemLink
        {...testMetadata?.PopoverTemplateItemLink}
        {...otherProps}
        label={label}
      />
    </Popover>
  ) : (
    <PopoverTemplateItemLink
      {...testMetadata?.PopoverTemplateItemLink}
      {...otherProps}
      label={label}
    />
  );
};

export default PopoverTemplateItem;
