import { FC, useCallback, useContext, useMemo } from 'react';
import classNames from 'classnames';
import get from 'lodash/get';

// Components
import Typography from '../Typography';
import Icon from '../Icon';
import Badge from '../Badge';
import Spinner from '../Spinner';

// Styles
import './TreeLeaf.scss';

// Types
import { TreeEntity } from './types';
import { TreeBrowserContext } from './TreeBrowser';

export interface TreeLeafProps {
  /* Leaf Data */
  node: TreeEntity;
  /* Current Level */
  level: number;
}

const TreeLeaf: FC<TreeLeafProps> = ({ node, level }) => {
  const {
    onClickItem,
    levels,
    idKey,
    primaryKey,
    statuses,
    statusKey,
    activeItemId,
  } = useContext(TreeBrowserContext);

  const id = get(node, idKey),
    name = get(node, primaryKey),
    status = get(node, statusKey),
    badge = get(statuses, status),
    displayLevel = get(levels, level) || level,
    isNew = node.new,
    disabled = node.disabled,
    customClass = node.customClass,
    iconName = node.iconName;

  const leafClassNames = useMemo(
    () =>
      classNames('sb-treebrowser__leaf', customClass, {
        'sb-treebrowser__leaf-disabled': disabled,
        'sb-treebrowser__new': isNew,
        'sb-treebrowser__leaf-active': activeItemId === id,
      }),
    [customClass, disabled, isNew, activeItemId, id],
  );

  const onClick = useCallback(() => {
    if (disabled || isNew) return;
    onClickItem?.(id || name, node, displayLevel);
  }, [onClickItem, id, name, displayLevel, disabled, node, isNew]);

  return (
    <div
      className={leafClassNames}
      onClick={onClick}
      data-testid="treeleaf-item"
    >
      <div className="sb-treebrowser__leaf-left">
        <Icon
          iconType="fa"
          iconName={iconName ?? 'fa-list'}
          size="smaller"
          faPackage="fas"
          data-testid="leaf-icon"
        />
        <Typography
          className="sb-treebrowser__leaf-name"
          variant="T10"
          name={name}
        >
          {name}
        </Typography>
      </div>
      {status && (
        <Badge
          className="sb-treebrowser__leaf-badge sb-treebrowser__header-status"
          color={badge || 'primary'}
          light
          shape="pill"
          size="small"
          title={status}
        >
          {status}
        </Badge>
      )}
      {isNew && (
        <Spinner
          size="small"
          testMetadata={{ 'data-testid': 'loading-leaf' }}
        />
      )}
    </div>
  );
};

export default TreeLeaf;
