import { FC } from 'react';

// Components
import Button from '../Button';
import Status from './Status';

// Types
import { TestMetadata } from 'types';

export interface NotFoundProps {
  goBack: () => void;
  // Test IDs for component sections
  testMetadata?: {
    notFound?: TestMetadata;
    goBackButton?: TestMetadata;
  };
}

const NotFound: FC<NotFoundProps> = ({ goBack, testMetadata }) => (
  <>
    <h2 {...testMetadata?.notFound}>Sorry, page not found.</h2>
    <p>
      We couldnt find the page youre looking for. It may have been moved or
      deleted.
    </p>
    <Status statusCode={404} />
    <Button
      className="sb-error-template_action-button"
      labelText="Back"
      iconType="fa-arrow-left"
      colorType="primary"
      onClick={goBack}
      {...testMetadata?.goBackButton}
    >
      Go back
    </Button>
  </>
);

export default NotFound;
