import React, { SyntheticEvent } from 'react';

// Components
import { PopoverOverlayTemplate } from '../..';
import Icon from '../../../Icon';
import Typography from '../../../Typography';
import PopoverButton from '../../../PopoverButton';
import MenuList from '../../../MenuList';
import Button from '../../../Button';
import UtilityBar from '../../../UtilityBar';
import Container from '../../../Container';

// Types
import { SelectOption } from 'types';

// Styles
import './JourneyHeader.scss';
import classNames from 'classnames';

export interface JourneyHeaderProps {
  name?: string;
  className?: string;
  backButton: {
    title: string;
    onClick: (e: SyntheticEvent) => void;
  };
  popover?: {
    options: SelectOption[];
    onChange: (value: SelectOption) => void;
  };
  coPilot: {
    onClick: () => void;
  };
}

const JourneyHeader: React.FC<JourneyHeaderProps> = ({
  name,
  className,
  backButton,
  popover,
  coPilot,
}) => {
  return (
    <UtilityBar
      leftSection={
        <div className="sb-cp-journey-header__left-region">
          <Button
            onClick={backButton?.onClick}
            iconPlacement="before"
            iconType="fa-arrow-left"
            labelText={backButton?.title}
            className="sb-cp-journey-header__back-button"
            sizeType="auto"
            styleType="link"
            testMetadata={{ 'data-testid': 'journeyHeaderBackTitle' }}
          >
            {backButton?.title}
          </Button>
          <div className="sb-cp-journey-header__content">
            {name && (
              <Typography
                variant="T30"
                bold={true}
                truncate={true}
                className="sb-cp-journey-header__name"
                testMetadata={{ 'data-testid': 'journeyHeaderName' }}
              >
                {name}
              </Typography>
            )}
          </div>
        </div>
      }
      rightSection={
        <Container className="sb-cp-journey-header__right-region">
          {popover?.options && (
            <PopoverButton
              buttonProps={{
                className: 'sb-cp-journey-header__popover',
                testMetadata: {
                  'data-testid': 'journeyHeaderPopover',
                },
              }}
            >
              <PopoverOverlayTemplate flush>
                <MenuList
                  onChange={popover?.onChange}
                  options={popover?.options}
                />
              </PopoverOverlayTemplate>
            </PopoverButton>
          )}
          <div className="sb-cp-journey-header__separator" />
          <Container
            className="sb-cp-journey-header__copilot"
            onClick={coPilot.onClick}
            testMetadata={{ 'data-testid': 'journeyHeaderCopilot' }}
          >
            <Icon
              altText="side bar icon"
              faPackage="fa-light"
              iconName="fa-light fa-sidebar-flip"
              iconType="fa"
              className="sb-cp-journey-header__copilot-icon"
              size="medium"
            />
            <Icon
              className="sb-cp-journey-header__arrow"
              iconType="fa"
              size="smaller"
              faPackage="fa-solid"
              iconName="fa-chevron-right"
            />
          </Container>
        </Container>
      }
      className={classNames('sb-cp-journey-header', className)}
    />
  );
};

export default JourneyHeader;
