import { FC } from 'react';
import classNames from 'classnames';

// Components
import ContentBlock from '../ContentBlock';
import Notification from '../Notification';

// Hooks
import { isBrowserSupported } from '@dtci/triton/utils';

// Styles
import './BrowserNotSupported.scss';

// Types
import { TestMetadata } from 'types';

export interface BrowserNotSupportedProps {
  /** Class name **/
  className?: string;
  /** Supported browsers object **/
  supportedBrowsers: { [key: string]: string };
  // Test IDs for sections of BrowserNotSupported
  testMetadata?: TestMetadata;
}

const BrowserNotSupported: FC<BrowserNotSupportedProps> = ({
  className,
  children,
  supportedBrowsers,
  testMetadata,
  ...otherProps
}) =>
  typeof window !== 'undefined' && !isBrowserSupported(supportedBrowsers) ? (
    <ContentBlock
      {...otherProps}
      {...testMetadata}
      className={classNames('sb-browser-not-supported', className)}
    >
      <Notification
        banner
        notifType="warning"
        iconTypePrimary="fa-exclamation-triangle"
        role="alert"
        hideClose
        light
      >
        <div className="sb-notification__content">
          {children || (
            <>
              Your browser is not supported and the experience may be limited.
            </>
          )}
        </div>
      </Notification>
    </ContentBlock>
  ) : null;

export default BrowserNotSupported;
