import { FC, HTMLAttributes, ReactNode } from 'react';
import classNames from 'classnames';

//Styles
import './BadgedContainer.scss';

// Types
import { TestMetadata } from 'types';

export interface BadgedContainerBadgeProps {
  position: 'top-left' | 'top-right' | 'bottom-left' | 'bottom-right';
  class?: string;
  component: ReactNode;
}

export interface BadgedContainerProps extends HTMLAttributes<HTMLDivElement> {
  /** Class name **/
  className?: string;
  /** Children */
  children: ReactNode;
  /** Badges that are positioned on the container */
  badges: BadgedContainerBadgeProps | BadgedContainerBadgeProps[];
  // Test IDs for sections of BadgedContainer
  testMetadata?: TestMetadata;
}

const BadgedContainer: FC<BadgedContainerProps> = ({
  className,
  badges,
  children,
  testMetadata,
  ...otherProps
}) => {
  if (!Array.isArray(badges)) {
    badges = [badges];
  }
  return (
    <div
      className={classNames('sb-badged-container__container', className)}
      {...testMetadata}
      {...otherProps}
    >
      {badges.map((badge, index) => (
        <div
          key={index}
          className={classNames(
            `sb-badged-container__badge 
            sb-badged-container__${badge.position}-badge`,
            badge.class,
          )}
        >
          {badge.component}
        </div>
      ))}
      {children}
    </div>
  );
};

export default BadgedContainer;
