import {
  ComponentProps,
  FC,
  ReactEventHandler,
  RefObject,
  useMemo,
} from 'react';
import classNames from 'classnames';

// Components
import Image from '../Image';

// Styles
import './Avatar.scss';

// Types
import { TestMetadata } from 'types';

// Utils
import { IMAGE_URL } from '../_utils/config';

export interface AvatarProps extends ComponentProps<'div'> {
  /** Accessibility label (do not set if name appears with text) */
  accessibilityLabel?: string;
  /** Image URL (fallback if none provided)*/
  imgSrc?: string;
  /** Type of border radius: circle (default), rounded */
  radiusType?: 'circle' | 'rounded';
  /** Size: xs (32x32), small (40x40), medium (48x48, default), large (72x72), xl (120x120) */
  size?: 'xs' | 'small' | 'medium' | 'large' | 'xl';
  /** Class name */
  className?: string;
  /** Enable text mode */
  textMode?: boolean;
  /** Text to display */
  text?: string;
  /** Title to display when mouseover */
  title?: string;
  // Test IDs for sections of Avatar
  testMetadata?: {
    avatar?: TestMetadata;
    avatarImg?: TestMetadata;
  };
  /** Callback for image loading failures */
  onError?: ReactEventHandler<HTMLImageElement>;
  /** Image reference */
  imgRef?: RefObject<HTMLImageElement>;
}

export const avatarPlaceholder = `${IMAGE_URL}/avatar.jpg`;

const Avatar: FC<AvatarProps> = ({
  accessibilityLabel,
  imgSrc,
  radiusType = 'circle',
  size = 'medium',
  className,
  textMode = false,
  text = '',
  title,
  testMetadata = {
    avatar: { 'data-testid': 'avatar' },
    avatarImg: { 'data-testid': 'avatarImg' },
  },
  onError,
  imgRef,
  ...otherProps
}) => {
  const avatarClassNames = useMemo(
    () =>
      classNames(
        'sb-avatar',
        {
          'sb-avatar_circle': radiusType === 'circle',
          'sb-avatar_rounded': radiusType === 'rounded',
          'sb-avatar_xs': size === 'xs',
          'sb-avatar_small': size === 'small',
          'sb-avatar_medium': size === 'medium',
          'sb-avatar_large': size === 'large',
          'sb-avatar_xl': size === 'xl',
          'sb-avatar_text': textMode,
        },
        className,
      ),
    [className, radiusType, size, textMode],
  );

  return (
    <div {...otherProps} className={avatarClassNames} {...testMetadata?.avatar}>
      {!textMode && (
        <Image
          className="sb-avatar__image"
          src={imgSrc || avatarPlaceholder}
          alt={accessibilityLabel}
          title={title}
          onError={onError}
          ref={imgRef}
          {...testMetadata?.avatarImg}
        />
      )}
      {textMode && (
        <div className="sb-avatar__text">
          <div className="sb-avatar__text_content">{text}</div>
        </div>
      )}
    </div>
  );
};

export default Avatar;
