import { FC, ReactNode, useMemo } from 'react';
import classNames from 'classnames';

// Components
import Typography from '../../../Typography';

// Types
import { SizeOptions, TestMetadata } from 'types';

// Styles
import './HeaderTemplate.scss';

export interface HeaderTemplateProps {
  /** size - only medium or small for now */
  size?: SizeOptions;
  /** title */
  title?: string | ReactNode;
  /** classname */
  className?: string;
  // Test IDs for HeaderTemplate
  testMetadata?: TestMetadata;
}

const HeaderTemplate: FC<HeaderTemplateProps> = ({
  size = 'medium',
  title,
  children,
  className,
  testMetadata,
  ...otherProps
}) => {
  const headerTemplateClassNames = useMemo(
    () =>
      classNames(
        'sb-cp-header-template',
        `sb-cp-header-template_${size}`,
        className,
      ),
    [className, size],
  );

  return (
    <>
      <div
        className={headerTemplateClassNames}
        {...otherProps}
        {...testMetadata}
      >
        {children ??
          (title && (
            <Typography
              variant={size == 'small' ? 'T20' : 'T40'}
              bold={size == 'small'}
            >
              {title}
            </Typography>
          ))}
      </div>
    </>
  );
};

export default HeaderTemplate;
