import { FC, useContext, useEffect, useMemo, useState } from 'react';
import classNames from 'classnames';

// Components
import Container, { ContainerProps } from '../../../Container';
import DisplayActionButton from './DisplayActionButton';
import SortActionButton from './SortActionButton';

// Context
import { DataUtilityContext } from './DataUtilityWrapper';

// Styles
import './DataUtilityBarActions.scss';

// Types
import {
  ActionType,
  DataDefinition,
  Option,
  TestMetadata,
} from '../../../types';
import ActionListButton from './ActionListButton';

export interface DataUtilityBarActionsProps {
  /** Select ID's */
  ids?: {
    sort?: string;
    display?: string;
    export?: string;
    moreActions?: string;
  };
  /** Any additional menu options, shows up in ellipsis */
  moreActions?: Option[];
  onMoreActionSelected?: (selected: Option) => void;
  containerProps?: Omit<ContainerProps, 'children'>;
  className?: string;
  testMetadata?: {
    wrapper?: TestMetadata;
    sort?: TestMetadata;
    export?: TestMetadata;
    display?: TestMetadata;
    moreActions?: TestMetadata;
  };
}

const DataUtilityBarActions: FC<DataUtilityBarActionsProps> = ({
  ids,
  moreActions = [],
  onMoreActionSelected,
  className,
  containerProps,
  testMetadata,
  children, // children passed in are responsible for integrating with context themselves
}) => {
  const {
    isLoading,
    dataDefinition,
    actionState,
    onActionStateChange,
    exportOptions,
    onExportOptionSelected,
    isExporting,
  } = useContext(DataUtilityContext);

  const [dataDefinitionMap, setDataDefinitionMap] =
    useState<Record<ActionType, DataDefinition[]>>();

  useEffect(() => {
    const tempMap: Record<ActionType, DataDefinition[]> = {
      Sort: [],
      Display: [],
    };

    // Array for each action type
    const sortArray: DataDefinition[] = [],
      displayArray: DataDefinition[] = [];

    dataDefinition &&
      dataDefinition.map((def) => {
        // Need to map the data definitions by ActionType
        if (def.sortDefinition) {
          sortArray.push(def);
        }

        if (def.displayDefinition) {
          displayArray.push(def);
        }
      });

    tempMap[ActionType.Sort] = sortArray;
    tempMap[ActionType.Display] = displayArray;

    setDataDefinitionMap(tempMap);
  }, [dataDefinition]);

  const actionClassNames = useMemo(
    () => classNames('sb-data-utility-bar-actions', className),
    [className],
  );

  return (
    <Container
      className={actionClassNames}
      {...containerProps}
      testMetadata={testMetadata?.wrapper}
    >
      {children}

      {dataDefinitionMap && dataDefinitionMap[ActionType.Sort].length > 0 && (
        <SortActionButton
          selectId={ids?.sort}
          isDisabled={isLoading}
          dataDefinition={dataDefinitionMap.Sort}
          actionState={actionState?.filter(
            (state) => state.actionType.toString() === ActionType.Sort,
          )}
          onActionStateChange={onActionStateChange}
          testMetadata={testMetadata?.sort}
        />
      )}

      {dataDefinitionMap &&
        dataDefinitionMap[ActionType.Display].length > 0 && (
          <DisplayActionButton
            selectId={ids?.display}
            isDisabled={isLoading}
            dataDefinition={dataDefinitionMap.Display}
            actionState={actionState?.filter(
              (state) => state.actionType.toString() === ActionType.Display,
            )}
            onActionStateChange={onActionStateChange}
            testMetadata={testMetadata?.display}
          />
        )}

      {exportOptions && onExportOptionSelected && (
        <ActionListButton
          selectId={ids?.export}
          overlayTitle="Export"
          buttonText="Export"
          buttonIcon="fa-regular fa-angle-down"
          actionList={exportOptions}
          onActionSelected={onExportOptionSelected}
          testMetadata={testMetadata?.export}
          buttonLabel="Export"
          isDisabled={isLoading}
          isFetching={isExporting}
        />
      )}

      {moreActions.length > 0 && onMoreActionSelected && (
        <ActionListButton
          selectId={ids?.moreActions}
          actionList={moreActions}
          onActionSelected={onMoreActionSelected}
          testMetadata={testMetadata?.moreActions}
          buttonLabel="More Actions"
        />
      )}
    </Container>
  );
};

export default DataUtilityBarActions;
