import { FC, ReactNode, SyntheticEvent, useContext, useMemo } from 'react';
import classNames from 'classnames';

// Components
import Button from '../Button';
import Overlay from '../Overlay';
import {
  StandardLayout,
  HeaderPanel,
  BodyPanel,
  FooterPanel,
} from '../StandardLayout';

// Styles
import './Drawer.scss';

// Types
import { TestMetadata } from 'types';

// Analytics
import {
  AnalyticsContext,
  TAnalyticsEventData,
  useDrawerTrack,
} from '@dtci/dash/analytics';

// Hooks
import { usePrevious } from '@dtci/triton/hooks';

export interface DrawerProps {
  /** Determines if the drawer should shrink the main content when opened */
  shouldShrinkMainContent?: boolean;
  /** Determines if the drawer is open */
  isPanelOpen: boolean;
  /** Panel component to be rendered as an overlay from the right side */
  panel: ReactNode;
  /** Short descriptor of drawer (shows in header and for ARIA labeling) */
  header: ReactNode;
  /** Determines if drawer header should be hidden */
  hideHeader?: boolean;
  /** Determines if drawer header should be frozen */
  freezeHeader?: boolean;
  /** Determines if close button on drawer should be hidden */
  hideClose?: boolean;
  /** Close button function */
  onClose?: (e: SyntheticEvent) => void;
  /** Class name for drawer container */
  className?: string;
  /** Animation direction for the drawer */
  position?: 'left' | 'right';
  /** Type of page overlay: blackout, whiteout */
  bodyOverlay?: 'blackout' | 'whiteout';
  // Test IDs for component sections
  testMetadata?: {
    drawerMain?: TestMetadata;
    drawerOverlay?: TestMetadata;
    drawerOverlayHeader?: TestMetadata;
    drawerOverlayCloseButton?: TestMetadata;
    drawerFooter?: TestMetadata;
  };
  /** Optional ReactNode to put in the footer of the drawer */
  footer?: ReactNode;
  size?: 'normal' | '80' | '85' | '90' | '95';
  /** Additional event information to pass to analytics tracking */
  analyticsEventData?: TAnalyticsEventData;
}

const Drawer: FC<DrawerProps> = ({
  isPanelOpen,
  shouldShrinkMainContent,
  panel,
  header,
  hideHeader,
  freezeHeader = false,
  hideClose,
  onClose = () => {
    // noop
  },
  className,
  children,
  position = 'right',
  bodyOverlay,
  footer,
  testMetadata = {
    drawerMain: { 'data-testid': 'drawerMain' },
    drawerOverlay: { 'data-testid': 'drawerOverlay' },
    drawerOverlayHeader: { 'data-testid': 'drawerOverlayHeader' },
    drawerOverlayCloseButton: { 'data-testid': 'drawerOverlayCloseButton' },
    drawerFooter: { 'data-testid': 'drawerFooter' },
  },
  size = 'normal',
  analyticsEventData,
  ...otherProps
}) => {
  const prevIsPanelOpen = usePrevious(isPanelOpen);

  const DrawerOverlayStyles = useMemo(
    () =>
      classNames(
        `sb-drawer__overlay${position === 'right' ? '' : '__' + position}`,
        {
          [`sb-drawer__overlay_is-open${
            position === 'right' ? '' : '__' + position
          }`]: isPanelOpen,
        },
      ),
    [isPanelOpen, position],
  );

  const mainLayoutStyles = useMemo(
    () =>
      classNames({
        [`sb-drawer__main_is-shrunk${
          position === 'right' ? '' : '__' + position
        }`]: shouldShrinkMainContent && isPanelOpen,
        'sb-drawer__main_is-open': isPanelOpen,
      }),
    [shouldShrinkMainContent, isPanelOpen, position],
  );

  const drawerClassNames = useMemo(
    () =>
      classNames(
        'sb-drawer',
        `sb-drawer__size-${size}`,
        {
          'sb-drawer_can-shrink': shouldShrinkMainContent,
          'sb-drawer_can-overlay': !shouldShrinkMainContent,
        },
        className,
      ),
    [className, shouldShrinkMainContent, size],
  );

  const closeButtonClassNames = useMemo(
    () =>
      classNames('sb-drawer__close', {
        'sb-drawer__close_hide-header': hideHeader,
      }),
    [hideHeader],
  );

  const analyticsContext = useContext(AnalyticsContext);

  useDrawerTrack(
    { analyticsContext, analyticsEventData },
    isPanelOpen,
    prevIsPanelOpen,
    {
      header,
      className,
    },
  );

  return (
    <div {...otherProps} className={drawerClassNames} data-testid="drawerPanel">
      <section
        className={classNames('sb-drawer__main', mainLayoutStyles)}
        {...testMetadata?.drawerMain}
      >
        {children}
        <Overlay
          isVisible={isPanelOpen && bodyOverlay !== undefined}
          type={bodyOverlay === 'whiteout' ? 'light' : 'dark'}
        />
      </section>
      <aside
        className={classNames('sb-drawer__overlay', DrawerOverlayStyles)}
        {...testMetadata?.drawerOverlay}
      >
        <StandardLayout>
          {!hideHeader && (
            <HeaderPanel
              className="sb-drawer__header"
              isScrollable={freezeHeader}
              {...testMetadata?.drawerOverlayHeader}
            >
              {header}
              {!hideClose && (
                <Button
                  sizeType="large"
                  iconOnly
                  tabIndex={isPanelOpen ? 0 : -1}
                  iconType="fa-times"
                  styleType="demoted"
                  labelText="Close"
                  className={closeButtonClassNames}
                  onClick={onClose}
                  analyticsEventData={{
                    componentName: 'Drawer Close Button',
                  }}
                  {...testMetadata?.drawerOverlayCloseButton}
                />
              )}
            </HeaderPanel>
          )}
          <BodyPanel isScrollable={freezeHeader}>
            {hideHeader && !hideClose && (
              <Button
                sizeType="large"
                iconOnly
                iconType="fa-times"
                styleType="demoted"
                labelText="Close"
                className={closeButtonClassNames}
                onClick={onClose}
                analyticsEventData={{
                  componentName: 'Drawer Close Button',
                }}
                {...testMetadata?.drawerOverlayCloseButton}
              />
            )}
            {panel}
          </BodyPanel>
          {footer && (
            <FooterPanel {...testMetadata?.drawerFooter}>{footer}</FooterPanel>
          )}
        </StandardLayout>
      </aside>
    </div>
  );
};

export default Drawer;
