import React, { forwardRef } from 'react';
import classNames from 'classnames';

// Components
import { Handle, Remove } from '../components';
import Container from '../../Container';

// Styles
import './DndContainer.scss';

// Types
import { ActionProps } from 'DnD/components/Action';
export interface Props {
  children: React.ReactNode;
  /**The number of columns the container will have*/
  columns?: number;
  /**A string that will be displayed as the container's header, if provided*/
  label?: string;
  /**A React.CSSProperties object that can be used to style the container*/
  style?: React.CSSProperties;
  /**A boolean value that determines if the container's items should be displayed horizontally instead of vertically*/
  horizontal?: boolean;
  /**A boolean value that determines if the container should display a hover effect when the mouse is over it*/
  hover?: boolean;
  /**An object of type ActionProps that is used to configure the Handle component that appears in the container's header */
  handleProps?: ActionProps;
  /**A boolean value that determines if the container should be scrollable. */
  scrollable?: boolean;
  /** A boolean value that determines if the container should have a shadow effect. */
  shadow?: boolean;
  /**A boolean value that determines if the container should render its children as a placeholder. This is used in the context of drag-and-drop interactions */
  placeholder?: boolean;
  /**A boolean value that determines if the container should not apply any default styles. */
  unstyled?: boolean;
  /**A function that will be called when the container is clicked */
  onClick?(): void;
  /**A function that will be called when the container's remove button is clicked. This is used in the context of drag-and-drop interactions */
  onRemove?(): void;
}

const DndContainer = forwardRef<HTMLDivElement, Props>(
  (
    {
      children,
      columns = 1,
      handleProps,
      horizontal,
      hover,
      onClick,
      onRemove,
      label,
      placeholder,
      style,
      scrollable,
      shadow,
      unstyled,
      ...props
    }: Props,
    ref,
  ) => {
    return (
      <Container
        {...props}
        containerRef={ref}
        style={
          {
            ...style,
            '--columns': columns,
          } as React.CSSProperties
        }
        className={classNames('sb-dnd-container', {
          'sb-dnd-container__unstyled': unstyled,
          'sb-dnd-container__horizontal': horizontal,
          'sb-dnd-container__hover': hover,
          'sb-dnd-container__placeholder': placeholder,
          'sb-dnd-container__scrollable': scrollable,
          'sb-dnd-container__shadow': shadow,
        })}
        onClick={onClick}
        tabIndex={onClick ? 0 : undefined}
      >
        {label ? (
          <div className="sb-dnd-container__header">
            {label}
            <div className="sb-dnd-container__header__actions">
              {onRemove ? <Remove onClick={onRemove} /> : undefined}
              <Handle {...handleProps} />
            </div>
          </div>
        ) : null}
        {placeholder ? (
          children
        ) : (
          <div className="sb-dnd-container__list">{children}</div>
        )}
      </Container>
    );
  },
);

export default DndContainer;
