import { FC, HTMLAttributes, ReactNode } from 'react';
import classNames from 'classnames';

// Components
import Icon from '../Icon';
import Badge from '../Badge';

// Types
import { BadgeTypes, TestMetadata } from 'types';

// Styles
import './Task.scss';

export interface TaskProps extends HTMLAttributes<HTMLDivElement> {
  /* Font Awesome Icon Name */
  icon?: string;
  /* Hide the icon */
  iconHidden?: boolean;
  /* Color of the icon */
  color?: BadgeTypes;
  /* Custom class name */
  className?: string;
  /* Test Metadata */
  testMetadata?: TestMetadata;
  /* Alt-Text of the Icon */
  altText?: string;
  /** Elements to Wrap */
  children: ReactNode;
}

const Task: FC<TaskProps> = ({
  icon,
  iconHidden = false,
  color = 'base',
  className,
  testMetadata,
  children,
  altText = 'Task',
  ...other
}) => {
  return (
    <div
      className={classNames('sb-task', className, {
        'sb-task--badge': !iconHidden,
      })}
      {...other}
      {...testMetadata}
    >
      {iconHidden ? (
        <div className="sb-task_task-container-no-badge">{children}</div>
      ) : (
        <>
          <Badge
            shape="circle"
            size="small"
            color={color}
            light
            iconOnly
            className="sb-task_task-badge"
          >
            {icon && (
              <Icon
                faPackage="fas"
                iconType="fa"
                className="sb-task_task-icon"
                altText={altText}
                iconName={icon}
                size="smaller"
              />
            )}
          </Badge>
          <div className="sb-task_task-container">{children}</div>
        </>
      )}
    </div>
  );
};

export default Task;
