import { useMemo } from 'react';
import classNames from 'classnames';

// Components
import { SideNavSection } from '../../Sections';
import { SideNavRendition } from '../../Renditions';
import {
  DualPanelLayout,
  PrimaryPanel,
  SecondaryPanel,
} from '../../../DualPanelLayout';

// Types
import { TestMetadata } from 'types';

// Styles
import './DomainCanvas.scss';

export interface DomainCanvasProps {
  /** navigation components */
  nav?: React.ReactNode;
  /** Additional class */
  className?: string;
  // Test IDs for System
  testMetadata?: {
    dualPanel?: TestMetadata;
    rendition?: TestMetadata;
    section?: TestMetadata;
  };
}

const DomainCanvas: React.FC<DomainCanvasProps> = ({
  nav,
  children,
  className,
  testMetadata,
  ...otherProps
}) => {
  const dualPanelClassNames = useMemo(
    () => classNames('sb-cp-domain-canvas', className),
    [className],
  );

  return (
    <DualPanelLayout
      {...otherProps}
      className={dualPanelClassNames}
      {...testMetadata?.dualPanel}
    >
      <SecondaryPanel>
        <SideNavRendition {...testMetadata?.rendition}>
          <SideNavSection {...testMetadata?.section}>{nav}</SideNavSection>
        </SideNavRendition>
      </SecondaryPanel>
      <PrimaryPanel>{children}</PrimaryPanel>
    </DualPanelLayout>
  );
};

export default DomainCanvas;
