import { FC, ReactChild } from 'react';

// Components
import Typography from '../../../Typography';
import Badge, { BadgeProps } from '../../../Badge';

// Types
import { BreadcrumbsProps } from 'Breadcrumbs';
import { PillboxProps } from 'Pillbox';

// Styles
import './EntityHeaderTemplate.scss';
import { ButtonProps } from 'Button';
import React from 'react';

export interface EntityHeaderTemplateProps {
  title: string;
  maxChipsVisible?: number;
  maxSubtextAttributesVisible?: number;
  subtextAttributes?: string[];
  meta?: string;
  breadcrumbs?: React.ReactElement<BreadcrumbsProps>;
  statusProps?: BadgeProps & { children: ReactChild };
  chips?: React.ReactElement<PillboxProps>[];
  options?: React.ReactElement<ButtonProps>[];
}

const EntityHeaderTemplate: FC<EntityHeaderTemplateProps> = ({
  title,
  subtextAttributes = [],
  maxChipsVisible = 5,
  maxSubtextAttributesVisible = 4,
  meta = '',
  breadcrumbs = null,
  statusProps,
  chips = [],
  options = [],
}) => {
  const subtextAttributesText: string = React.useMemo(() => {
    return subtextAttributes.slice(0, maxSubtextAttributesVisible).join('  ');
  }, [subtextAttributes, maxSubtextAttributesVisible]);

  const visibleChips = React.useMemo(() => {
    return chips.slice(0, maxChipsVisible);
  }, [chips, maxChipsVisible]);

  return (
    <div
      data-testid="sb-cp-entity-header-template"
      className="sb-cp-entity-header-template"
    >
      <div className="sb-cp-entity-header-template__row sb-cp-entity-header-template__breadcrumbs-row">
        {breadcrumbs && (
          <div className="sb-cp-entity-header-template__breadcrumbs">
            {breadcrumbs}
          </div>
        )}
        <Typography
          variant="T15"
          className="sb-cp-entity-header-template__meta"
        >
          {meta}
        </Typography>
      </div>
      <div className="sb-cp-entity-header-template__row sb-cp-entity-header-template__title-row">
        <div className="sb-cp-entity-header-template__title">
          <Typography variant="T60">{title}</Typography>
          {statusProps && <Badge {...statusProps} />}
        </div>
        <div className="sb-cp-entity-header-template__options">
          {options.map((option, index) => (
            <React.Fragment key={`sb-cp-entity-header-template-chip-${index}`}>
              {option}
            </React.Fragment>
          ))}
        </div>
      </div>
      {subtextAttributes && (
        <div className="sb-cp-entity-header-template__row sb-cp-entity-header-template__subtext-row">
          <Typography
            variant="T20"
            className="sb-cp-entity-header-template__subtext"
          >
            {subtextAttributesText}
          </Typography>
        </div>
      )}
      <div className="sb-cp-entity-header-template__row">
        <div className="sb-cp-entity-header-template__chips">
          {visibleChips.map((chip, index) => (
            <React.Fragment key={`sb-cp-entity-header-template-chip-${index}`}>
              {chip}
            </React.Fragment>
          ))}
        </div>
      </div>
    </div>
  );
};

export default EntityHeaderTemplate;
