import {
  createContext,
  DetailedHTMLProps,
  FC,
  forwardRef,
  HTMLProps,
  OlHTMLAttributes,
  useMemo,
} from 'react';
import classNames from 'classnames';

// Styles
import './Steps.scss';

// Types
import { TestMetadata } from 'types';
import React from 'react';

interface StepsContextProps {
  /** Show steps connected */
  connected?: boolean;
  /** Selected Step  */
  currentStep: number;
  /** Steps fills the width of the container  */
  fullWidth?: boolean;
  /** Badge mode of Steps */
  mode: 'text' | 'number' | 'both';
  /** Direction of Steps */
  orientation?: 'horizontal' | 'vertical';
}
export const StepsContext = createContext<StepsContextProps>({
  connected: false,
  currentStep: 0,
  fullWidth: false,
  mode: 'number',
  orientation: 'horizontal',
});

export interface StepsProps
  extends DetailedHTMLProps<
    OlHTMLAttributes<HTMLOListElement>,
    HTMLOListElement
  > {
  /** Classname of Steps */
  className?: string;
  /** Show steps connected */
  connected?: boolean;
  /** Active step */
  currentStep: number;
  /** Fill container's width */
  fullWidth?: boolean;
  /** Badge mode of Steps */
  mode?: 'text' | 'number' | 'both';
  /** Direction of Steps */
  orientation?: 'horizontal' | 'vertical';
  /** Test data IDs for Steps */
  testMetadata?: TestMetadata;
}

const Steps = forwardRef<HTMLOListElement, StepsProps>(
  (
    {
      children,
      className,
      connected,
      currentStep,
      fullWidth,
      mode = 'number',
      type = '1',
      orientation = 'horizontal',
      ...otherProps
    },
    ref,
  ) => {
    const stepsClassNames = useMemo(
      () =>
        classNames(
          'sb-steps',
          {
            'sb-steps_full-width': fullWidth,
            'sb-steps_connected': connected,
            'sb-steps_vertical': orientation === 'vertical',
          },
          className,
        ),
      [connected, fullWidth, className, orientation],
    );

    const contextValue = useMemo(
      () => ({
        connected,
        currentStep,
        fullWidth,
        mode,
        orientation,
      }),
      [connected, currentStep, fullWidth, mode, orientation],
    );

    return (
      <StepsContext.Provider value={contextValue}>
        <ol ref={ref} type={type} className={stepsClassNames} {...otherProps}>
          {children}
        </ol>
      </StepsContext.Provider>
    );
  },
);

Steps.displayName = 'Steps';
export default Steps as FC<HTMLProps<HTMLOListElement> & StepsProps>;
