import {
  FC,
  useMemo,
  useCallback,
  useContext,
  useEffect,
  useState,
} from 'react';
import classNames from 'classnames';
import { TreeBrowserContext } from './TreeBrowser';

//Components
import TreeNodeHeader from './TreeNodeHeader';
import CollapsiblePanel from '../CollapsiblePanel';
import Icon from '../Icon';
import TreeContainer from './TreeContainer';
import TreeEmptyMessage from './TreeEmptyMessage';

// Types
import { TreeEntity } from './types';

// Styles
import './TreeNode.scss';

export interface TreeNodeProps {
  /* Node Data */
  node: TreeEntity;
  /* Current level in the tree */
  level?: number;
  /* Current Path */
  path: string;
}

const TreeNode: FC<TreeNodeProps> = ({ node, level = 1, path }) => {
  const { toggleAll, activeItemId } = useContext(TreeBrowserContext);
  const [open, setOpen] = useState(true);

  const isActivated = node.id === activeItemId;
  const treeNodeHeaderClassNames = useMemo(
    () =>
      classNames('sb-treebrowser__header-center', node.customClass, {
        'sb-treebrowser__header-center-selected': isActivated,
      }),
    [isActivated, node.customClass],
  );

  const toggleOpen = useCallback(() => {
    setOpen(!open);
  }, [open]);

  useEffect(() => {
    setOpen(toggleAll);
  }, [toggleAll]);

  return (
    <>
      {node.data && node.data.length > 0 ? (
        <CollapsiblePanel
          toggleBarClick="icon"
          renderToggleBar={() => (
            <TreeNodeHeader node={node} level={level} path={path} />
          )}
          borderless
          toggleIcon="chevron-down"
          isOpen={open}
          onToggleOpen={toggleOpen}
          className={`sb-treebrowser__level_${level} sb-treebrowser__header-container`}
          toggleClass="sb-treebrowser__header-icon"
          selected={isActivated}
          selectable={!node.disabled}
        >
          <TreeContainer
            data={node.data}
            level={level + 1}
            path={`${path}.data`}
          />
        </CollapsiblePanel>
      ) : (
        <>
          <div
            className={treeNodeHeaderClassNames}
            data-testid="tree-node-no-children"
          >
            <Icon
              iconType="fa"
              iconName={node.iconName ?? 'fa-list'}
              size="smaller"
              faPackage="fas"
              className="sb-treebrowser__header-list"
            />
            <TreeNodeHeader node={node} level={level} path={path} />
          </div>
          {!node.hideEmptyMessage && <TreeEmptyMessage level={level + 1} />}
        </>
      )}
    </>
  );
};

export default TreeNode;
