export enum JourneyState {
  Active = 'active',
  Canceled = 'canceled',
  Completed = 'completed',
  AtRisk = 'at risk',
}

export enum JourneyStatus {
  InProgress = 'In Progress',
  Late = 'Late',
  Paused = 'Paused',
  Resumed = 'Resumed',
  Done = 'Done',
}

export type JourneyItemType = {
  journeyType:
    | 'state'
    | 'status'
    | 'workflow'
    | 'due'
    | 'initiated'
    | 'priority'
    | 'content'
    | 'platform'
    | 'general';
  title?: string;
  state?: JourneyState;
  status?: JourneyStatus;
  start?: string;
  end?: string;
  due?: string;
  initiated?: string;
  general?: string;
};
