import { ChangeEvent, FC, useCallback, useMemo, useState } from 'react';

// Components
import CollapsiblePanel from '../CollapsiblePanel';
import Container from '../Container';
import Input from '../Input';
import MultiMenuList from '../MultiMenuList';
import Pillbox from '../Pillbox';

//Styles
import './MenuListSearch.scss';

// Types
import { SelectOption } from '../types';

export interface MenuListSearchProps {
  // Options that populate the menu list
  options: SelectOption[];
  // Option(s) change event
  onSelectOptions: (options: SelectOption[]) => void;
  // Selected option(s)
  selectedOptions: SelectOption[];
  // Show the collapsible panel
  showSelectedPanel?: boolean;
}

const MenuListSearch: FC<MenuListSearchProps> = ({
  options,
  onSelectOptions,
  selectedOptions,
  showSelectedPanel,
}) => {
  const [searchTerm, setSearchTerm] = useState<string>('');
  const [isOpen, setIsOpen] = useState(false);

  const handlePillboxDelete = useCallback(
    (currentLabel: string) => () => {
      onSelectOptions(
        selectedOptions.filter(
          (selectedOption) => selectedOption.label !== currentLabel,
        ),
      );
    },
    [onSelectOptions, selectedOptions],
  );

  const handleInputSearchChange = (e: ChangeEvent<HTMLInputElement>): void => {
    setSearchTerm(e.target.value);
  };

  const filterOptions = useMemo(
    () =>
      options.filter((filterOption: SelectOption) =>
        filterOption.label.toLowerCase().includes(searchTerm.toLowerCase()),
      ),
    [searchTerm, options],
  );

  return (
    <Container
      fill="white"
      className="sb-menu-list-search"
      data-testid="pg-display-dropdown"
    >
      {showSelectedPanel && (
        <CollapsiblePanel
          className="sb-menu-list-search-collapsible-panel"
          isOpen={isOpen}
          onToggleOpen={setIsOpen}
          toggleBarLocation="top"
          renderToggleBar={(isOpen) =>
            isOpen ? <div>Hide Selected</div> : <div>Show Selected</div>
          }
        >
          <Container className="sb-menu-list-search-selected">
            {selectedOptions.map((option) => (
              <span key={option.label} title={option.label}>
                <Pillbox
                  shape="pill"
                  size="small"
                  className="sb-menu-list-search-badge"
                  key={option.label}
                  onClose={handlePillboxDelete(option.label)}
                >
                  {option.label}
                </Pillbox>
              </span>
            ))}
          </Container>
        </CollapsiblePanel>
      )}
      <Input
        className="sb-menu-list-search-input"
        data-testid="sb-menu-list-search-input"
        placeholder="Search"
        width="auto"
        type="text"
        onChange={handleInputSearchChange}
      />
      <MultiMenuList
        options={filterOptions}
        selectedOptions={selectedOptions}
        onChange={onSelectOptions}
      />
    </Container>
  );
};

export default MenuListSearch;
