import { FC, useState, useCallback, useMemo } from 'react';
import classNames from 'classnames';

// Components
import Button from '../../Button';
import NavBarItem from '../../NavBarItem';

// Types
import { TestMetadata } from 'types';

// Styles
import './Controls.scss';

export interface ControlsProps {
  /** Classname for Controls section */
  className?: string;
  /** TestMetadata for container and expand button */
  testMetadata?: {
    container?: TestMetadata;
    expandButton?: TestMetadata;
  };
}

const Controls: FC<ControlsProps> = ({ className, testMetadata, children }) => {
  const [collapse, setCollapse] = useState<boolean>(true);

  const hamburgerClick = useCallback(() => {
    setCollapse(!collapse);
  }, [collapse]);

  const controlsClassNames = useMemo(
    () =>
      classNames(
        'sb-controls',
        {
          'sb-controls__collapsed': collapse,
          'sb-controls__open': !collapse,
        },
        className,
      ),
    [className, collapse],
  );

  return (
    <>
      <NavBarItem className={controlsClassNames} {...testMetadata?.container}>
        {children}
      </NavBarItem>

      {children && (
        <Button
          className="sb-controls__button"
          iconType={collapse ? 'fa-bars' : 'fa-times'}
          iconOnly
          labelText="Toggle Controls Section"
          styleType="link"
          sizeType="large"
          {...testMetadata?.expandButton}
          onClick={hamburgerClick}
          role="button"
        />
      )}
    </>
  );
};

export default Controls;
