import { ComponentProps, FC } from 'react';
import classNames from 'classnames';
import { NavLink, NavLinkProps } from 'react-router-dom';

// Styles
import './NavBarLink.scss';

// Types
import { TestMetadata } from 'types';

export interface NavBarLinkProps extends ComponentProps<'li'> {
  /** Path for link to go to */
  path: string;
  /** Custom class to give to li */
  className?: string;
  /** NavLinkProps override props to spread to link */
  linkProps?: NavLinkProps;
  /** TestMetadata to use on li and link */
  testMetadata?: {
    container?: TestMetadata;
    link?: TestMetadata;
  };
}

const NavBarLink: FC<NavBarLinkProps> = ({
  path,
  className,
  testMetadata,
  children,
  linkProps,
  ...otherProps
}) => {
  return (
    <li
      role="navigation"
      className={classNames('sb-navbar-link', className)}
      {...testMetadata?.container}
      {...otherProps}
    >
      <NavLink
        to={path}
        {...testMetadata?.link}
        className={(isActive) =>
          isActive
            ? 'sb-navbar-link__active-link'
            : 'sb-navbar-link__non-active-link'
        }
        {...linkProps}
      >
        {children}
      </NavLink>
    </li>
  );
};

export default NavBarLink;
