import { useState, useMemo, useCallback, FC, ReactNode } from 'react';
import classNames from 'classnames';

// Components
import ContentGridSkeleton from './ContentGridSkeleton';

// Styles
import './ContentGrid.scss';

// Types
import { TestMetadata } from 'types';

export interface ContentGridProps {
  /** Class name for Content Grid */
  className?: string;
  /** Size of the items */
  size?: 'smaller' | 'small' | 'medium' | 'large' | 'larger';
  itemCount?: number;
  isFetching?: boolean;
  fetchingSkeleton?: ReactNode;
  // Test IDs for component sections
  testMetadata?: TestMetadata;
}

const ContentGrid: FC<ContentGridProps> = ({
  children,
  className,
  isFetching,
  fetchingSkeleton,
  size = 'medium',
  itemCount,
  testMetadata,
  ...otherProps
}) => {
  const [width, setWidth] = useState(0);
  const contentGridClassNames = useMemo(
    () =>
      classNames(
        'sb-content-grid',
        size && `sb-content-grid_${size}`,
        className,
      ),
    [size, className],
  );

  const container = useCallback((node) => {
    if (node !== null) {
      setWidth(node.getBoundingClientRect().width);
    }
  }, []);

  return (
    <div
      ref={container}
      {...otherProps}
      className={contentGridClassNames}
      {...testMetadata}
    >
      {isFetching ? (
        fetchingSkeleton ? (
          fetchingSkeleton
        ) : (
          <ContentGridSkeleton
            width={width}
            size={size}
            itemCount={itemCount}
          />
        )
      ) : (
        children
      )}
    </div>
  );
};

export default ContentGrid;
