import { FC } from 'react';

// Components
import { Panel, PanelLayout } from '../PanelLayout';

// Types
import { PanelTemplateProps, TestMetadata } from 'types';

export interface SinglePanelLayoutProps {
  /** Test data IDs for layout */
  testMetadata?: {
    layout?: TestMetadata;
    panel?: TestMetadata;
  };
  /** Container overrides */
  panelProps?: Omit<PanelTemplateProps, 'children'>;
}

const SinglePanelLayout: FC<SinglePanelLayoutProps> = ({
  testMetadata,
  children,
  panelProps,
}) => {
  return (
    <PanelLayout
      slim={{
        columns: ['1fr'],
        rows: ['1fr'],
        areas: [['single']],
      }}
      {...testMetadata?.layout}
    >
      <Panel name="single" testMetadata={testMetadata?.panel} {...panelProps}>
        {children}
      </Panel>
    </PanelLayout>
  );
};

export default SinglePanelLayout;
