import { FC, useMemo } from 'react';
import classNames from 'classnames';

// Styles
import './StateType.scss';

// Types
import { JourneyState } from '../../types';

interface StateProps {
  state?: JourneyState;
}

const StateType: FC<StateProps> = ({ state }): JSX.Element => {
  const bulletStyles = useMemo(
    () =>
      classNames('sb-cp-journey-summary-state-bullet', {
        'sb-cp-journey-summary-state-bullet__active':
          state === JourneyState.Active,
        'sb-cp-journey-summary-state-bullet__canceled':
          state === JourneyState.Canceled,
        'sb-cp-journey-summary-state-bullet__completed':
          state === JourneyState.Completed,
        'sb-cp-journey-summary-state-bullet__at-risk':
          state === JourneyState.AtRisk,
      }),
    [state],
  );

  const stateStyles = useMemo(
    () =>
      classNames('sb-cp-journey-summary-state-item', {
        'sb-cp-journey-summary-state-item__active':
          state === JourneyState.Active,
        'sb-cp-journey-summary-state-item__canceled':
          state === JourneyState.Canceled,
        'sb-cp-journey-summary-state-item__completed':
          state === JourneyState.Completed,
        'sb-cp-journey-summary-state-item__at-risk':
          state === JourneyState.AtRisk,
      }),
    [state],
  );

  return (
    <div className={stateStyles}>
      <div className={bulletStyles} />
      {state?.toUpperCase()}
    </div>
  );
};

export default StateType;
