import { FC } from 'react';
import {
  components as optionComponents,
  OptionProps,
  OptionTypeBase,
} from 'react-select';
import classNames from 'classnames';

// Components
import Select from '../Select';
import Icon from '../Icon';
import Typography from '../Typography';

// Styles
import './MenuList.scss';

// Types
import { SelectOption, TestMetadata } from '../types';
import isEqual from 'lodash/isEqual';

export interface MenuListProps {
  // ID of select
  id?: string;
  // Adds elevated styles
  elevated?: boolean;
  // Whether to indicate an item is selected, for example, an item wouldn't be selected if choosing a report to run
  showSelected?: boolean;
  // Options that populate the menu list
  options: SelectOption[];
  // Selected option
  selectedOption?: SelectOption;
  // Option change event
  onChange: (value: SelectOption) => void;
  /** Classname for the MenuList component */
  className?: string;
  // Test IDs for component sections
  testMetadata?: {
    wrapper?: TestMetadata;
  };
}

const CustomOption = <T extends OptionTypeBase>(
  props: OptionProps<T, false>,
  showSelected: boolean,
  selectedOption: SelectOption | undefined,
): JSX.Element => {
  return (
    <optionComponents.Option {...props}>
      <div className="sb-menu-list__option">
        {showSelected && (
          <Icon
            altText="Selected"
            faPackage="fa-light"
            iconName="fa-check"
            iconType="fa"
            size="smaller"
            title="Selected"
            className={classNames(
              'sb-menu-list__check',
              !isEqual(selectedOption, props.data) &&
                'sb-menu-list__check-unselected',
            )}
          />
        )}
        <Typography
          variant="T15"
          className={classNames(
            showSelected &&
              isEqual(selectedOption, props.data) &&
              'sb-menu-list__label-selected',
          )}
        >
          {props.label}
        </Typography>
      </div>
    </optionComponents.Option>
  );
};

const MenuList: FC<MenuListProps> = ({
  id,
  elevated = false,
  showSelected = true,
  options,
  selectedOption,
  onChange,
  className,
  testMetadata,
}) => {
  const classes = classNames(className, 'sb-menu-list', {
    'sb-menu-list__flat': !elevated,
  });
  return (
    <div className={classes} {...testMetadata?.wrapper}>
      <Select
        id={id}
        instanceId="menu-list-select"
        data-testid="menu-list-select"
        menuIsOpen
        testMetadata={testMetadata?.wrapper}
        options={options}
        value={showSelected ? selectedOption : null}
        onChange={(option) => onChange(option as SelectOption)}
        components={{
          Option: (props) => CustomOption(props, showSelected, selectedOption),
        }}
      />
    </div>
  );
};

export default MenuList;
