import { FC } from 'react';
import {
  components as optionComponents,
  OptionProps,
  OptionTypeBase,
} from 'react-select';
import classNames from 'classnames';

// Components
import Checkbox from '../Checkbox';
import { MultiSelect } from '../MultiSelect';

// Styles
import './MultiMenuList.scss';

// Types
import { Option, SelectOption, TestMetadata } from '../types';

export interface MultiMenuListProps {
  // ID of select component
  id?: string;
  // Adds elevated styles
  elevated?: boolean;
  // Options that populate the menu list
  options: SelectOption[];
  // Selected option(s)
  selectedOptions: SelectOption[] | undefined;
  // Option(s) change event
  onChange: (selectedOptions: SelectOption[]) => void;
  // Test IDs for component sections
  testMetadata?: TestMetadata;
  // Class Name
  className?: string;
}

const CustomOption = <T extends OptionTypeBase>(
  props: OptionProps<T, true>,
): JSX.Element => {
  return (
    <optionComponents.Option {...props}>
      <Checkbox
        className={classNames(
          props.isSelected && 'sb-multi-menu-list__selected',
        )}
        disabled={props.isDisabled}
        checked={props.isSelected}
        option={{
          id: props.innerProps.id,
          value: props.data.value,
          label: props.data.label,
        }}
      />
    </optionComponents.Option>
  );
};

const MultiMenuList: FC<MultiMenuListProps> = ({
  id,
  elevated = false,
  options,
  selectedOptions,
  onChange,
  testMetadata,
  className,
}) => {
  const classes = classNames('sb-multi-menu-list', {
    'sb-multi-menu-list__flat': !elevated,
  });

  return (
    <div className={classes} {...testMetadata}>
      <MultiSelect
        id={id}
        instanceId="multi-menu-list-select"
        data-testid="multi-menu-list-select"
        menuIsOpen
        options={options}
        value={selectedOptions}
        onChange={(options) => onChange(options as Option[])}
        components={{ Option: (props) => CustomOption(props) }}
        className={className}
        borderless={!elevated}
      />
    </div>
  );
};

export default MultiMenuList;
