import React, { useCallback, useEffect, useMemo, useState } from 'react';

// Components
import MenuList from '../../../MenuList';
import PopoverOverlayTemplate from '../PopoverOverlayTemplate';
import Button from '../../../Button';
import Popover from '../../../Popover';

// Styles
import './ActionButton.scss';

// Types
import {
  DataDefinition,
  ActionState,
  ActionType,
  Option,
  SortDirection,
  TestMetadata,
} from '../../../types';

// Utils
import { getActionLabel } from './actionButton.util';

export interface SortActionButtonProps {
  dataDefinition?: DataDefinition[];
  actionState?: ActionState[];
  selectId?: string;
  testMetadata?: TestMetadata;
  isDisabled?: boolean;
  onActionStateChange?: (
    type: ActionType,
    newActionStates: ActionState[],
  ) => void;
}

const SortActionButton: React.FC<SortActionButtonProps> = ({
  selectId,
  isDisabled,
  dataDefinition,
  actionState,
  onActionStateChange,
  testMetadata,
}) => {
  const [open, setOpen] = useState<boolean>(false);
  const [selected, setSelected] = useState<Option | undefined>();

  const handleChange = useCallback(
    (selected?: Option) => {
      const selectedOptionArray: ActionState[] = [];

      selected &&
        selectedOptionArray.push({
          actionType: ActionType.Sort,
          dataKey: selected.value,
          dataLabel: selected.label,
          direction: selected.meta
            ? (selected.meta['DIRECTION'] as SortDirection)
            : 'ASC',
        });

      onActionStateChange &&
        onActionStateChange(ActionType.Sort, selectedOptionArray);
      setOpen(false);
    },
    [onActionStateChange],
  );

  const handleClear = useCallback(() => {
    handleChange();
  }, [handleChange]);

  const getMenuListOptions = (dataDefinition?: DataDefinition[]): Option[] => {
    const newOptionList: Option[] = [];

    if (dataDefinition) {
      // Loop over the data definitions to add the action button
      dataDefinition.map((val) => {
        const newAscOption = {
          label:
            val.dataType === 'text'
              ? `${val.label} A-Z`
              : val.dataType === 'number'
              ? `${val.label} 1-9`
              : `${val.label} ASC`,
          value: val.field,
          meta: { DIRECTION: 'ASC' },
        };

        const newDescOption = {
          label:
            val.dataType === 'text'
              ? `${val.label} Z-A`
              : val.dataType === 'number'
              ? `${val.label} 9-1`
              : `${val.label} DESC`,
          value: val.field,
          meta: { DIRECTION: 'DESC' },
        };

        newOptionList.push(newAscOption, newDescOption);
      });
    }

    return newOptionList;
  };

  const menuOptions = useMemo(
    () => getMenuListOptions(dataDefinition),
    [dataDefinition],
  );

  useEffect(() => {
    setSelected(getSelectedMenuOption(actionState));
  }, [actionState]);

  const getSelectedMenuOption = (
    actionState?: ActionState[],
  ): Option | undefined => {
    if (actionState) {
      const sortAction = actionState?.find(
        (action) => action.actionType === 'Sort',
      );
      if (sortAction) {
        return {
          label: sortAction.dataLabel,
          value: sortAction.dataKey,
          meta: { DIRECTION: sortAction.direction },
        };
      }
    }

    return undefined;
  };

  return (
    <Popover
      visible={open}
      popoverContent={
        <PopoverOverlayTemplate
          title="Sort"
          onClear={handleClear}
          flush
          elevated={false}
        >
          <MenuList
            id={selectId}
            options={menuOptions}
            onChange={handleChange}
            showSelected
            selectedOption={selected}
          />
        </PopoverOverlayTemplate>
      }
      className="sb-action-button__popover"
      tippyProps={{
        arrow: true,
        interactive: true,
        theme: 'light-border',
        placement: 'bottom-end',
        onClickOutside: () => setOpen(false),
      }}
    >
      <Button
        {...testMetadata}
        styleType="standard"
        iconType="fa-regular fa-angle-down"
        iconPlacement="after"
        sizeType="smaller"
        onClick={() => setOpen(!open)}
        type="button"
        disabled={isDisabled}
        className="sb-cp-action-button__button"
      >
        {getActionLabel('Sort', actionState, true)}
      </Button>
    </Popover>
  );
};

export default SortActionButton;
