import { Children, FC, ReactElement, useMemo } from 'react';
import classNames from 'classnames';

// Components
import Button from '../Button';

// Styles
import './InputGroup.scss';

// Types
import { TestMetadata } from 'types';

export interface InputGroupProps {
  className?: string;
  vertical?: boolean;
  testMetadata?: TestMetadata;
  children?: ReactElement[];
}

/** Input components are useful for editing text in forms */
const InputGroup: FC<InputGroupProps> = ({
  className,
  children,
  vertical,
  testMetadata,
  ...otherProps
}) => {
  const inputGroupClassNames = useMemo(
    () =>
      classNames(
        'sb-input-group',
        {
          'sb-input-group__horizontal': vertical !== true,
          'sb-input-group__vertical': vertical === true,
        },
        className,
      ),
    [className, vertical],
  );

  return (
    <div className={inputGroupClassNames} {...otherProps} {...testMetadata}>
      {Children.map(children, (child, index) => {
        return (
          <div
            data-testid={
              testMetadata?.['data-testid']
                ? `${testMetadata['data-testid']}-${index}`
                : undefined
            }
            className={classNames(
              'sb-input-group__item',
              {
                'sb-input-group__item-button':
                  child && (child as ReactElement).type === Button,
              },
              child && (child as ReactElement).props.className,
            )}
          >
            {child}
          </div>
        );
      })}
    </div>
  );
};

export default InputGroup;
