// Types
import { CellRendererFunc, MasterColumnCellRenderedParam } from '../types';
import { ICellRendererParams } from 'ag-grid-community';

// Components
import MasterDetailCell from '../MasterDetailCell/MasterDetailCell';

// Utils
import { getCellValue } from './utils';

// Styles
import './TextFieldRenderer.scss';

const TextFieldRenderedCell: React.FC<{
  params: ICellRendererParams;
}> = ({ params }) => {
  const cellValue = getCellValue(params);

  if (!cellValue) {
    return null;
  }

  return (
    <div
      title={cellValue}
      className="sb-cp-standard-table_cell-text"
      data-testid="StandardTable_Field_Text"
    >
      {cellValue}
    </div>
  );
};

const TextFieldRenderer: CellRendererFunc = (
  params: ICellRendererParams & MasterColumnCellRenderedParam,
) => {
  if (params.isMasterColumn) {
    return (
      <MasterDetailCell
        gridAPI={params.api}
        node={params.node}
        onMasterDetailCellMouseOver={params.onMasterDetailCellMouseOver}
      >
        <TextFieldRenderedCell params={params} />
      </MasterDetailCell>
    );
  }

  return <TextFieldRenderedCell params={params} />;
};

export default TextFieldRenderer;
