import React, { useMemo } from 'react';
import classNames from 'classnames';

// Components
import Typography from '../Typography';
import Button from '../Button';
import {
  BodyPanel,
  FooterPanel,
  HeaderPanel,
  StandardLayout,
} from '../StandardLayout';
import ButtonGroup from '../ButtonGroup';

// Types
import { TestMetadata } from '../types';

// Styles
import './PopoverOverlay.scss';

export interface PopoverOverlayProps {
  // What to show inside the overlay
  children: React.ReactNode;
  // Footer actions
  actions?: React.ReactNode;
  // Header Title
  title?: string;
  // Clear callback
  onClear?: () => void;
  // Enable clear button
  isClearEnabled?: boolean;
  // Size, TODO: Remove grid
  size?: 'small' | 'standard' | 'grid';
  // Display flush, when using child components that need full-width effects (like MenuList)
  flush?: boolean;
  // Display elevated, set to false for embedding somewhere that handles popover styling for you (ie. tippy)
  elevated?: boolean;
  // Types
  testMetaData?: {
    container?: TestMetadata;
    body?: TestMetadata;
    clear?: TestMetadata;
  };
}

const PopoverOverlay: React.FC<PopoverOverlayProps> = ({
  title,
  children,
  onClear,
  isClearEnabled = true,
  size = 'small',
  flush = false,
  elevated = true,
  actions,
  testMetaData,
}) => {
  const overlayClasses = useMemo(
    () =>
      classNames('sb-popover-overlay_container', `sb-popover-overlay_${size}`, {
        'sb-popover-overlay_elevated': elevated,
      }),
    [size, elevated],
  );

  return (
    <StandardLayout
      className={overlayClasses}
      testMetadata={{
        'data-testid': 'PopoverOverlay__container',
      }}
      {...testMetaData?.container}
    >
      {title && (
        <HeaderPanel fill="white" className="sb-popover-overlay_header">
          <Typography variant="T15" className="sb-popover-overlay_header-title">
            {title}
          </Typography>

          {onClear && (
            <Button
              testMetadata={{
                'data-testid': 'PopoverOverlay_Clear',
                ...testMetaData?.clear,
              }}
              styleType="link"
              sizeType="smaller"
              onClick={onClear}
              type="button"
              disabled={!isClearEnabled}
              className="sb-popover-overlay_header-clear"
            >
              Clear
            </Button>
          )}
        </HeaderPanel>
      )}
      <BodyPanel
        fill="white"
        className={classNames('sb-popover-overlay_body', {
          'sb-popover-overlay_body-padding_none': flush,
        })}
        testMetadata={testMetaData?.body}
      >
        {children}
      </BodyPanel>

      {actions && (
        <FooterPanel
          fill="white"
          padding="tight"
          className="sb-popover-overlay_footer"
        >
          <ButtonGroup className="sb-popover-overlay_footer-actions">
            {actions}
          </ButtonGroup>
        </FooterPanel>
      )}
    </StandardLayout>
  );
};

export default PopoverOverlay;
