import { FC } from 'react';
import classNames from 'classnames';

// Components
import ContentBlock from '../ContentBlock';
import Modal from '../Modal';
import ReactMarkdown from 'react-markdown';

// Styles
import './ReleaseNotesModal.scss';

// Types
import { ReleaseNote, TestMetadata } from '../types';

// Util
import format from 'date-fns/format';

export interface ReleaseNotesModalProps {
  /** Sets the modalId on the modal */
  id: string;
  /** Determines if modal is open */
  isOpen: boolean;
  /** Function for when modal closes */
  onClose: () => void;
  /** Array of release notes to display in modal */
  releaseNotes: ReleaseNote[];
  /** Custom class name */
  className?: string;
  // Test IDs for component sections
  testMetadata?: {
    releaseNotesModal?: TestMetadata;
    content?: TestMetadata;
    note?: TestMetadata;
  };
}

const ReleaseNotesModal: FC<ReleaseNotesModalProps> = ({
  isOpen,
  onClose,
  id,
  releaseNotes,
  className,
  testMetadata,
}) => {
  return (
    <>
      {isOpen && (
        <Modal
          bodyOverlay="blackout"
          headerText="What's new"
          modalId={id}
          primaryButton="Close"
          primaryButtonAction={onClose}
          onClose={onClose}
          footerAlign="center"
          modalClass={classNames('sb-release-notes-modal', className)}
          dialogBodyClassName="sb-release-notes-modal__dialog-body"
          {...testMetadata?.releaseNotesModal}
        >
          {releaseNotes.map((releaseNote) => (
            <ContentBlock
              key={releaseNote.id}
              className="sb-release-notes-modal__content-block"
              {...testMetadata?.content}
            >
              <h3 className="sb-release-notes-modal__title">
                {releaseNote.title}
              </h3>
              <p className="sb-release-notes-modal__release-date">
                {format(new Date(releaseNote.releaseDate), 'MMMM d, yyyy')}
              </p>
              <ReactMarkdown
                className="sb-release-notes-modal__note"
                {...testMetadata?.note}
              >
                {releaseNote.note}
              </ReactMarkdown>
            </ContentBlock>
          ))}
        </Modal>
      )}
    </>
  );
};

export default ReleaseNotesModal;
