import { useContext } from 'react';

// Components
import SpellbookDualPanelCanvas from '../../../Canvases/DualPanelCanvas';

// Context
import { PageGeneratorContext } from '../../../PageGenerator';

// Types
import { CanvasTemplateProps } from '../../../PageGenerator/types';

// Styles
import './DualPanelCanvas.scss';

const DualPanelCanvas: React.FC<CanvasTemplateProps> = ({ sectionNames }) => {
  const context = useContext(PageGeneratorContext);
  if (context === undefined) {
    throw new Error('PageGeneratorContext is not defined');
  }

  const header = context.utils.renderChildren(sectionNames?.['header']);

  return (
    <SpellbookDualPanelCanvas
      header={header.props?.children?.length ? <>{header}</> : null}
      secondaryPanel={
        <>{context.utils.renderChildren(sectionNames?.['secondary'])}</>
      }
      testMetadata={{
        standardSection: {
          'data-testid': 'PageGenerator_DualPanelCanvas',
        },
      }}
    >
      <>{context.utils.renderChildren(sectionNames?.['body'])}</>
    </SpellbookDualPanelCanvas>
  );
};

export default DualPanelCanvas;
