import { FC, useMemo } from 'react';
import classNames from 'classnames';

// Components
import Button from '../Button';

// Styles
import './Notification.scss';

// Types
import { TestMetadata } from 'types';
import { OnChangeType } from '../types';

export interface NotificationProps {
  /** Boolean to determine if notification is a banner notification */
  banner?: boolean;
  /** Boolean to determine if notification is a toast notification */
  toast?: boolean;
  /* Boolean to determine if notification is showing in line with other text elements */
  inline?: boolean;
  /** Type of notification: info, warning, error, success */
  notifType?: 'info' | 'error' | 'success' | 'warning';
  /** Name of Font Awesome icon to show (if any)*/
  iconTypePrimary?: string;
  /** Set role to status, alert, or alertdialog for screen readers  */
  role?: 'status' | 'alert' | 'alertdialog';
  /** Class name for notification */
  className?: string;
  /** Boolean to determine if user can dismiss notification with close button  */
  hideClose?: boolean;
  /** function to click the close button  */
  onClose?: OnChangeType;
  /** Boolean to turn off aria-live announcements */
  stopAnnouncements?: boolean;
  /** Determine if light color theme should be used */
  light?: boolean;
  /** Determine if dark color theme should be used */
  dark?: boolean;
  // Test IDs for component sections
  testMetadata?: {
    notification?: TestMetadata;
    closeButton?: TestMetadata;
  };
}

const ariaLiveProp = (
  stopAnnouncements?: boolean,
  banner?: boolean,
  toast?: boolean,
): 'off' | 'polite' | 'assertive' | undefined => {
  let result: 'off' | 'polite' | 'assertive' | undefined;

  if (stopAnnouncements) {
    result = 'off';
  } else if (toast) {
    result = 'polite';
  } else if (banner) {
    result = 'assertive';
  }

  return result;
};

const Notification: FC<NotificationProps> = ({
  banner,
  toast,
  inline,
  notifType,
  iconTypePrimary,
  role = 'status',
  hideClose,
  stopAnnouncements,
  children,
  light,
  dark,
  onClose,
  className,
  testMetadata,
  ...otherProps
}) => {
  const notificationClassNames = useMemo(
    () =>
      classNames(
        'sb-notification',
        {
          'sb-notification_banner': banner,
          'sb-notification_toast': toast,
          'sb-notification_inline': inline,
          'sb-notification_info': notifType === 'info',
          'sb-notification_error': notifType === 'error',
          'sb-notification_success': notifType === 'success',
          'sb-notification_warning': notifType === 'warning',
          'sb-notification_light': light,
          'sb-notification_dark': dark,
          'sb-notification_closable': !hideClose,
        },
        className,
      ),
    [banner, dark, inline, light, notifType, className, toast, hideClose],
  );

  return (
    <section
      {...otherProps}
      className={notificationClassNames}
      role={role}
      tabIndex={banner ? 0 : undefined}
      aria-live={ariaLiveProp(stopAnnouncements, banner, toast)}
      {...testMetadata?.notification}
    >
      {iconTypePrimary && (
        <i
          className={classNames(
            'sb-notification__type-icon',
            'fas',
            iconTypePrimary,
          )}
          aria-hidden="true"
        />
      )}
      {children}
      {!hideClose && (
        <Button
          styleType="demoted"
          iconOnly
          iconType="fa-times"
          labelText="Close"
          className="sb-notification__close-button"
          onClick={onClose}
          {...testMetadata?.closeButton}
        />
      )}
    </section>
  );
};

export default Notification;
