// Components
import Badge from '../../../Badge';
import MasterDetailCell from '../MasterDetailCell/MasterDetailCell';

// Types
import { CellRendererFunc, TypeColumn } from '../types';
import { ICellRendererParams } from 'ag-grid-community';

// Utils
import { getCellValue } from './utils';

// Styles
import './TypeFieldRenderer.scss';

const TypeFieldRenderedCell: React.FC<{
  params: ICellRendererParams & TypeColumn;
}> = ({ params }) => {
  const cellValue = getCellValue(params);

  if (!cellValue) {
    return null;
  }

  return (
    <Badge
      color="primary"
      shape="pill"
      size="small"
      light
      className="sb-standard-table_cell-type"
      testMetadata={{
        'data-testid': 'StandardTable_Field_Type',
      }}
      {...(params.badgeProps && params.badgeProps(params.data))}
    >
      {cellValue}
    </Badge>
  );
};

const TypeFieldRenderer: CellRendererFunc<TypeColumn> = (params) => {
  if (params.isMasterColumn) {
    return (
      <MasterDetailCell
        gridAPI={params.api}
        node={params.node}
        onMasterDetailCellMouseOver={params.onMasterDetailCellMouseOver}
      >
        <TypeFieldRenderedCell params={params} />
      </MasterDetailCell>
    );
  }

  return <TypeFieldRenderedCell params={params} />;
};

export default TypeFieldRenderer;
