import { ComponentProps, FC } from 'react';

// Components
import Button from '../Button';

// Types
import { TestMetadata } from 'types';

export type SaveButtonProps = ComponentProps<typeof Button> & {
  testMetadata?: TestMetadata;
};

const SaveButton: FC<SaveButtonProps> = ({
  children = 'Save',
  isFetching,
  sizeType = 'small',
  testMetadata,
  ...other
}) => (
  <Button
    sizeType={sizeType}
    className="sb-save-button sb-action-button"
    disabled={isFetching}
    isFetching={isFetching}
    {...testMetadata}
    {...other}
  >
    {children}
  </Button>
);

export default SaveButton;
