import { FC } from 'react';

// Components
import Button from '../Button';

// Types
import { TestMetadata } from 'types';

export interface ExpiredSessionProps {
  handleLogin: () => void;
  // Test IDs for component sections
  testMetadata?: {
    expiredSession?: TestMetadata;
    logInButton?: TestMetadata;
  };
}

const ExpiredSession: FC<ExpiredSessionProps> = ({
  handleLogin,
  testMetadata,
}) => {
  return (
    <>
      <h2 {...testMetadata?.expiredSession}>
        Sorry, your session has expired.
      </h2>
      <p>
        For security purposes, you have been logged out. We apologize for the
        inconvenience.
      </p>
      <p>Please log in again to continue.</p>
      <Button
        className="sb-error-template_action-button"
        labelText="Log in with MyID"
        colorType="primary"
        onClick={handleLogin}
        {...testMetadata?.logInButton}
      >
        Log in with MyID
      </Button>{' '}
    </>
  );
};

export default ExpiredSession;
