import { useContext } from 'react';

// Components
import { SideNavTemplate } from '../../../Templates/SideNavTemplate';

// Context
import { PageGeneratorContext } from '../../../../PageGenerator';

// Types
import { CanvasTemplateProps } from '../../../../PageGenerator/types';

// Styles
import './SideNavCanvas.scss';

const SideNavCanvas: React.FC<CanvasTemplateProps> = ({ sectionNames }) => {
  const context = useContext(PageGeneratorContext);
  if (context === undefined) {
    throw new Error('PageGeneratorContext is not defined');
  }

  return (
    <SideNavTemplate
      actions={<>{context.utils.renderChildren(sectionNames?.['header'])}</>}
    >
      <>{context.utils.renderChildren(sectionNames?.['body'])}</>
    </SideNavTemplate>
  );
};

export default SideNavCanvas;
