import { ComponentProps, FC } from 'react';
import classNames from 'classnames';

// Styles
import './NavBar.scss';

// Types
import { TestMetadata } from 'types';

export interface NavBarProps extends ComponentProps<'nav'> {
  /** Prop to control if you want navbar to be sticky to top (default true) */
  sticky?: boolean;
  /** Custom class name passed down to nav */
  className?: string;
  /** TestMetadata used for nav */
  testMetadata?: TestMetadata;
  /** Pass in your Brand, Controls, and Utility children */
  children?: JSX.Element | JSX.Element[];
}

const NavBar: FC<NavBarProps> = ({
  sticky = true,
  className,
  testMetadata,
  children,
  ...otherProps
}) => {
  return (
    <nav
      className={classNames(
        'sb-navbar',
        { [`sb-navbar__sticky`]: sticky },
        className,
      )}
      {...testMetadata}
      {...otherProps}
    >
      {children}
    </nav>
  );
};

export default NavBar;
