import { forwardRef, useImperativeHandle, useState, useRef } from 'react';
import get from 'lodash/get';

// Components
import PopoverOverlayTemplate from '../../../Templates/PopoverOverlayTemplate';
import MultiMenuList from '../../../../MultiMenuList';

// Types
import { Option } from '../../../../types';
import { GridModel } from '../../types';
import { IDoesFilterPassParams, IFilterParams } from 'ag-grid-community';

import './DataGridMultiselectFilter.scss';

const DataGridMultiselectFilter = forwardRef((props: IFilterParams, ref) => {
  // eslint-disable-next-line @typescript-eslint/ban-ts-comment
  // @ts-ignore
  const options = props.filters;
  // eslint-disable-next-line @typescript-eslint/ban-ts-comment
  // @ts-ignore
  const objKey = props.objKey;
  // Actual applied value of the filter
  const [selectedValues, setSelectedValues] = useState<Option[]>([]);

  // Actual applied value of the filter
  const activeValuesRef = useRef<Option[]>([]);

  // Required to communicate with ag-grid
  useImperativeHandle(ref, () => ({
    // Is the filter currently applied
    isFilterActive() {
      return activeValuesRef.current.length > 0;
    },

    // Does the row pass the filter criteria
    doesFilterPass(params: IDoesFilterPassParams) {
      const column = props?.colDef?.field;
      if (column && params.data?.[column]) {
        if (typeof params.data?.[column] === 'string') {
          return activeValuesRef.current.some(
            (option) =>
              option.value.toLowerCase() === params.data[column].toLowerCase(),
          );
        }
        if (Array.isArray(params.data?.[column])) {
          return activeValuesRef.current.some((option) =>
            params.data[column]?.some(
              (item: Record<string, string> | string) => {
                if (typeof item === 'string') {
                  return item.toLowerCase() === option.value.toLowerCase();
                }

                if (objKey) {
                  return (
                    get(item, objKey).toLowerCase() ===
                    option.value.toLowerCase()
                  );
                }

                return false;
              },
            ),
          );
        }
        if (
          params.data?.[column] &&
          objKey &&
          get(params.data?.[column], objKey)
        ) {
          return activeValuesRef.current.some(
            (option) =>
              params.data?.[column][objKey] === option.value.toLowerCase(),
          );
        }
      }
      return false;
    },

    // Save Model
    getModel() {
      if (!activeValuesRef.current.length) {
        return undefined;
      }

      return {
        state: activeValuesRef.current,
      };
    },

    // Restore Model
    setModel(model: GridModel<Option[]>) {
      if (model == null) {
        setSelectedValues([]);
        activeValuesRef.current = [];
      } else {
        setSelectedValues(model.state);
        activeValuesRef.current = model.state;
      }
    },
  }));

  // Set active filter on apply
  const onApply = (): void => {
    if (selectedValues.length > 0) {
      activeValuesRef.current = selectedValues;
    } else {
      activeValuesRef.current = [];
    }
    props.filterChangedCallback();
    props.api.hidePopupMenu();
  };

  // Clear input text
  const onClear = (): void => {
    setSelectedValues([]);
  };

  // Close popup and restore input text to set filter
  const onCancel = (): void => {
    setSelectedValues(activeValuesRef.current);
    props.api.hidePopupMenu();
  };

  const onSelectFilter = (selectedValues: Option[]): void => {
    setSelectedValues(selectedValues);
  };

  return (
    <PopoverOverlayTemplate
      title="Filter"
      size="grid"
      onApply={onApply}
      onCancel={onCancel}
      onClear={onClear}
      isClearEnabled={selectedValues.length > 0}
      flush
      testMetaData={{
        cancel: {
          'data-testid': 'StandardTable_Filter_Cancel',
        },
        apply: {
          'data-testid': 'StandardTable_Filter_Apply',
        },
        clear: {
          'data-testid': 'StandardTable_Filter_Clear',
        },
      }}
    >
      <MultiMenuList
        options={options}
        selectedOptions={selectedValues}
        onChange={onSelectFilter}
        className="sb-data-grid-filter_milti-menu"
        testMetadata={{
          'data-testid': 'StandardTable_Filter_List',
        }}
      />
    </PopoverOverlayTemplate>
  );
});

export default DataGridMultiselectFilter;
