import React, { useCallback, useMemo, useState, useEffect } from 'react';
import classNames from 'classnames';
import debounce from 'lodash/debounce';

// Types
import { GridApi, ICellRendererParams, IRowNode } from 'ag-grid-community';

export interface MasterDetailCellProps {
  gridAPI: GridApi;
  node: IRowNode;
  onMasterDetailCellMouseOver?: (rowData: ICellRendererParams['data']) => void;
}

// styles
import './MasterDetailCell.scss';

const MasterDetailCell: React.FC<MasterDetailCellProps> = ({
  gridAPI,
  node,
  children,
  onMasterDetailCellMouseOver,
}) => {
  const [isExpanded, setIsExpanded] = useState(false);
  const expandedGroupClasses = useMemo(
    () =>
      classNames('ag-group-expanded', {
        'ag-hidden': !isExpanded || !node.master,
      }),
    [isExpanded, node],
  );

  const contractedGroupClasses = useMemo(
    () =>
      classNames('ag-group-contracted', {
        'ag-hidden': isExpanded || !node.master,
      }),
    [isExpanded, node],
  );

  const clickActionHandler = useCallback(
    (expand: boolean) => {
      if (node.master) {
        gridAPI.setRowNodeExpanded(node, expand);
        setIsExpanded(expand);
      }
    },
    [gridAPI, node],
  );

  const onMouseLeaveHandler = (): void => {
    debouncedOnMouserEnterHandler.cancel();
  };

  const debouncedOnMouserEnterHandler = useMemo(
    () =>
      debounce(() => {
        onMasterDetailCellMouseOver && onMasterDetailCellMouseOver(node?.data);
      }, 400),
    [onMasterDetailCellMouseOver, node?.data],
  );

  useEffect(() => {
    return () => debouncedOnMouserEnterHandler.cancel();
  }, [debouncedOnMouserEnterHandler]);

  return (
    <div
      className="sb-standard-table_cell-master-detail"
      data-testid="master-detail-cell"
    >
      <div className="sb-standard-table_cell-master-actions">
        <span
          className={expandedGroupClasses}
          onClick={() => clickActionHandler(false)}
          data-testid="master-detail-cell-expanded-action"
        >
          <span className="ag-icon ag-icon-tree-open" role="presentation" />
        </span>
        <span
          data-testid="master-detail-cell-contracted-action"
          className={contractedGroupClasses}
          onClick={() => clickActionHandler(true)}
          onMouseEnter={debouncedOnMouserEnterHandler}
          onMouseLeave={onMouseLeaveHandler}
        >
          <span className="ag-icon ag-icon-tree-closed" role="presentation" />
        </span>
      </div>

      <div>{children}</div>
    </div>
  );
};

export default MasterDetailCell;
