import {
  KeyboardEvent,
  forwardRef,
  useImperativeHandle,
  useState,
  useRef,
} from 'react';
import { isValid as isValidDate } from 'date-fns';

// Components
import PopoverOverlayTemplate from '../../../Templates/PopoverOverlayTemplate';
import DateInput from '../../../../DateInput';

// Types
import { IDoesFilterPassParams, IFilterParams } from 'ag-grid-community';
import { DateFilterValue, GridModel } from '../../types';

// Styles
import './DataGridDateFilter.scss';

const DataGridDateFilter = forwardRef((props: IFilterParams, ref) => {
  const [startDate, setStartDate] = useState<Date | undefined>();
  const [endDate, setEndDate] = useState<Date | undefined>();
  const activeFilterRef = useRef<DateFilterValue | undefined>();

  // Required to communicate with ag-grid
  useImperativeHandle(ref, () => ({
    // Is the filter currently applied
    isFilterActive() {
      return (
        !!activeFilterRef.current?.startDate ||
        !!activeFilterRef.current?.endDate
      );
    },

    // Does the row pass the filter criteria
    doesFilterPass(params: IDoesFilterPassParams) {
      const column = props?.colDef?.field;
      if (column && params.data?.[column]) {
        const cellValue =
          typeof params.data[column] === 'string'
            ? new Date(Date.parse(params.data[column]))
            : params.data[column];

        return (
          isValidDate(cellValue) &&
          (!startDate || cellValue.getTime() >= startDate.getTime()) &&
          (!endDate || cellValue.getTime() <= endDate.getTime())
        );
      }

      return false;
    },

    // Save Model
    getModel() {
      if (!activeFilterRef.current) {
        return undefined;
      }

      return {
        state: activeFilterRef.current,
      };
    },

    // Restore Model
    setModel(model: GridModel<DateFilterValue | undefined>) {
      if (model == null) {
        setStartDate(undefined);
        setEndDate(undefined);
        activeFilterRef.current = undefined;
      } else {
        setStartDate(model.state?.startDate);
        setEndDate(model.state?.endDate);
        activeFilterRef.current = model.state;
      }
    },
  }));

  // Set active filter on apply
  const onApply = (): void => {
    if (startDate || endDate) {
      activeFilterRef.current = {
        startDate,
        endDate,
      };
    } else {
      activeFilterRef.current = undefined;
    }
    props.filterChangedCallback();
    props.api.hidePopupMenu();
  };

  // Set active filter on enter
  const onKeyPress = (e: KeyboardEvent<HTMLInputElement>): void => {
    e.key === 'Enter' && onApply();
  };

  // Clear input text
  const onClear = (): void => {
    setStartDate(undefined);
    setEndDate(undefined);
  };

  // Close popup and restore input text to set filter
  const onCancel = (): void => {
    if (startDate !== activeFilterRef.current?.startDate) {
      setStartDate(activeFilterRef.current?.startDate);
    }

    if (endDate !== activeFilterRef.current?.endDate) {
      setEndDate(activeFilterRef.current?.endDate);
    }

    props.api.hidePopupMenu();
  };

  return (
    <PopoverOverlayTemplate
      title="Filter"
      size="grid"
      onApply={onApply}
      onCancel={onCancel}
      onClear={onClear}
      isClearEnabled={!!startDate || !!endDate}
      testMetaData={{
        cancel: {
          'data-testid': 'StandardTable_Filter_Cancel',
        },
        apply: {
          'data-testid': 'StandardTable_Filter_Apply',
        },
        clear: {
          'data-testid': 'StandardTable_Filter_Clear',
        },
      }}
    >
      <DateInput
        containerClassName="sb-cp-data-grid-date-filter_input"
        labelText="Start Date"
        maxDate={endDate}
        onDateChange={(e) => setStartDate(e)}
        placeholderText="From"
        selected={startDate || undefined}
        onKeyDown={onKeyPress}
        preventOpenOnFocus={true}
        testMetadata={{
          button: {
            'data-testid': 'StandardTable_Filter_Date_StartDate_Button',
          },
          input: {
            'data-testid': 'StandardTable_Filter_Date_StartDate_Input',
          },
          wrapper: {
            'data-testid': 'StandardTable_Filter_Date_StartDate_Wrapper',
          },
        }}
      />
      <DateInput
        containerClassName="sb-cp-data-grid-date-filter_input"
        labelText="End Date"
        minDate={startDate}
        onDateChange={(e) => setEndDate(e)}
        placeholderText="To"
        selected={endDate || undefined}
        onKeyDown={onKeyPress}
        testMetadata={{
          button: {
            'data-testid': 'StandardTable_Filter_Date_EndDate_Button',
          },
          input: {
            'data-testid': 'StandardTable_Filter_Date_EndDate_Input',
          },
          wrapper: {
            'data-testid': 'StandardTable_Filter_Date_EndDate_Wrapper',
          },
        }}
      />
    </PopoverOverlayTemplate>
  );
});

export default DataGridDateFilter;
