import { FC, useMemo, useCallback, useContext, useState } from 'react';
import classNames from 'classnames';
import { getSavingState } from './utils';

//Components
import Typography from '../Typography';
import Badge from '../Badge';
import Icon from '../Icon';
import Button from '../Button';
import PopoverButton from '../PopoverButton';
import MenuList from '../MenuList';
import { TreeBrowserContext } from './TreeBrowser';
import PopoverOverlay from '../PopoverOverlay';

// Styles
import './TreeHeader.scss';

// Types
import { TreeEntity } from './types';
import get from 'lodash/get';

export interface TreeHeaderProps {
  /* Entity */
  node: TreeEntity;
  /* Current Level */
  level: number;
  /* Path */
  path: string;
}

const TreeHeader: FC<TreeHeaderProps> = ({ node, path, level }) => {
  const {
    actions,
    isSaving,
    onClickItem,
    onCreate,
    updateTree,
    levels,
    idKey,
    primaryKey,
    toggleAll,
    setToggleAll,
    statuses,
    statusKey,
    lengthKey,
    activeItemId,
  } = useContext(TreeBrowserContext);
  const [isPopoverButtonOpen, setIsPopoverButtonOpen] = useState(false);

  const name = get(node, primaryKey),
    count = get(node, lengthKey),
    id = get(node, idKey),
    status = get(node, statusKey),
    badge = get(statuses, status),
    nodeActions = node.actions,
    displayLevel = get(levels, level) || level,
    disabled = node.disabled;

  const onClick = useCallback(() => {
    if (disabled) return;
    onClickItem?.(id || name, node, displayLevel);
  }, [displayLevel, id, name, onClickItem, disabled, node]);

  const isCreatable = onCreate && node?.creatable;
  const savingState = getSavingState(id || name, isSaving);
  const isActivated = id === activeItemId;

  const treeHeaderClassNames = useMemo(
    () =>
      classNames('sb-treebrowser__tree-header', {
        'sb-treebrowser__tree-header-disabled': disabled,
        'sb-treebrowser__tree-header-selected': isActivated,
      }),
    [isActivated, disabled],
  );

  return (
    <div className={treeHeaderClassNames}>
      <div className="sb-treebrowser__header">
        {node.data && node.data.length > 0 ? (
          <Button
            iconOnly
            iconType="fa-chevron-up"
            iconPackage="fas"
            className={classNames('sb-treebrowser__header-expand', {
              'sb-treebrowser__header-down': !toggleAll,
            })}
            onClick={() => {
              setToggleAll(!toggleAll);
            }}
            styleType="link"
            data-testid="collapsible-icon"
          />
        ) : (
          <Icon
            iconType="fa"
            iconName="fa-list"
            size="smaller"
            faPackage="fas"
            className="sb-treebrowser__header-list"
          />
        )}
        <div className="sb-treebrowser__header-interactive" onClick={onClick}>
          <Typography
            className={classNames('sb-treebrowser__header-name', {
              'sb-treebrowser__header-name-disabled': disabled,
            })}
            variant="T15"
          >
            <span
              className={classNames(
                `sb-treebrowser__header-trigger${disabled ? '-disabled' : ''}`,
              )}
              data-testid="header-title"
            >
              {name}
            </span>
            {!!count && (
              <Badge
                className="sb-treebrowser__header-badge sb-treebrowser__header-count"
                color="base"
                light
                shape="pill"
                size="small"
                testMetadata={{
                  'data-testid': 'header-count',
                }}
              >
                {count}
              </Badge>
            )}
            {status && (
              <Badge
                className="sb-treebrowser__header-badge sb-treebrowser__header-status"
                color={badge || 'success'}
                light
                shape="pill"
                size="small"
              >
                {status}
              </Badge>
            )}
          </Typography>
        </div>
        <div className="sb-treebrowser__header-actions">
          {isCreatable && (
            <Button
              iconOnly
              iconType="fa-plus"
              iconPackage="fas"
              className={classNames(
                'sb-treebrowser__header-add',
                'sb-treebrowser__header-action',
              )}
              onClick={() => {
                updateTree?.(
                  id || name,
                  node,
                  level,
                  path,
                  level < levels.length - 2,
                );
              }}
              styleType="standard"
              data-testid="add-icon"
              sizeType="smaller"
              alt={isSaving ? 'Creating' : 'Create'}
              disabled={disabled}
              {...savingState}
            />
          )}
          {nodeActions && (
            <PopoverButton
              buttonProps={{
                styleType: 'standard',
                className: classNames(
                  'sb-treebrowser__option',
                  'sb-treebrowser__header-action',
                ),
                testMetadata: { 'data-testid': 'options-button' },
                disabled: disabled || savingState.disabled,
              }}
              handleToggle={(state, event) => {
                event?.stopPropagation();
                setIsPopoverButtonOpen(state);
              }}
              isOpen={isPopoverButtonOpen}
            >
              <PopoverOverlay flush elevated={false}>
                <MenuList
                  showSelected={false}
                  options={nodeActions.map((action) => {
                    const actionName = actions?.[action]?.label || action;
                    return {
                      label: actionName,
                      value: action,
                    };
                  })}
                  onChange={(selected) => {
                    actions?.[selected.value].onClick(
                      selected.value,
                      id || name,
                    );
                    setIsPopoverButtonOpen(false);
                  }}
                />
              </PopoverOverlay>
            </PopoverButton>
          )}
        </div>
      </div>
    </div>
  );
};

export default TreeHeader;
