import { ComponentProps, FC } from 'react';
import classNames from 'classnames';

// Components
import Button from '../../../Button';

// Types
import { TestMetadata } from 'types';

// Styles
import './DangerButton.scss';

export type DangerButtonProps = ComponentProps<typeof Button> & {
  testMetadata?: TestMetadata;
};

const DangerButton: FC<DangerButtonProps> = ({
  children = 'Cancel',
  styleType = 'standard',
  sizeType = 'small',
  className,
  testMetadata,
  ...other
}) => (
  <Button
    type="button"
    className={classNames(className, 'sb-danger-button sb-action-button')}
    styleType={styleType}
    sizeType={sizeType}
    {...testMetadata}
    {...other}
  >
    {children}
  </Button>
);

export default DangerButton;
