import { Children, createContext, useMemo, useCallback } from 'react';
import Helmet from 'react-helmet';

// Components
import Canvas from './Canvas';

// Canvases
import BasicCanvas from './Canvases/BasicCanvas';
import DualPanelCanvas from './Canvases/DualPanelCanvas';
import BodyCanvas from './Canvases/BodyCanvas';
import FormCanvas from './Canvases/FormCanvas';

// Layouts
import StandardLayout from './Layouts/StandardLayout';
import DualPanelLayout from './Layouts/DualLayout';
import SidebarLayout from './Layouts/SidebarLayout';

// Renditions
import {
  GrayRendition,
  WhiteRendition,
  PrimaryRendition,
  TransparentRendition,
  FooterRendition,
  AppHeaderRendition,
} from '../Renditions';

// Utils
import { getChildren, parsePageSchema } from './utils';

// Types
import {
  CustomCanvasType,
  CustomLayoutType,
  CustomRenditionType,
  PageChildren,
  PageOptions,
  PageSchema,
  AreaObject,
} from './types';

export const PageGeneratorContext = createContext<
  | {
      hash: string;
      page: PageSchema;
      options: PageOptions;
      children: (React.ReactChild | React.ReactFragment | React.ReactPortal)[];
      utils: {
        renderChildren: (section: AreaObject) => JSX.Element;
      };
    }
  | undefined
>(undefined);

const parseOptions = (
  page: PageSchema,
  customRenditions?: CustomRenditionType,
  customLayouts?: CustomLayoutType,
  customCanvases?: CustomCanvasType,
): PageOptions => {
  return {
    pageName: page.pageName || '',
    layouts: {
      standard: StandardLayout,
      dual: DualPanelLayout,
      sidebar: SidebarLayout,
      ...customLayouts,
    },
    renditions: {
      basic: TransparentRendition,
      foreground: WhiteRendition,
      background: GrayRendition,
      primary: PrimaryRendition,
      footer: FooterRendition,
      header: AppHeaderRendition,
      ...customRenditions,
    },
    canvases: {
      basic: BasicCanvas,
      dual: DualPanelCanvas,
      body: BodyCanvas,
      form: FormCanvas,
      ...customCanvases,
    },
  };
};

export interface PageGeneratorProps {
  /* Page Schema */
  page: string | PageSchema;
  /* Custom Renditions */
  customRenditions?: CustomRenditionType;
  /* Custom Layouts */
  customLayouts?: CustomLayoutType;
  /* Custom Canvases */
  customCanvases?: CustomCanvasType;
  /* Panels to inject into content areas: Must include the "name" attribute as a prop */
  children?: PageChildren;
}

const PageGenerator = ({
  page,
  customRenditions,
  customLayouts,
  customCanvases,
  children,
}: PageGeneratorProps): JSX.Element => {
  const schema = parsePageSchema(page);
  const schemaHash = JSON.stringify(schema);
  const options = useMemo(
    () => parseOptions(schema, customRenditions, customLayouts, customCanvases),
    // We want to use the string version of form for more accurate nested dependency checks
    // eslint-disable-next-line react-hooks/exhaustive-deps
    [schemaHash, customRenditions, customLayouts, customCanvases],
  );

  const childrenArray = Children.toArray(children);

  const renderChildren = useCallback(
    (section: AreaObject) => {
      if (section?.canvas) {
        return <Canvas canvas={section?.canvas} />;
      } else {
        return <>{getChildren(childrenArray, section?.name)}</>;
      }
    },
    [childrenArray],
  );

  return (
    <PageGeneratorContext.Provider
      value={{
        hash: schemaHash,
        page: schema,
        options: options,
        children: childrenArray,
        utils: {
          renderChildren: renderChildren,
        },
      }}
    >
      {options.pageName && (
        <Helmet>
          <title>{options.pageName}</title>
        </Helmet>
      )}
      {schema.canvas && <Canvas canvas={schema.canvas} />}
    </PageGeneratorContext.Provider>
  );
};

export default PageGenerator;
