import { useContext } from 'react';

// Components
import { BasicFullBodyCanvas } from '../../../Canvases';

// Context
import { PageGeneratorContext } from '../..';

// Types
import { CanvasTemplateProps } from '../../types';

// Styles
import './FormCanvas.scss';

const FormCanvas: React.FC<CanvasTemplateProps> = ({ sectionNames }) => {
  const context = useContext(PageGeneratorContext);
  if (context === undefined) {
    throw new Error('PageGeneratorContext is not defined');
  }

  return (
    <BasicFullBodyCanvas
      testMetadata={{
        standardSection: {
          'data-testid': 'PageGenerator_FormCanvas',
        },
      }}
    >
      <>{context.utils.renderChildren(sectionNames?.['full'])}</>
    </BasicFullBodyCanvas>
  );
};

export default FormCanvas;
