import React, { ReactElement, useMemo } from 'react';
import classNames from 'classnames';

// Styles
import './SearchResultsTemplate.scss';

// Components
import Button from '../../../../Button';
import Typography from '../../../../Typography';
import Loader from '../../../../Loader';
import SearchResultRowTemplate from '../SearchResultRowTemplate/SearchResultRowTemplate';

// Types
import { SearchResultBase } from '../SearchTypeaheadTemplate';
import { BadgeProps } from '../../../../Badge';

export type SearchResultsTemplateProps<T extends SearchResultBase> = {
  /** The data to fill search results list */
  results: T[];
  /** Event to fire when result selected */
  onSelect: (selectedResult: T) => void;
  /** Header text to display */
  headerLabel?: string;
  /** Footer text to display */
  footerLabel?: string;
  /** Event to fire when footer link clicked */
  onViewMoreClick?: () => void;
  /** Additional badge props */
  badgeProps?: BadgeProps;
  /** Renders a loader */
  isLoading?: boolean;
  /** Class name for search container */
  className?: string;
};

type SearchResultList = <T extends SearchResultBase>(
  p: SearchResultsTemplateProps<T>,
) => ReactElement<SearchResultsTemplateProps<T>> | null;

const SearchResultsTemplate: SearchResultList = ({
  results,
  onSelect,
  headerLabel,
  footerLabel,
  onViewMoreClick,
  badgeProps,
  isLoading,
  className,
}) => {
  const searchResultsClassNames = useMemo(
    () => classNames('sb-cp-search-results', className),
    [className],
  );

  if (isLoading)
    return (
      <div
        className={searchResultsClassNames}
        data-testid="searchTypeaheadLoading"
      >
        <Loader
          size="small"
          type="spinner"
          className="sb-cp-search-results-loader"
        />
      </div>
    );

  return results.length > 0 ? (
    <div className={searchResultsClassNames} data-testid="searchResultList">
      {headerLabel && (
        <div className="sb-cp-search-result-header">
          <Typography variant="T10">{headerLabel}</Typography>
        </div>
      )}

      {results.map((item) => (
        <SearchResultRowTemplate
          key={item.id}
          item={item}
          onSelect={onSelect}
          badgeProps={badgeProps}
        />
      ))}

      {onViewMoreClick && footerLabel && (
        <div className="sb-cp-search-result-footer">
          <Button
            data-testid="searchResultFooterLink"
            className="sb-cp-search-result-footer-link"
            labelText={footerLabel}
            onClick={onViewMoreClick}
            styleType="link"
            sizeType="small"
          >
            {footerLabel}
          </Button>
        </div>
      )}
    </div>
  ) : null;
};

export default SearchResultsTemplate;
