import React, { useMemo } from 'react';
import classNames from 'classnames';

// Components
import Typography from '../../../Typography';
import { image as defaultImage } from './DomainLoaderImage';

// Layouts
import { StandardLayout, BodyPanel } from '../../../StandardLayout';

// Types
import { TestMetadata } from 'types';

// Styles
import './FullPageLoader.scss';

export interface FullPageLoaderProps {
  /** The name of the page or section being loaded */
  pageName: string;
  /** Use a dataUri to ensure the image is available when requested. Alternatively, use a preload tag for the image. */
  image?: string;
  /** The subheader text */
  subHeader?: string;
  /** Test ID's */
  testMetadata?: {
    body?: TestMetadata;
    image?: TestMetadata;
  };
}

const FullPageLoader: React.FC<FullPageLoaderProps> = ({
  pageName,
  subHeader,
  image,
  testMetadata,
}) => {
  const loaderImageClassNames = useMemo(
    () => classNames('sb-cp-loader_image'),
    [],
  );

  const loaderHeaderClassNames = useMemo(
    () => classNames('sb-cp-loader_header'),
    [],
  );

  const loaderSubHeaderClassNames = useMemo(
    () => classNames('sb-cp-loader_subheader'),
    [],
  );

  return (
    <StandardLayout>
      <BodyPanel
        padding="loose"
        className="sb-cp-loader_body"
        {...testMetadata?.body}
      >
        <img
          className={loaderImageClassNames}
          alt={`Loading ${pageName}`}
          src={image || defaultImage}
          {...testMetadata?.image}
        />
        <Typography variant="T90" className={loaderHeaderClassNames}>
          {pageName}
        </Typography>
        <Typography variant="T70" className={loaderSubHeaderClassNames}>
          {subHeader}
        </Typography>
      </BodyPanel>
    </StandardLayout>
  );
};

export default FullPageLoader;
