import React, { ReactElement, ReactNode, SyntheticEvent } from 'react';

// Components
import { HeaderTemplate } from '../..';
import Typography from '../../../../Typography';
import Icon, { IconProps } from '../../../../Icon';
import Badge, { BadgeProps } from '../../../../Badge';
import TooltipTemplate from '../../../../TooltipTemplate';

// Styles
import './TitleHeaderTemplate.scss';

export interface TitleHeaderTemplateProps {
  name?: string;
  badgeText?: string;
  badgeProps?: BadgeProps;
  tooltip?: ReactElement | string[];
  iconProps?: IconProps;
  description?: string | ReactNode;
  backClick?: (e: SyntheticEvent) => void;
}

const TitleHeaderTemplate: React.FC<TitleHeaderTemplateProps> = ({
  name,
  badgeText,
  badgeProps,
  tooltip,
  iconProps,
  description,
  backClick,
}) => {
  return (
    <HeaderTemplate>
      <div className="sb-cp-title-header-template">
        {backClick && (
          <div
            className="sb-cp-title-header-template__back"
            data-testid="titleHeaderTemplateBack"
            onClick={backClick}
          >
            <Icon
              altText="Go Back"
              faPackage="fas"
              iconName="fa-arrow-left"
              iconType="fa"
              size="smaller"
              title="Go Back"
            />
            <Typography
              variant="T15"
              className="sb-cp-title-header-template__back-text"
            >
              Back
            </Typography>
          </div>
        )}
        <div className="sb-cp-title-header-template__content">
          <div className="sb-cp-title-header-template__title">
            <Typography
              variant="T40"
              className="sb-cp-title-header-template__name"
              testMetadata={{ 'data-testid': 'titleHeaderTemplateName' }}
            >
              {name}
            </Typography>
            {badgeText && (
              <Badge
                testMetadata={{ 'data-testid': 'titleHeaderTemplateBadge' }}
                light
                className="sb-cp-title-header-template__badge"
                shape="pill"
                size="small"
                color="primary"
                {...badgeProps}
              >
                {badgeText}
              </Badge>
            )}
            {tooltip && (
              <TooltipTemplate
                tooltipContent={tooltip}
                size="small"
                testMetadata={{
                  icon: {
                    'data-testid': 'titleHeaderTemplateTooltip',
                  },
                }}
              />
            )}
          </div>
          <div className="sb-cp-title-header-template__subtitle">
            {iconProps && (
              <Icon
                className="sb-cp-title-header-template__icon"
                faPackage="fa-thin"
                testMetadata={{
                  iconFASpan: { 'data-testid': 'titleHeaderTemplateIcon' },
                }}
                {...iconProps}
              />
            )}
            <Typography
              variant="T15"
              className="sb-cp-title-header-template__description"
              testMetadata={{ 'data-testid': 'titleHeaderTemplateDescription' }}
            >
              {description}
            </Typography>
          </div>
        </div>
      </div>
    </HeaderTemplate>
  );
};

export default TitleHeaderTemplate;
