import { FC } from 'react';

// Components
import { BasicCanvas } from '../../../Canvases';
import HeaderTemplate from '../../Templates/HeaderTemplate';
import EntityGrid, { EntityGridProps } from '../../EntityGrid';

// Styles
import './EntityGridSurface.scss';

export interface EntityGridTemplateProps {
  /* Header Title */
  title: string;
  /* Entity grid props */
  entityList: Omit<EntityGridProps, 'portal'>;
  /* Class name for bar */
  className?: string;
  /* Menu Portal */
  portal?: HTMLElement | null;
}

const EntityGridTemplate: FC<EntityGridTemplateProps> = ({
  title,
  entityList,
  portal,
}) => {
  return (
    <BasicCanvas header={<HeaderTemplate title={title} />}>
      <EntityGrid title="Results" {...entityList} portal={portal} />
    </BasicCanvas>
  );
};

export default EntityGridTemplate;
