import { useContext } from 'react';

// Context
import { PageGeneratorContext } from './PageGenerator';

// Components
import { Panel } from '../PanelLayout';
import Container from '../Container';

// Types
import { CanvasObject, AreaObject } from './types';

// utils
import { getContainerPadding, isLayout } from './utils';

export interface CanvasProps {
  canvas: CanvasObject;
}

/* Recursively generates canvases from templates or layouts */
const Canvas = ({ canvas }: CanvasProps): JSX.Element | null => {
  const context = useContext(PageGeneratorContext);
  if (context === undefined) {
    throw new Error('PageGeneratorContext is not defined');
  }

  if (!canvas) {
    return null;
  }

  const Layout = canvas.layout
    ? typeof canvas.layout === 'string'
      ? context.options.layouts[canvas.layout]
      : context.options.layouts[canvas.layout.type]
    : context.options.layouts['standard'];

  const Template =
    canvas.template && context.options.canvases[canvas.template]
      ? context.options.canvases[canvas.template]
      : undefined;

  // Group areas by areaMap location
  const sectionNames = canvas.areas
    ? canvas.areas.reduce((acc, section) => {
        return {
          ...acc,
          [section.area]: section,
        };
      }, {})
    : {};

  if (Template) {
    return <Template sectionNames={sectionNames} />;
  } else if (canvas.layout) {
    return (
      <Layout
        areas={sectionNames}
        className={
          (canvas.layout &&
            isLayout(canvas.layout) &&
            canvas.layout.className) ||
          undefined
        }
      >
        {canvas.areas &&
          canvas.areas.map((section: AreaObject) => {
            const rendition = section?.rendition?.type || 'basic';
            const Rendition = context.options.renditions[rendition];
            return (
              <Panel
                key={section.name}
                name={section.name}
                overflow={section?.overflow}
                isScrollable={false}
              >
                <Rendition
                  fullSize={
                    section?.rendition?.fullSize !== undefined
                      ? section?.rendition?.fullSize
                      : true
                  }
                  elevated={
                    section?.rendition?.elevated !== undefined
                      ? section?.rendition?.elevated
                      : false
                  }
                >
                  <Container
                    padding={getContainerPadding(section)}
                    height="full"
                    testMetadata={{
                      'data-testid': `PageGenerator_Section_${section.name}`,
                    }}
                  >
                    {context.utils.renderChildren(section)}
                  </Container>
                </Rendition>
              </Panel>
            );
          })}
      </Layout>
    );
  } else {
    return null;
  }
};

export default Canvas;
