import { FC, ReactNode, useMemo } from 'react';
import classNames from 'classnames';

// Styles
import './SecondaryNav.scss';

// Types
import { TestMetadata } from 'types';

export interface SecondaryNavProps {
  /** classname */
  className?: string;
  /** title and brand for the nav */
  titleRegion?: ReactNode;
  /** links placed to the right */
  navRegion?: ReactNode;
  // Test IDs for component sections
  testMetadata?: TestMetadata;
}

const SecondaryNav: FC<SecondaryNavProps> = ({
  className,
  titleRegion,
  navRegion,
  testMetadata,
}) => {
  const secondaryNavClassNames = useMemo(
    () => classNames('sb-secondary-nav', className),
    [className],
  );
  return (
    <div className={secondaryNavClassNames} {...testMetadata}>
      {titleRegion && (
        <div className="sb-secondary-nav__title-region">{titleRegion}</div>
      )}
      {navRegion && (
        <div className="sb-secondary-nav__nav-region">{navRegion}</div>
      )}
    </div>
  );
};

export default SecondaryNav;
