import { ComponentCanvas, DualPanelCanvas } from '../../../Canvases';
import TreeBrowser, { TreeBrowserProps } from '../../../TreeBrowser';
import LottiePlayer from '../../../LottiePlayer';

// Types
import { TestMetadata } from 'types';

// Styles
import './EntityBrowser.scss';
import { ReactNode } from 'react';

/* Removing FormGenerator from EntityBrowser Notes:
Embeding FormGenerator in EntityBrowser caused an issue where we could not access
The FormGeneratorContext from inside EntityBrowser. It seems that EntityBrowser was hiding
the FormGeneratorContext from the application.
Removing FormGenerator from EntityBrowser and passing it in as a child component
allows us to access the FormGeneratorContext from inside the application. */
export interface EntityBrowserProps<I extends Record<string, unknown>> {
  /* Tree Browser Config */
  browser: TreeBrowserProps;
  isFetching?: boolean;
  testMetaData?: {
    loader: TestMetadata;
  };
  children?: ReactNode;
}

const EntityBrowser = <I extends Record<string, unknown>>({
  browser,
  isFetching,
  testMetaData,
  children,
}: EntityBrowserProps<I>): JSX.Element => {
  return (
    <DualPanelCanvas
      secondaryPanel={
        <ComponentCanvas fullSize flush>
          <TreeBrowser {...browser} />
        </ComponentCanvas>
      }
    >
      {isFetching ? (
        <LottiePlayer
          style={{ width: '50%', margin: '0 auto' }}
          testMetadata={testMetaData?.loader}
        />
      ) : (
        <>{children}</>
      )}
    </DualPanelCanvas>
  );
};

export default EntityBrowser;
