import { FC, useContext } from 'react';
import { TreeBrowserContext } from './TreeBrowser';
import get from 'lodash/get';

// Components
import Typography from '../Typography';

// Styles
import './TreeEmptyMessage.scss';

export interface MessageProps {
  // Current Level
  level: number;
}

const TreeEmptyMessage: FC<MessageProps> = ({ level }) => {
  const { levels } = useContext(TreeBrowserContext);

  const entity = get(levels, level);
  const displayLevel = entity ? `${entity}s` : 'Items';

  return (
    <Typography
      className="sb-treebrowser__message"
      variant="T10"
      data-testid="treeleaf-message"
    >
      No {displayLevel} Currently Exist
    </Typography>
  );
};

export default TreeEmptyMessage;
