import { FC, ReactNode, useContext, useMemo } from 'react';
import classNames from 'classnames';

// Components
import Container, { ContainerProps } from '../../../Container';
import Loader from '../../../Loader';
import ResultsBadge from './ResultsBadge';
import Typography from '../../../Typography';

// Context
import { DataUtilityContext } from './DataUtilityWrapper';

// Styles
import './DataUtilityBarSummary.scss';

// Types
import { TestMetadata } from '../../../types';

export interface DataUtilityBarSummaryProps {
  /** Icon to show to left of title */
  icon?: ReactNode | null;
  /** Title of data visualization */
  titleLabel?: string;
  /** What name to give the result count */
  entityName?: string;
  /** Visible count shown in the data */
  visibleCount?: number;
  /** Total count shown in the data */
  totalCount?: number;
  /** Container props to spread to parent */
  containerProps?: Omit<ContainerProps, 'children'>;
  /** Class name to give to summary section */
  className?: string;
  /** Test ID to give to summary section */
  testMetadata?: {
    container?: TestMetadata;
    titleLabel?: TestMetadata;
    loader?: TestMetadata;
    resultsBadge?: TestMetadata;
  };
}

const DataUtilityBarSummary: FC<DataUtilityBarSummaryProps> = ({
  visibleCount,
  totalCount,
  containerProps,
  icon,
  titleLabel,
  entityName = 'Items',
  className,
  testMetadata,
}) => {
  const { isLoading } = useContext(DataUtilityContext);

  const summaryClassNames = useMemo(
    () => classNames('sb-data-utility-bar-summary', className),
    [className],
  );

  return (
    <Container
      className={summaryClassNames}
      {...containerProps}
      {...testMetadata?.container}
    >
      {icon}
      {titleLabel ? (
        <Typography variant="T15" {...testMetadata?.titleLabel}>
          {titleLabel}
        </Typography>
      ) : null}
      {isLoading ? (
        <Loader size="small" {...testMetadata?.loader} />
      ) : (
        <ResultsBadge
          visibleCount={visibleCount}
          totalCount={totalCount}
          entityName={entityName}
          testMetadata={testMetadata?.resultsBadge}
        />
      )}
    </Container>
  );
};

export default DataUtilityBarSummary;
