import { FC, ReactNode, useMemo } from 'react';
import classNames from 'classnames';

// Types
import { TestMetadata } from 'types';

// Styles
import './FilterBarTemplate.scss';

export interface FilterBarTemplateProps {
  /** replacement components on the left section */
  leftSection?: ReactNode;
  /** components on the right section */
  rightSection?: ReactNode;
  /** classname */
  className?: string;
  // Test IDs for FilterBarTemplate
  testMetadata?: TestMetadata;
}

const FilterBarTemplate: FC<FilterBarTemplateProps> = ({
  leftSection,
  rightSection,
  className,
  testMetadata,
  ...otherProps
}) => {
  const filterBarTemplateClassNames = useMemo(
    () => classNames('sb-cp-filter-bar-template', className),
    [className],
  );

  return (
    <div
      className={filterBarTemplateClassNames}
      {...otherProps}
      {...testMetadata}
    >
      <div className="sb-cp-filter-bar-template__left-region">
        {leftSection}
      </div>
      <div className="sb-cp-filter-bar-template__right-region">
        {rightSection}
      </div>
    </div>
  );
};

export default FilterBarTemplate;
