import { FC, ReactNode, useMemo } from 'react';
import classNames from 'classnames';

// Types
import { TestMetadata } from '../../../types';

// Styles
import './ActionsTemplate.scss';

export interface ActionsTemplateProps {
  /** replacement components on the left section */
  leftSection?: ReactNode;
  /** components on the right section */
  rightSection?: ReactNode;
  /** classname */
  className?: string;
  // Test IDs for ActionsTemplate
  testMetadata?: TestMetadata;
}

const ActionsTemplate: FC<ActionsTemplateProps> = ({
  leftSection,
  rightSection,
  className,
  testMetadata,
  ...otherProps
}) => {
  const ActionsTemplateClassNames = useMemo(
    () => classNames('sb-cp-actions-template', className),
    [className],
  );

  return (
    <div
      className={ActionsTemplateClassNames}
      {...otherProps}
      {...testMetadata}
    >
      <div className="sb-cp-actions-template__left-region">{leftSection}</div>
      <div className="sb-cp-actions-template__right-region">{rightSection}</div>
    </div>
  );
};

export default ActionsTemplate;
