import { useContext } from 'react';

// Components
import SpellbookDomainCanvas from '../../../Canvases/DomainCanvas';

// Context
import { PageGeneratorContext } from '../../../../PageGenerator';

// Types
import { CanvasTemplateProps } from '../../../../PageGenerator/types';

// Styles
import './DomainCanvas.scss';

const DomainCanvas: React.FC<CanvasTemplateProps> = ({ sectionNames }) => {
  const context = useContext(PageGeneratorContext);
  if (context === undefined) {
    throw new Error('PageGeneratorContext is not defined');
  }

  return (
    <SpellbookDomainCanvas
      nav={<>{context.utils.renderChildren(sectionNames?.['nav'])}</>}
      testMetadata={{
        dualPanel: {
          'data-testid': 'PageGenerator_DomainCanvas',
        },
      }}
    >
      <>{context.utils.renderChildren(sectionNames?.['body'])}</>
    </SpellbookDomainCanvas>
  );
};

export default DomainCanvas;
