import { LayoutProps } from '../types';
import { StandardLayout as SpellbookLayout } from '../../StandardLayout';

const StandardLayout: React.FC = ({
  areas,
  className,
  children,
}: LayoutProps) => {
  return (
    <SpellbookLayout
      className={className}
      areaMap={{
        header: areas?.['header']?.name || 'header',
        body: areas?.['body']?.name || 'body',
        footer: areas?.['footer']?.name || 'footer',
      }}
      testMetadata={{
        'data-testid': 'PageGenerator_StandardLayout',
      }}
    >
      {children}
    </SpellbookLayout>
  );
};

export default StandardLayout;
