import { createContext, useState, useCallback, useEffect, FC } from 'react';
import classNames from 'classnames';

// Styles
import './ControlSet.scss';

export interface ControlSetContextProps {
  /** control id that is currently selected */
  selectedControlId: string | undefined;
  /** If true, selection is required */
  requireSelection: boolean;
  /** Function that gets called when a control is toggled */
  onControlSelect: (controlId: string) => void;
}
// This default declarations are ignored as it is necessary, but they're never called
/* istanbul ignore next */
export const ControlSetContext = createContext<ControlSetContextProps>({
  selectedControlId: undefined,
  requireSelection: false,
  onControlSelect: () => {
    // noop
  },
});

export interface ControlSetProps {
  /** Class name */
  className?: string;
  /** control selection event */
  onControlSelect?: (controlId: string) => void;
  /** If true, selection is required */
  requireSelection?: boolean;
  /** Orientation: 'vertical' or 'horizontal' */
  orientation?: 'vertical' | 'horizontal';
  /** if true, add spacing for horizontal orientation, similar to button group */
  segmented?: boolean;
  /** External value to set the state from */
  value?: string;
}

const ControlSet: FC<ControlSetProps> = ({
  children,
  className,
  onControlSelect,
  requireSelection = false,
  orientation = 'horizontal',
  segmented = false,
  value,
  ...otherProps
}) => {
  const [selectedControlId, setSelectedControlId] = useState<
    string | undefined
  >();

  const handleControlSelect = useCallback(
    (controlId: string): void => {
      setSelectedControlId(controlId);
      if (onControlSelect) {
        onControlSelect(controlId);
      }
    },
    [onControlSelect],
  );

  useEffect(() => {
    value && setSelectedControlId(value);
  }, [value]);
  return (
    <ControlSetContext.Provider
      value={{
        selectedControlId,
        requireSelection,
        onControlSelect: handleControlSelect,
      }}
    >
      <div
        {...otherProps}
        className={classNames(
          `sb-control-set`,
          {
            'sb-control-set_vertical': orientation === 'vertical',
            'sb-control-set_horizontal': orientation === 'horizontal',
            'sb-control-set_segmented': segmented,
          },
          className,
        )}
      >
        {children}
      </div>
    </ControlSetContext.Provider>
  );
};

export default ControlSet;
