import React, { useMemo } from 'react';
import classNames from 'classnames';

// Styles
import './StatusType.scss';

// Types
import { JourneyStatus } from '../../types';

interface StatusProps {
  status?: JourneyStatus;
}

const StatusType = ({ status }: StatusProps): JSX.Element => {
  const bulletStyles = useMemo(
    () =>
      classNames('sb-cp-journey-summary-status-bullet', {
        'sb-cp-journey-summary-status-bullet__in-progress':
          status === JourneyStatus.InProgress ||
          status === JourneyStatus.Resumed,
        'sb-cp-journey-summary-status-bullet__late':
          status === JourneyStatus.Late,
        'sb-cp-journey-summary-status-bullet__paused':
          status === JourneyStatus.Paused,
        'sb-cp-journey-summary-status-bullet__done':
          status === JourneyStatus.Done,
      }),
    [status],
  );

  const statusStyles = useMemo(
    () =>
      classNames('sb-cp-journey-summary-status-item', {
        'sb-cp-journey-summary-status-item__in-progress':
          status === JourneyStatus.InProgress ||
          status === JourneyStatus.Resumed,
        'sb-cp-journey-summary-status-item__late':
          status === JourneyStatus.Late,
        'sb-cp-journey-summary-status-item__paused':
          status === JourneyStatus.Paused,
        'sb-cp-journey-summary-status-item__done':
          status === JourneyStatus.Done,
      }),
    [status],
  );

  return (
    <div className={statusStyles}>
      <div className={bulletStyles} />
      {status?.toUpperCase()}
    </div>
  );
};

export default StatusType;
