import React, { SyntheticEvent } from 'react';

// Components
import PopoverOverlay, { PopoverOverlayProps } from '../../../PopoverOverlay';
import Button from '../../../Button';
import DangerButton from '../DangerButton';

// Types
import { TestMetadata } from '../../../types';

export interface PopoverOverlayTemplateProps extends PopoverOverlayProps {
  // Apply Action
  onApply?: (e: SyntheticEvent) => void;
  // Cancel Action
  onCancel?: () => void;
  // Test Meta Data
  testMetaData?: {
    container?: TestMetadata;
    body?: TestMetadata;
    apply?: TestMetadata;
    cancel?: TestMetadata;
    clear?: TestMetadata;
  };
}

const PopoverOverlayTemplate: React.FC<PopoverOverlayTemplateProps> = ({
  children,
  onCancel,
  onApply,
  isClearEnabled = true,
  testMetaData,
  ...otherProps
}) => {
  const actions =
    onApply && onCancel
      ? {
          actions: (
            <>
              <DangerButton
                data-testid="PopoverOverlayTemplate_Cancel"
                {...testMetaData?.cancel}
                sizeType="small"
                onClick={onCancel}
                className="sb-cp-popover-overlay_footer-action"
              />

              <Button
                data-testid="PopoverOverlayTemplate_Apply"
                {...testMetaData?.apply}
                onClick={onApply}
                sizeType="small"
                type="button"
                className="sb-cp-popover-overlay_footer-action"
              >
                Apply
              </Button>
            </>
          ),
        }
      : {};

  return (
    <PopoverOverlay
      {...otherProps}
      {...actions}
      isClearEnabled={isClearEnabled}
      testMetaData={testMetaData}
    >
      {children}
    </PopoverOverlay>
  );
};

export default PopoverOverlayTemplate;
