import { CSSProperties, FC } from 'react';

// Components
import { ContainerProps } from '../Container';

// Constants
import dmedJson from './animation/dmed.json';

// Hooks
import Lottie, { LottieOptions } from 'lottie-react';

// Types
import { TestMetadata } from 'types';

const defaultOptions: LottieOptions = {
  loop: true,
  autoplay: true,
  animationData: dmedJson,
  rendererSettings: {
    preserveAspectRatio: 'xMidYMid slice',
  },
};

export interface LottiePlayerProps {
  /** Custom lottie options to overwrite */
  customOptions?: LottieOptions;
  /** Custom styles for the lottie */
  style?: CSSProperties;
  /** Test IDs for component sections */
  testMetadata?: TestMetadata;
  /** Container props overrides */
  containerProps?: Omit<ContainerProps, 'children'>;
}

const LottiePlayer: FC<LottiePlayerProps> = ({
  customOptions,
  style,
  testMetadata,
}) => {
  return (
    <Lottie
      {...defaultOptions}
      {...customOptions}
      style={style}
      {...testMetadata}
    />
  );
};

export default LottiePlayer;
