// Utils
import { DataGridContext, getDataGridContext, setContextValue } from './utils';

// Types
import { ColDef, GridApi, IRowNode } from 'ag-grid-community';

export const hasAutoHeightRows = (columnDefs: ColDef[]): boolean =>
  columnDefs.some((columnDef) => columnDef?.autoHeight);

export const findExpandedAutoHeightRowContext = (
  gridApi: GridApi,
  nodeId: IRowNode['id'],
): DataGridContext['expandedAutoHeightRows'][1] | undefined =>
  getDataGridContext(gridApi).expandedAutoHeightRows.find(
    (expandedRow) => expandedRow.id === nodeId,
  );

export const isExpandedAutoHeightRow = (
  gridApi: GridApi,
  nodeId: IRowNode['id'],
): boolean =>
  getDataGridContext(gridApi).expandedAutoHeightRows.some(
    (expandedRow) => expandedRow.id === nodeId,
  );

export const createAutoHeightRowResizeObservable = (
  gridApi: GridApi,
  rowNode: IRowNode,
  elementToObserve: Element,
  expanded: boolean,
): (() => void) =>
  // NOTE: `beans` property does exist in IRowNode.
  // eslint-disable-next-line @typescript-eslint/ban-ts-comment
  // @ts-ignore
  rowNode.beans.resizeObserverService.observeResize(
    elementToObserve,
    (resizeObserverEntry: ResizeObserverEntry[]) => {
      const resizedElement = resizeObserverEntry[0].target;

      if (
        expanded &&
        !!rowNode.id &&
        !isExpandedAutoHeightRow(gridApi, rowNode.id)
      ) {
        setContextValue(gridApi, {
          expandedAutoHeightRows: [
            ...getDataGridContext(gridApi).expandedAutoHeightRows,
            {
              id: rowNode.id,
              height: resizedElement.scrollHeight,
            },
          ],
        });
      }

      if (
        !expanded &&
        !!rowNode.id &&
        isExpandedAutoHeightRow(gridApi, rowNode.id)
      ) {
        setContextValue(gridApi, {
          expandedAutoHeightRows: getDataGridContext(
            gridApi,
          ).expandedAutoHeightRows.filter(
            (expandedRow) => expandedRow.id !== rowNode.id,
          ),
        });
      }

      // We need to trigger a redraw to force `getRowStyle` callback to be invoked so maxHeight value can be recalculated
      // based on the new row height value and spacing will be adjusted accordinly.
      gridApi.redrawRows({
        rowNodes: [rowNode],
      });
    },
  );
