import { FC } from 'react';

// Styles
import './TypeRow.scss';

// Types
import { JourneyItemType } from '../../types';

// Components
import Typography from '../../../../../Typography';
import StateItem from '../StateType';
import StatusItem from '../StatusType/StatusType';
import Workflow from '../WorkflowType/WorkflowType';
import Due from '../DueType/DueType';
import Initiated from '../InitiatedType/InitiatedType';
import GeneralItem from '../GeneralItemType/GeneralItemType';

interface TypeRowProps {
  item: JourneyItemType;
}

const TypeRow: FC<TypeRowProps> = ({ item }): JSX.Element => {
  return (
    <div className="sb-cp-journey-summary-type-row">
      <div className="sb-cp-journey-summary-type-title">
        {item.journeyType === 'general' ? (
          <Typography className="sb-lightFont" variant="T20">
            {item.title?.toUpperCase()}
          </Typography>
        ) : (
          <Typography className="sb-lightFont" variant="T20">
            {item.journeyType.toUpperCase()}
          </Typography>
        )}
      </div>
      {item.journeyType === 'state' && <StateItem state={item.state} />}
      {item.journeyType === 'status' && <StatusItem status={item.status} />}
      {item.journeyType === 'workflow' && (
        <Workflow start={item?.start} end={item?.end} />
      )}
      {item.journeyType === 'due' && <Due due={item?.due} />}
      {item.journeyType === 'initiated' && (
        <Initiated initiated={item?.initiated} />
      )}
      {item.journeyType === 'general' && <GeneralItem value={item?.general} />}
    </div>
  );
};

export default TypeRow;
