import { FC, useMemo } from 'react';
import classNames from 'classnames';

// Styles
import './ContentGridItem.scss';

// Types
import { TestMetadata } from 'types';

export interface ContentGridItemProps {
  /** Class name for Content List Item*/
  className?: string;
  /** Determines if the grid item is an interactive object*/
  isInteractive?: boolean;
  /** Determines if the item in a active state */
  isActive?: boolean;
  // Test IDs for component sections
  testMetadata?: TestMetadata;
}

const ContentGridItem: FC<ContentGridItemProps> = ({
  children,
  className,
  isInteractive = false,
  isActive = false,
  testMetadata,
  ...otherProps
}) => {
  const contentGridItemClassNames = useMemo(
    () =>
      classNames('sb-content-grid-item', className, {
        'sb-content-grid-item_active': isActive,
        'sb-content-grid-item__interactive': isInteractive,
      }),
    [className, isActive, isInteractive],
  );

  return (
    <div
      {...otherProps}
      className={classNames(contentGridItemClassNames)}
      {...testMetadata}
    >
      {children}
    </div>
  );
};

export default ContentGridItem;
