import { ReactNode } from 'react';
import { ActionState } from '../../../types';

export const getActionLabel = (
  actionName: string,
  actionStates?: ActionState[],
  onlyNumberSelected?: boolean,
): ReactNode => {
  if (!actionStates || actionStates.length === 0) {
    return actionName;
  }

  if (!onlyNumberSelected && actionStates.length === 1) {
    return `${actionName} ${actionStates[0].dataLabel}`;
  }

  return `${actionName} (${actionStates.length})`;
};
