import { FC, useMemo } from 'react';
import classNames from 'classnames';

// Styles
import './SearchTemplate.scss';

// Types
import { TestMetadata } from 'types';

export interface SearchOption {
  value: string;
  label: string;
}

export interface SearchTemplateProps {
  title?: string;
  className?: string;
  /** Sets test id */
  testMetadata?: {
    template?: TestMetadata;
    search?: TestMetadata;
  };
}

const SearchTemplate: FC<SearchTemplateProps> = ({
  className,
  children,
  testMetadata,
  ...otherProps
}) => {
  const searchTemplateClassNames = useMemo(
    () => classNames('sb-cp-search-template', className),
    [className],
  );

  return (
    <div
      className={searchTemplateClassNames}
      {...testMetadata?.template}
      {...otherProps}
    >
      <div className="sb-cp-search-template__search" {...testMetadata?.search}>
        {children}
      </div>
    </div>
  );
};

export default SearchTemplate;
