import { useMemo } from 'react';
import classNames from 'classnames';
import { ComponentsClassNames } from './PersonCard';

interface ReturnClasses {
  cardClassName: string;
  containerClassName: string;
  titleClassName: string;
  nameClassName: string;
}

export function useMinimalCardClasses(
  componentsClassNames: ComponentsClassNames,
  isMinimalVariant: boolean,
): ReturnClasses {
  const cardClassName = useMemo(
    () =>
      classNames(
        { 'sb-minimal-person-card': isMinimalVariant },
        componentsClassNames.card,
      ),
    [componentsClassNames.card, isMinimalVariant],
  );
  const containerClassName = useMemo(
    () =>
      classNames(
        { 'sb-minimal-person-card__container': isMinimalVariant },
        componentsClassNames.container,
      ),
    [componentsClassNames.container, isMinimalVariant],
  );
  const titleClassName = useMemo(
    () =>
      classNames(
        { 'sb-minimal-person-card__title': isMinimalVariant },
        componentsClassNames.title,
      ),
    [componentsClassNames.title, isMinimalVariant],
  );
  const nameClassName = useMemo(
    () =>
      classNames(
        { 'sb-minimal-person-card__name': isMinimalVariant },
        componentsClassNames.name,
      ),
    [componentsClassNames.name, isMinimalVariant],
  );

  return { cardClassName, containerClassName, titleClassName, nameClassName };
}
