import {
  FC,
  MutableRefObject,
  useCallback,
  useMemo,
  useRef,
  useState,
  useEffect,
} from 'react';
import classNames from 'classnames';

// Components
import { AgGridReact, AgGridReactProps } from 'ag-grid-react';
import DataGridSelectAllHeader from './DataGridSelectallHeader';

// Types
import { TestMetadata } from '../types';
import { GroupRowInnerRenderer } from './Overrides/GroupRowInnerRenderer';

import {
  GridReadyEvent,
  GridApi,
  ColDef,
  IServerSideDatasource,
  ColumnApi,
  RowStyle,
  RowClassParams,
  RowSelectedEvent,
  IServerSideGetRowsParams,
} from 'ag-grid-community';

// Styles
import './DataGrid.theme.scss';
import './DataGrid.scss';

// Utils
import {
  getChildCount,
  dataGridContextInitialValue,
  getDataGridContext,
  selectAllRows,
  selectGroupData,
  generateGroupId,
  setContextValue,
} from './utils';
import {
  findExpandedAutoHeightRowContext,
  hasAutoHeightRows,
} from './rowHeight.utils';

export interface DataGridProps {
  /** Column Definitions */
  columnDefs: ColDef[];
  /** Default Col Definitions */
  defaultColumnDef?: ColDef;
  /* Ag-grid properties */
  agGrid?: AgGridReactProps;
  /* Container class name */
  className?: string;
  /* Grid class name */
  gridClassName?: string;
  /* Spellbook theme */
  theme?: string;
  /** Data Grid API ref */
  dataGridApiRef?: MutableRefObject<GridApi | null>;
  /** Data Grid API ref */
  dataGridColumnApiRef?: MutableRefObject<ColumnApi | null>;
  /** Table Borders */
  bordered?: 'horizontal' | 'vertical' | 'rows' | 'all' | 'none';
  /** Striped Rows */
  striped?: boolean;
  /**
   * Space in-between rows.
   */
  rowSpace?: boolean;
  /**
   * Whether to use ag-grids server row model.
   */
  dataMode?: 'client' | 'server';
  /**
   * Server row model.
   */
  serverDatasource?: IServerSideDatasource;
  /**
   * Set filtering to on by default (uses agTextColumnFilter unless changed on a per column basis).
   */
  filterable?: boolean;
  /** set the selected rows list */
  onSelectedRowsChanged?: (selectedRows: unknown[]) => void;
  /**
   * Set sorting to on by default (uses a text comparator by default unless changed on a per column basis).
   */
  sortable?: boolean;
  /**
   * Rows are selectable with checkboxes
   */
  selectable?: boolean;
  /**
   * Defines if the row selection is multiple or single.
   */
  selectMode?: 'multiple' | 'single';
  /**
   * Fields are editable
   */
  editable?: boolean;
  /**
   * Cells can be pasted into
   */
  pasteable?: boolean;
  /**
   * Allow columns can be re-arranged via drag & drop (defaults to false)
   */
  moveableColumns?: boolean;
  /**
   * Size of rows
   */
  size?: 'default' | 'large';
  /**
   * Autoheight using DomLayout property - allows grid to auto-size it's height to fit rows
   */
  autoHeight?: boolean;
  /**
   * Test metadata
   */
  testMetadata?: {
    container?: TestMetadata;
  };
  /**
   * Text to display if no results are found
   */
  noResultsMessage?: string;
  /**
   * total row count (server-side)
   */
  totalCount?: number;
  /**
   * is data fetching (server-side)
   */
  isFetching?: boolean;
}

const DataGrid: FC<DataGridProps> = ({
  agGrid,
  columnDefs,
  className,
  gridClassName,
  dataGridApiRef,
  dataGridColumnApiRef,
  serverDatasource,
  filterable,
  sortable,
  selectable,
  selectMode = 'multiple',
  editable,
  pasteable,
  moveableColumns = false,
  defaultColumnDef,
  testMetadata,
  dataMode = 'client',
  bordered = 'all',
  rowSpace = false,
  striped = false,
  theme = 'ag-theme-sb-data-grid-theme',
  size = 'default',
  autoHeight,
  noResultsMessage = 'No results found',
  totalCount,
  isFetching,
}) => {
  const gridApiRef = useRef<GridApi | null>();
  const columnApiRef = useRef<ColumnApi>();
  const isGrouped = useMemo(() => {
    return columnDefs.some((colDef) => colDef.rowGroup != undefined);
  }, [columnDefs]);
  const [rowDataCount, setRowDataCount] = useState<number | undefined>(
    totalCount,
  );
  const [gridApi, setGridApi] = useState<GridApi>();

  const onModelUpdated = useCallback(() => {
    if (!gridApiRef.current) {
      return;
    }

    const context = getDataGridContext(gridApiRef.current);

    if (context.shouldSelectAll) {
      selectAllRows(gridApiRef.current);
    } else if (context.groupSelectionId) {
      const groupNode = gridApiRef.current?.getRowNode(
        context.groupSelectionId,
      );

      // Select group node children when more data is fetched and the group node is selected.
      if (groupNode && groupNode.isSelected()) {
        selectGroupData(
          groupNode,
          true,
          gridApiRef.current,
          context.groupSelectionId,
        );
      }
    }
  }, []);

  const getRowStyle = useCallback(
    (params: RowClassParams): RowStyle | undefined => {
      const rowSpacing = 8;
      // Ignore detail rows and group rows.
      if (params.node.detail || params.node.group) {
        return;
      }

      const autoHeightRowContext = findExpandedAutoHeightRowContext(
        params.api,
        params.node.id,
      );

      if (autoHeightRowContext) {
        // For dynamic row heights the maxHeight value needs to be calculated based on the current row height to achieve row spacing.
        // When row height value is standard spacing is added with css to optimize performance ( .sb-data-grid-row-space class).
        return {
          maxHeight: autoHeightRowContext.height - rowSpacing,
        };
      }
    },
    [],
  );

  // Pre-parse intelligent defaults on columns
  const parsedColumns = useMemo(() => {
    const newColumnDefs: ColDef[] = [];

    if (selectable) {
      if (dataMode === 'server' && !isGrouped) {
        newColumnDefs.push({
          cellClass: 'sb-checkbox-grid',
          checkboxSelection: true,
          field: 'checkbox',
          headerCheckboxSelection: true,
          headerComponentFramework: DataGridSelectAllHeader,
          suppressSizeToFit: true,
          suppressAutoSize: true,
          type: 'component',
          width: 57,
          flex: 0,
        });
      } else if (selectMode === 'multiple') {
        newColumnDefs.push({
          headerClass: 'sb-checkbox-header ag-cell-flex',
          cellClass: 'sb-checkbox-cell ag-cell-no-border ag-cell-flex',
          checkboxSelection: true,
          field: 'checkbox',
          headerCheckboxSelection: true,
          headerName: '',
          suppressSizeToFit: true,
          suppressAutoSize: true,
          type: 'component',
          width: 48,
          flex: 0,
          sortable: false,
          filter: false,
          menuTabs: [],
          lockPosition: true,
          resizable: false,
        });
      }
    }

    newColumnDefs.push(
      ...columnDefs.map((column) => {
        if (column?.width || column?.flex) {
          return column;
        }

        // Flex setting on fields without explicit sizing to fill remaining space
        return {
          ...column,
          flex: 1,
        };
      }),
    );

    return newColumnDefs;
  }, [columnDefs, selectable, selectMode, dataMode, isGrouped]);

  const onRowSelectedChangedHandler = useCallback(
    (e: RowSelectedEvent): void => {
      selectGroupData(e.node, e.node.isSelected() || false, e.api, e?.node?.id);
    },
    [],
  );

  const serverSideDatasourceCustom = useCallback(():
    | IServerSideDatasource
    | undefined => {
    if (dataMode === 'server' && serverDatasource) {
      const getRows = async (
        params: IServerSideGetRowsParams,
      ): Promise<void> => {
        serverDatasource?.getRows(params);
        const { groupKeys, rowGroupCols } = params.request;
        // When grouping and fetching leaf nodes we track this flow by setting
        // groupSelectionId so after new data is render We update selected rows
        // in onModelUpdated event.
        if (rowGroupCols.length > 0 && groupKeys.length > 0) {
          const groupNodeId = generateGroupId(groupKeys);
          const groupNode = gridApiRef.current?.getRowNode(groupNodeId);

          if (groupNode?.isSelected()) {
            setContextValue(params.api, {
              groupSelectionId: generateGroupId(groupKeys),
            });
          }
        }
      };
      return {
        ...serverDatasource,
        getRows,
      };
    }
    return undefined;
  }, [dataMode, serverDatasource, gridApiRef]);

  // Default options for the data grid
  const gridOptions: AgGridReactProps = useMemo(() => {
    return {
      defaultColDef: {
        // Only show the filter menu by default
        menuTabs: filterable ? ['filterMenuTab'] : [],
        // Don't allow visibility to be updated
        lockVisible: true,
        // Don't allow pinned status to be updated
        lockPinned: true,
        // Allow columns to be resized
        resizable: true,
        // If sortable passed, all columns defaulted to sortable
        sortable: typeof sortable === 'undefined' ? false : sortable,
        // If filterable passed, all columns defaulted to filterable with agTextColumnFilter
        filter:
          typeof filterable === 'undefined' ? false : 'agTextColumnFilter',
        // If editable passed in, all columns are editable by default
        editable: editable || false,
        ...defaultColumnDef,
      },
      context: dataGridContextInitialValue,
      // Lets you copy text from cells
      enableCellTextSelection: true,
      // Prevents dragging columns within the table
      suppressMovableColumns: !moveableColumns,
      // Prevents hiding columns when dragging them off-screen
      suppressDragLeaveHidesColumns: true,
      // Header cell height
      headerHeight: 48,
      // Must be 48 or greater for "spacing" mode to work as intended
      rowHeight: size === 'large' ? 64 : 56,
      // Grouped
      ...(isGrouped &&
        !selectable && {
          groupRowRendererParams: {
            // Show the count badge in the grouping row
            innerRenderer: GroupRowInnerRenderer,
            // Suppress the built in count, which will be represented in the renderer
            suppressCount: true,
          },
        }),
      ...(isGrouped &&
        selectable && {
          groupRowRendererParams: {
            // Show the count badge in the grouping row
            innerRenderer: GroupRowInnerRenderer,
            // Suppress the built in count, which will be represented in the renderer
            suppressCount: true,
            checkbox: !!selectMode,
          },
        }),
      // Server Mode
      ...(dataMode === 'server' && {
        rowModelType: 'serverSide',
        // Loads server side data in blocks (set to full for a complete level fetch)
        serverSideStoreType: 'partial',
        serverSideDatasource: serverSideDatasourceCustom(),
        serverSideInfiniteScroll: true,
      }),
      // Grouped Server
      ...(isGrouped &&
        dataMode === 'server' && {
          getChildCount: getChildCount,
        }),
      ...(selectable &&
        selectMode === 'multiple' && {
          rowSelection: 'multiple',
        }),
      ...(selectable &&
        selectMode === 'single' && {
          rowSelection: 'single',
          suppressRowClickSelection: false,
        }),
      ...(selectable && {
        rowSelection: 'multiple',
      }),
      // No Results message
      ...(noResultsMessage && {
        overlayNoRowsTemplate: noResultsMessage,
      }),
      // Editable
      ...(editable && {
        singleClickEdit: editable,
      }),
      // Pasteable
      ...(pasteable && {
        enableRangeSelection: true,
        rowSelection: 'multiple',
        singleClickEdit: false,
      }),
      rowMultiSelectWithClick: !!selectMode,
      // Extra rows stored in memory (fetches one block ahead)
      rowBuffer: agGrid?.rowBuffer ?? 50,
      // Block of data
      cacheBlockSize: agGrid?.cacheBlockSize ?? 50,
      // -1 prevents it from tossing old blocks as you scroll (can scroll back to previous data without reloading)
      maxBlocksInCache: -1,
      // Allows skipping ahead in infinite loading
      blockLoadDebounceMillis: 1000,
      // Use rows for groups instead of a grouping column
      groupDisplayType: 'groupRows',
      // Not clickable
      ...(selectMode !== 'single' && {
        suppressRowClickSelection: true,
      }),
      // No highlight on Master/Detail
      suppressRowHoverHighlight: agGrid?.masterDetail,
      // Always show menu button if available
      suppressMenuHide: true,
      // Only use `getRowStyle` callback when we are using autoHeight columns to improve performance.
      ...(rowSpace && hasAutoHeightRows(columnDefs) && { getRowStyle }),
      // Set domlayout to auto height
      ...(autoHeight && { domLayout: 'autoHeight' }),
      ...agGrid,
    };
  }, [
    filterable,
    sortable,
    defaultColumnDef,
    isGrouped,
    dataMode,
    agGrid,
    size,
    selectable,
    selectMode,
    editable,
    pasteable,
    moveableColumns,
    noResultsMessage,
    getRowStyle,
    rowSpace,
    autoHeight,
    columnDefs,
    serverSideDatasourceCustom,
  ]);

  // Expose grid API to parent component
  const setDataGridApi = useCallback(
    (api: GridApi, columnApi: ColumnApi): void => {
      if (dataGridApiRef) {
        dataGridApiRef.current = api;
      }
      if (dataGridColumnApiRef) {
        dataGridColumnApiRef.current = columnApi;
      }
      return;
    },
    [dataGridApiRef, dataGridColumnApiRef],
  );

  // Hookup API to component
  const setGridReady = useCallback(
    (params: GridReadyEvent): void => {
      gridApiRef.current = params.api;
      setGridApi(params.api);
      columnApiRef.current = params.columnApi;
      setDataGridApi(params.api, params.columnApi);
      gridOptions?.onGridReady && gridOptions?.onGridReady(params);
    },
    [gridOptions, setDataGridApi],
  );

  useEffect(() => {
    if (gridApi) {
      if (dataMode === 'client') {
        setRowDataCount(gridApi.getDisplayedRowCount());
      } else {
        setRowDataCount(totalCount);
      }

      if (
        (dataMode === 'server' && isFetching === false && rowDataCount === 0) ||
        (dataMode === 'client' && rowDataCount === 0)
      ) {
        gridApi.showNoRowsOverlay();
      } else {
        gridApi.hideOverlay();
      }
    }
  }, [gridApi, isFetching, totalCount, dataMode, gridOptions, rowDataCount]);

  // Class interface
  const containerClassNames = useMemo(
    () =>
      classNames(
        'sb-data-grid',
        {
          'sb-data-grid_stripeless': !striped,
          'sb-data-grid_borderless': bordered === 'none',
          'sb-data-grid_bordered': bordered === 'all',
          ['sb-data-grid_bordered-' + bordered]:
            bordered !== 'all' && !isGrouped,
          'sb-data-grid_bordered-horizontal':
            isGrouped || !!agGrid?.masterDetail,
          'sb-data-grid-row-space': rowSpace && !isGrouped,
          'sb-data-grid-grouped': isGrouped,
          'sb-data-grid-master-detail': !!agGrid?.masterDetail,
          [`sb-data-grid-size_${size}`]: size,
        },
        theme,
        className,
      ),
    [
      theme,
      className,
      striped,
      bordered,
      rowSpace,
      isGrouped,
      size,
      agGrid?.masterDetail,
    ],
  );

  return (
    <div className={containerClassNames} {...testMetadata?.container}>
      <AgGridReact
        className={gridClassName}
        {...gridOptions}
        onGridReady={setGridReady}
        columnDefs={parsedColumns}
        noRowsOverlayComponentParams={{
          noRowsMessage: noResultsMessage,
        }}
        getContextMenuItems={agGrid?.getContextMenuItems}
        onModelUpdated={onModelUpdated}
        onRowSelected={onRowSelectedChangedHandler}
      />
    </div>
  );
};

export default DataGrid;
