import { FC, useMemo } from 'react';
import classNames from 'classnames';

// Components
import Button from '../../../Button';
import { IconProps } from '../../../Icon';
import Popover, { PopoverProps } from '../../../Popover';
import PopoverTemplateItem, {
  PopoverTemplateItemProps,
} from './PopoverTemplateItem';
import Typography from '../../../Typography';

// Types
import { TippyProps } from '@tippyjs/react';
import { TestMetadata } from 'types';

// Styles
import './PopoverTemplate.scss';

const DefaultLeftIconProps: IconProps = {
  iconName: 'fa-align-left',
  faPackage: 'far',
  iconType: 'fa',
};

const DefaultRightIconProps: IconProps = {
  iconName: 'fa-chevron-right',
  faPackage: 'far',
  iconType: 'fa',
};

const defaultTippyProps: TippyProps = {
  arrow: false,
  duration: 0,
  interactive: true,
  maxWidth: 'none',
  offset: [0, -8],
  placement: 'right-start',
};

export interface PopoverTemplateProps
  extends Omit<PopoverProps, 'testMetadata' | 'popoverContent'> {
  /** Title of the parent Popover **/
  title?: string;
  /** Props to be passed to the children Tippy components */
  items?: PopoverTemplateItemProps[];
  /** Props to be passed to the children Tippy components */
  itemsTippyProps?: TippyProps;
  /** Overrides the left icon shown in the first level items */
  leftIconProps?: IconProps;
  /** Overrides the right icon chevron */
  rightIconProps?: IconProps;
  /** Test IDs for sections of PopoverTemplate */
  testMetadata?: {
    button?: TestMetadata;
    popover?: TestMetadata;
    popoverContent?: TestMetadata;
  };

  /** Function fired when popover close button is clicked */
  onClose?: () => void;
}

const PopoverTemplate: FC<PopoverTemplateProps> = ({
  title,
  visible,
  children,
  tippyProps,
  className,
  items,
  itemsTippyProps,
  leftIconProps,
  rightIconProps,
  testMetadata,
  onClose,
}) => {
  const childrenTippyProps: TippyProps = useMemo(
    () => ({
      ...defaultTippyProps,
      offset: [-8, -8],
      ...itemsTippyProps,
    }),
    [itemsTippyProps],
  );

  const parentTippyProps: TippyProps = useMemo(
    () => ({
      ...defaultTippyProps,
      arrow: true,
      placement: 'right-start',
      offset: [-8, 8],
      ...tippyProps,
    }),
    [tippyProps],
  );

  const renderItems = (
    items: PopoverTemplateItemProps[],
  ): PopoverTemplateItemProps[] =>
    items.map(
      ({ label, items: childItems, isFirstLevel, ...otherProps }, index) => (
        <PopoverTemplateItem
          label={label}
          key={index}
          items={childItems && renderItems(childItems)}
          rightIconProps={
            childItems && (rightIconProps ?? DefaultRightIconProps)
          }
          leftIconProps={
            isFirstLevel ? leftIconProps ?? DefaultLeftIconProps : undefined
          }
          isFirstLevel={isFirstLevel}
          tippyProps={childrenTippyProps}
          {...otherProps}
        />
      ),
    );

  return (
    <Popover
      visible={visible}
      tippyProps={parentTippyProps}
      className={classNames('sb-cp-popover-template__wrapper', className)}
      {...testMetadata?.popover}
      popoverContent={
        <>
          <Typography
            variant="T10"
            bold
            className="sb-cp-popover-template__header"
          >
            {title}
            <Button
              styleType="link"
              iconOnly
              iconType="fa-times"
              labelText="Close"
              className="sb-cp-popover-template__close-button"
              {...testMetadata?.button}
              onClick={onClose}
            />
          </Typography>
          <div
            className="sb-cp-popover-template__content"
            {...testMetadata?.popoverContent}
          >
            {items &&
              renderItems(
                items.map((item) => ({ ...item, isFirstLevel: true })),
              )}
          </div>
        </>
      }
    >
      {children}
    </Popover>
  );
};

export default PopoverTemplate;
