import React, { useCallback, useState } from 'react';

// Components
import ButtonGroup from '../../../ButtonGroup';
import Button from '../../../Button';
import MenuList from '../../../MenuList';
import PopoverButton from '../../../PopoverButton';
import PopoverOverlayTemplate from '../../Templates/PopoverOverlayTemplate';

// Types
import {
  CellRendererFunc,
  GridMoreOption,
  GridOption,
  OptionPopover,
} from '../types';
import { Option, TestMetadata } from '../../../types';

// Styles
import './DataGridOptions.scss';

export interface GridOptionsContext {
  options: GridOption[];
  moreOptions?: GridMoreOption[];
  portal?: HTMLElement | null;
  moreOptionsTestMetadata?: TestMetadata;
}

const DataGridOptions: CellRendererFunc<GridOptionsContext> = (
  props,
): JSX.Element => {
  const [isPopoverButtonOpen, setIsPopoverButtonOpen] = useState(false);

  const options = props.options;
  const more = props?.moreOptions;
  const optionsPopover: OptionPopover[] | undefined = more?.map(
    (moreOption) => ({
      value: moreOption.title.toLocaleLowerCase(),
      label: moreOption.title,
      isDisabled: moreOption.isEnabled
        ? !moreOption.isEnabled(props.data)
        : false,
    }),
  );

  const handleClick = useCallback(
    (option: Option) => {
      const onClickEvent = more?.find(
        (action) => action.title === option.label,
      );
      if (onClickEvent) {
        onClickEvent.action(props.data);
      }
      setIsPopoverButtonOpen(false);
    },
    [props.data, more, setIsPopoverButtonOpen],
  );

  const handlePopoverButtonToggle = useCallback(
    (open: boolean) => {
      setIsPopoverButtonOpen(open);
    },
    [setIsPopoverButtonOpen],
  );

  return (
    <ButtonGroup
      data-testid="StandardTable_Cell_Options"
      className="sb-cp-standard-table__options-group"
    >
      {options &&
        options.map((option: GridOption, index: number) => {
          return (
            <Button
              key={index}
              iconType={option.icon}
              iconPackage="fa-light"
              sizeType="smaller"
              className="sb-cp-standard-table__option"
              onClick={(e) => {
                option.action(props.data, e);
              }}
              labelText={option.title}
              iconOnly
              type="button"
              testMetadata={
                option.testMetadata ?? {
                  'data-testid': 'StandardTable_Cell_Option',
                }
              }
              disabled={
                option.isEnabled ? !option.isEnabled(props.data) : false
              }
            />
          );
        })}
      {typeof more !== 'undefined' && (
        <PopoverButton
          handleToggle={handlePopoverButtonToggle}
          isOpen={isPopoverButtonOpen}
          portal={props.portal}
          buttonProps={{
            iconType: 'fa-ellipsis',
            iconPackage: 'fa-light',
            styleType: 'promoted',
            className: 'sb-cp-standard-table__option',
            labelText: 'More',
            testMetadata: props.moreOptionsTestMetadata ?? {
              'data-testid': 'StandardTable_Cell_MoreOption',
            },
          }}
        >
          {optionsPopover && (
            <PopoverOverlayTemplate flush elevated={false}>
              <MenuList
                className="sb-cp-standard-table__options-menu"
                options={optionsPopover}
                selectedOption={undefined}
                onChange={handleClick}
                showSelected={false}
                testMetadata={{
                  wrapper: {
                    'data-testid': 'StandardTable_Cell_MenuItem',
                  },
                }}
              />
            </PopoverOverlayTemplate>
          )}
        </PopoverButton>
      )}
    </ButtonGroup>
  );
};

export default DataGridOptions;
