import { MutableRefObject, useMemo, useCallback } from 'react';
import classNames from 'classnames';
import cloneDeep from 'lodash/cloneDeep';

// Components
import DataGrid from '../../DataGrid';
import DataGridTextFilter from './Filters/DataGridTextFilter';
import DataGridOptions from './Options/DataGridOptions';
import IDFieldRenderer from './Renderers/IDFieldRenderer';
import TypeFieldRenderer from './Renderers/TypeFieldRenderer';
import TextFieldRenderer from './Renderers/TextFieldRenderer';
import DataGridHeader from './Overrides/DataGridHeader';
import DataGridNoRowsOverlay from './Overrides/DataGridNoRowsOverlay';
import DataGridMultiselectFilter from './Filters/DataGridMultiselectFilter';
import DataGridDateFilter from './Filters/DataGridDateFilter';
import PrimaryFieldRenderer from './Renderers/PrimaryFieldRenderer';
import PillboxListRenderer from './Renderers/PillboxListRenderer';
import LinkRenderer from './Renderers/LinkRenderer';
import ExpandableFieldRenderer from './Renderers/ExpandableFieldRenderer';

// Types
import {
  ColDef,
  ColumnApi,
  FilterChangedEvent,
  FirstDataRenderedEvent,
  GridApi,
  GridReadyEvent,
  ICellRendererParams,
  IServerSideGetRowsParams,
  RowEditingStartedEvent,
  RowEditingStoppedEvent,
  RowValueChangedEvent,
  RowHeightParams,
  SelectionChangedEvent,
} from 'ag-grid-community';
import { Column, GridMoreOption, GridOption } from './types';
import { AgGridReactProps } from 'ag-grid-react';
import { TestMetadata } from '../../types';

// Styles
import './StandardTable.scss';

export type StandardTableProps = StandardTableCommonProps &
  DataProps &
  MasterDetailProps;

export interface StandardTableCommonProps {
  /**
   *  Column Definitions
   */
  columns: Column[];
  /**
   *  Grid ready event
   */
  onGridReady?: (params: GridReadyEvent) => void;
  /**
   * First Data Rendered
   */
  onFirstDataRendered?: (params: FirstDataRenderedEvent) => void;
  /**
   * On Select Change
   */
  onSelectionChanged?: (event: SelectionChangedEvent) => void;
  /**
   * Filter has been modified and applied.
   */
  onFilterChanged?: (event: FilterChangedEvent) => void;
  /**
   * Class name
   */
  className?: string;
  /**
   * Grid class name
   */
  gridClassName?: string;
  /**
   * Enable sorting on columns by default
   */
  sortable?: boolean;
  /**
   * Enable filtering on columns by default
   */
  filterable?: boolean;
  /**
   * Add checkbox select column
   */
  selectable?: boolean;
  /**
   * Defines if the row selection is multiple or single.
   */
  selectMode?: 'multiple' | 'single';
  /**
   * Enable editing on columns by default
   */
  editable?: boolean;
  /**
   * Enable paste functionality
   */
  pasteable?: boolean;
  /**
   * Autoheight using DomLayout property - allows grid to auto-size it's height to fit rows
   */
  autoHeight?: boolean;
  /**
   * Enables moveable columns
   */
  moveableColumns?: boolean;
  /**
   * Add options column
   */
  options?: {
    // Pin it to the end or not
    pinned?: boolean;
    // List of top level options
    options: GridOption[];
    // List of sub options
    moreOptions?: GridMoreOption[];
  };
  /**
   * Data Grid API ref
   */
  dataGridApiRef?: MutableRefObject<GridApi | null>;
  /**
   * Data Grid Column API ref
   */
  dataGridColumnApiRef?: MutableRefObject<ColumnApi | null>;
  /**
   * Test metadata
   */
  testMetadata?: {
    container?: TestMetadata;
    moreOptions?: TestMetadata;
  };
  /**
   * Portal element to be used by this component.
   */
  portal?: HTMLElement | null;
  // message to display if no results are found
  noResultsMessage?: string;
  // is data still fetching
  isFetching?: boolean;
  /** Triggers when row is submitted on edit mode */
  onRowValueChanged?: (event: RowValueChangedEvent) => void;
  /** Triggers when row enters edit mode */
  onRowEditingStopped?: (event: RowEditingStoppedEvent) => void;
  /** Triggers when row exists edit mode */
  onRowEditingStarted?: (event: RowEditingStartedEvent) => void;
  /** Should edit mode be cell (no value) or row (fullRow) */
  editType?: 'fullRow';
  /** Allows you to process cells from the clipboard. Handy if for example you have number fields, and want to block non-numbers from getting into the grid. */
  processCellFromClipboard?: AgGridReactProps['processCellFromClipboard'];
  /** Allows complete control of the paste operation, including cancelling the operation  or replacing the data with other data. */
  processDataFromClipboard?: AgGridReactProps['processDataFromClipboard'];
}

export type DataProps =
  | {
      /**
       * Sets the StandardTable mode to be client or server side.
       */
      mode?: 'client';
      /**
       * When mode is `client` set this prop to define the data StandardTable will be populated with.
       */
      rowData: AgGridReactProps['rowData'];
      /**
       * When mode is `server` this function will be called every time StandardTable request more data.
       */
      loadData?: never;
      /**
       * When mode is `server` this will determine how many records will be rendered by page.
       */
      pageSize?: never;
      /**
       * When mode is `server` this will determine how many total records will be rendered.
       */
      totalCount?: never;
    }
  | {
      mode: 'server';
      rowData?: never;
      loadData: (params: IServerSideGetRowsParams) => void;
      pageSize?: number;
      totalCount?: number;
    };

export type MasterDetailProps =
  | {
      /**
       * Enables Master/Detail mode. Set a column as `masterColumn` in column configuration to be able to expand details.
       */
      masterDetail: true;
      /**
       * When `masterDetail` is enabled use it prop to provide a custom detailCellRenderer to use when a master row is expanded.
       */
      detailCellRenderer: AgGridReactProps['detailCellRenderer'];
      /**
       * When `masterDetail` is enabled, we can optionally use this prop to control the height of a detail row. Default to `auto`.
       * - > Use `auto` to calculate the height automatically. This option will turn off virtualization, do not use it if you have many rows (eg 100+).
       * - > Pass a `number` to define a fixed height in pixels.
       * - > Pass a `function` to calculate the row height dynamically (only runs on detail rows). Function should return a positive number of pixels, or return null/undefined to use the default row height.
       */
      masterDetailRowHeight?:
        | 'auto'
        | AgGridReactProps['detailRowHeight']
        | AgGridReactProps['getRowHeight'];
      /**
       *  Mouse entered cell on master column (useful for pre-fetching details)
       */
      onMasterDetailCellMouseOver?: (
        rowData: ICellRendererParams['data'],
      ) => void;
      /**
       * Chooses whether something is a master row or not
       */
      isRowMaster?: AgGridReactProps['isRowMaster'];
    }
  | {
      masterDetail?: false;
      detailCellRenderer?: never;
      onMasterDetailCellMouseOver?: never;
      masterDetailRowHeight?: never;
      isRowMaster?: never;
    };

const StandardTable: React.FC<StandardTableProps> = ({
  columns,
  className,
  gridClassName,
  filterable,
  selectable,
  selectMode,
  editable,
  pasteable,
  processCellFromClipboard,
  processDataFromClipboard,
  rowData,
  options,
  mode,
  loadData,
  testMetadata,
  dataGridApiRef,
  dataGridColumnApiRef,
  autoHeight,
  portal,
  pageSize = 50,
  onGridReady,
  onFirstDataRendered,
  onSelectionChanged,
  onFilterChanged,
  masterDetail,
  detailCellRenderer,
  onMasterDetailCellMouseOver,
  masterDetailRowHeight = 'auto',
  noResultsMessage,
  isFetching,
  totalCount,
  isRowMaster,
  moveableColumns,
  onRowValueChanged,
  onRowEditingStopped,
  onRowEditingStarted,
  editType,
}) => {
  const tableColumns = useMemo(() => {
    const initialColumns = cloneDeep(columns);
    if (initialColumns.length > 7) {
      console.warn(
        'A standard table should have no more than 7 columns for an optimal user experience.',
      );
    }

    const finalColumns: ColDef[] = initialColumns.map((columnDef) => {
      const isMasterColumn = !!columnDef.masterColumn && masterDetail;
      return {
        // Custom Definition
        ...columnDef.definition,
        // Convert types to renderers
        ...((columnDef.type === 'text' || !columnDef.type) && {
          cellRenderer: 'TextFieldRenderer',
          cellRendererParams: { isMasterColumn, onMasterDetailCellMouseOver },
        }),
        ...(columnDef.type === 'expandable' && {
          cellRenderer: 'ExpandableFieldRenderer',
          cellRendererParams: { isMasterColumn, onMasterDetailCellMouseOver },
          autoHeight: true,
        }),
        ...(columnDef.type === 'id' && {
          cellRenderer: 'IDFieldRenderer',
          cellRendererParams: { isMasterColumn, onMasterDetailCellMouseOver },
        }),
        ...(columnDef.type === 'type' && {
          cellRenderer: 'TypeFieldRenderer',
          cellRendererParams: {
            badgeProps: columnDef.badgeProps,
            isMasterColumn,
            onMasterDetailCellMouseOver,
          },
        }),
        ...(columnDef.type === 'custom' && {
          cellRenderer: columnDef.definition?.cellRenderer,
          cellRendererParams: columnDef.definition?.cellRendererParams,
        }),
        ...(columnDef.type === 'pillbox-list' && {
          cellRenderer: 'PillboxListRenderer',
          cellRendererParams: {
            badgeProps: columnDef.badgeProps,
            countBadgeProps: columnDef.countBadgeProps,
            appendTo: portal,
            groupItems: columnDef.groupItems,
            isMasterColumn,
            onMasterDetailCellMouseOver,
          },
        }),
        ...(columnDef.type === 'primary' && {
          cellRenderer: 'PrimaryFieldRenderer',
          cellRendererParams: {
            secondaryField: columnDef.secondaryField,
            isMasterColumn,
            onMasterDetailCellMouseOver,
          },
        }),
        ...(columnDef.type === 'link' && {
          cellRenderer: 'LinkRenderer',
          cellRendererParams: {
            onClick: columnDef.onClick,
            linkTitle: columnDef.linkTitle,
            isMasterColumn,
            onMasterDetailCellMouseOver,
          },
        }),
        // Convert filter types to named filters
        ...((columnDef.filter === 'text' ||
          (typeof columnDef.definition?.filter === 'undefined' &&
            filterable)) && { filter: 'dataGridTextFilter' }),
        ...(columnDef.filter === 'list' && {
          filter: 'dataGridListFilter',
          filterParams: {
            filters: columnDef.filters,
            ...(columnDef.objKey && { objKey: columnDef.objKey }),
          },
        }),
        ...(columnDef.filter === 'date' && {
          filter: 'dataGridDateFilter',
        }),
        ...(typeof columnDef.filter === 'undefined' &&
          typeof columnDef.definition?.filter === 'undefined' &&
          !filterable && { filter: false }),
        resizable: false,
        suppressMovable: !moveableColumns,
      };
    });
    if (options) {
      finalColumns.push({
        field: 'options',
        headerName: 'Options',
        cellRenderer: DataGridOptions,
        cellRendererParams: {
          ...options,
          portal: portal,
          moreOptionsTestMetadata: testMetadata?.moreOptions,
        },
        cellClass: 'ag-cell-flex sb-cp-options',
        suppressSizeToFit: true,
        type: 'component',
        pinned: options.pinned ? 'right' : false,
        lockPosition: options.pinned ? true : false,
        lockPinned: options.pinned ? true : false,
        resizable: false,
        filter: false,
        width: 146,
        menuTabs: [],
      });
    }

    return finalColumns;
  }, [
    columns,
    filterable,
    options,
    portal,
    testMetadata?.moreOptions,
    masterDetail,
    onMasterDetailCellMouseOver,
    moveableColumns,
  ]);

  // Class interface
  const classes = useMemo(
    () =>
      classNames(
        'sb-cp-standard-table',
        {
          'sb-cp-standard-table_selectable': selectable,
          'sb-cp-standard-table_options': options,
          'sb-cp-standard-table_options-pinned': options?.pinned,
        },
        className,
      ),
    [className, options, selectable],
  );

  const masterDetailRowHeightConfig = useMemo<
    | Pick<
        AgGridReactProps,
        'detailRowAutoHeight' | 'detailRowHeight' | 'getRowHeight'
      >
    | undefined
  >(() => {
    if (masterDetailRowHeight === 'auto') {
      return {
        detailRowAutoHeight: true,
      };
    }

    if (typeof masterDetailRowHeight === 'number') {
      return {
        detailRowHeight: masterDetailRowHeight,
      };
    }

    if (typeof masterDetailRowHeight === 'function') {
      return {
        getRowHeight: (params: RowHeightParams) => {
          const isDetailRow = params.node.detail;

          // Ignore rows that are not master detail rows.
          if (!isDetailRow) {
            return undefined;
          }

          return masterDetailRowHeight(params);
        },
      };
    }
  }, [masterDetailRowHeight]);

  const onGridReadyHandler = (params: GridReadyEvent): void => {
    if (loadData) {
      params.api.setServerSideDatasource({ getRows: loadData });
    }

    if (onGridReady) {
      onGridReady(params);
    }
  };

  // Manipulate the context Menu
  const getContextMenuItems = useCallback((): string[] => {
    const result: string[] = ['copy'];
    return result;
  }, []);

  return (
    <DataGrid
      dataGridApiRef={dataGridApiRef}
      dataGridColumnApiRef={dataGridColumnApiRef}
      size={
        columns.find(
          (column) => column.type === 'primary' && column.secondaryField,
        )
          ? 'large'
          : 'default'
      }
      agGrid={{
        onGridReady: onGridReadyHandler,
        isRowMaster: isRowMaster,
        onFirstDataRendered: onFirstDataRendered,
        onSelectionChanged: onSelectionChanged,
        onFilterChanged: onFilterChanged,
        rowData: rowData,
        getContextMenuItems,
        frameworkComponents: {
          agColumnHeader: DataGridHeader,
          agNoRowsOverlay: DataGridNoRowsOverlay,
          dataGridTextFilter: DataGridTextFilter,
          dataGridListFilter: DataGridMultiselectFilter,
          dataGridDateFilter: DataGridDateFilter,
          IDFieldRenderer: IDFieldRenderer,
          PillboxListRenderer: PillboxListRenderer,
          TypeFieldRenderer: TypeFieldRenderer,
          TextFieldRenderer: TextFieldRenderer,
          PrimaryFieldRenderer: PrimaryFieldRenderer,
          LinkRenderer: LinkRenderer,
          ExpandableFieldRenderer: ExpandableFieldRenderer,
        },
        cacheBlockSize: pageSize,
        rowBuffer: pageSize,
        // Master/Detail config
        ...masterDetailRowHeightConfig,
        masterDetail: masterDetail,
        detailCellRenderer: detailCellRenderer,
        onRowValueChanged,
        onRowEditingStopped,
        onRowEditingStarted,
        stopEditingWhenCellsLoseFocus: true,
        editType,
        processCellFromClipboard,
        processDataFromClipboard,
      }}
      defaultColumnDef={{
        resizable: false,
        filter:
          typeof filterable === 'undefined' ? false : 'dataGridTextFilter',
      }}
      columnDefs={tableColumns}
      className={classes}
      gridClassName={gridClassName}
      filterable={filterable}
      selectable={selectable}
      selectMode={selectMode}
      editable={editable}
      pasteable={pasteable}
      autoHeight={autoHeight}
      rowSpace
      bordered="rows"
      dataMode={mode}
      testMetadata={testMetadata}
      noResultsMessage={noResultsMessage}
      isFetching={isFetching}
      totalCount={totalCount}
      moveableColumns={moveableColumns}
    />
  );
};

export default StandardTable;
