import { TreeSaving } from './types';

export const getSavingState = (
  id: string | number,
  isSaving: string,
): TreeSaving => {
  if (!isSaving) {
    return {};
  }

  if (isSaving === id) {
    return {
      isFetching: true,
      disabled: true,
    };
  }

  return {
    disabled: true,
  };
};
