import { SideNavRendition } from '../Renditions';
import PageGenerator, { PageGeneratorProps } from '../../PageGenerator';

// Canvases
import SideNavCanvas from './Canvases/SideNavCanvas';
import DomainCanvas from './Canvases/DomainCanvas';
// Schemas
import schemaValues from './Schemas';

export interface SurfaceGeneratorProps {
  page: string;
  pageName?: string;
}

/* This wraps the default page generator with opinionated CP templates automatically included */
const SurfaceGenerator = ({
  customLayouts,
  customRenditions,
  customCanvases,
  children,
  page,
  pageName,
  ...otherProps
}: Omit<PageGeneratorProps, 'page'> &
  SurfaceGeneratorProps): JSX.Element | null => {
  const schemas = {
    ...schemaValues,
  };

  const schema = schemas?.[page];

  if (!schema) {
    throw Error('SurfaceGenerator: Unable to find schema');
  }

  return (
    <PageGenerator
      customLayouts={{
        ...customLayouts,
      }}
      customRenditions={{
        aside: SideNavRendition,
        ...customRenditions,
      }}
      customCanvases={{
        'secondary-nav': SideNavCanvas,
        domain: DomainCanvas,
        ...customCanvases,
      }}
      {...otherProps}
      page={{
        pageName: pageName,
        canvas: schema.canvas,
      }}
    >
      {children}
    </PageGenerator>
  );
};

export default SurfaceGenerator;
