import classNames from 'classnames';
import { createContext, FC, useCallback, useState } from 'react';

// Styles
import './DataUtilityWrapper.scss';

// Types
import {
  ActionState,
  ActionType,
  DataDefinition,
  DataUtilityWrapperConfig,
  Option,
  TestMetadata,
} from '../../../types';

export interface DataUtilityContextProps {
  dataDefinition?: DataDefinition[];
  actionState?: ActionState[];
  exportOptions?: Option[];
  isLoading?: boolean;
  isExporting?: boolean;
  setIsLoading?: (loading: boolean) => void;
  onActionStateChange?: (
    type: ActionType,
    newActionStates: ActionState[],
  ) => void;
  onExportOptionSelected?: (option: Option) => void;
}

export const DataUtilityContext = createContext<DataUtilityContextProps>({
  dataDefinition: [],
  actionState: [],
  isLoading: false,
});

export interface DataUtilityWrapperProps {
  dataUtilityWrapperConfig: DataUtilityWrapperConfig;
  onActionStateChange: (
    type: ActionType,
    newActionStates: ActionState[],
  ) => void;
  onExportOptionSelected?: (exportOption: Option) => void;
  initialIsLoading?: boolean;
  className?: string;
  testMetadata?: TestMetadata;
  isLoading?: boolean;
  isExporting?: boolean;
}

const DataUtilityWrapper: FC<DataUtilityWrapperProps> = ({
  dataUtilityWrapperConfig,
  onActionStateChange,
  onExportOptionSelected,
  testMetadata,
  initialIsLoading,
  children,
  className,
  isLoading,
  isExporting,
  ...otherProps
}) => {
  const [actionState, setActionState] = useState(
    dataUtilityWrapperConfig.actionState,
  );
  const [dataDefinition] = useState(dataUtilityWrapperConfig.dataDefinition);
  const [exportOptions] = useState(dataUtilityWrapperConfig.exportOptions);
  const [contextIsLoading, setContextIsLoading] = useState(initialIsLoading);

  const onContextActionStateChange = useCallback(
    (type: ActionType, newActionStates: ActionState[]): void => {
      // Filter out the current active states from that action type
      const currentActionStates =
        actionState
          ?.filter((state) => state.actionType !== type)
          .concat(newActionStates) ?? newActionStates;

      // Pass back to parent the type that was changed, and the new full set of action states
      onActionStateChange && onActionStateChange(type, currentActionStates);

      setActionState(currentActionStates);
    },
    [actionState, onActionStateChange],
  );

  const onContextExportOptionSelected = useCallback(
    (value: Option): void => {
      onExportOptionSelected && onExportOptionSelected(value);
    },
    [onExportOptionSelected],
  );

  return (
    <DataUtilityContext.Provider
      value={{
        dataDefinition: dataDefinition,
        actionState: actionState,
        exportOptions: exportOptions,
        onActionStateChange: onContextActionStateChange,
        onExportOptionSelected: onContextExportOptionSelected,
        isLoading: isLoading || contextIsLoading,
        setIsLoading: setContextIsLoading,
        isExporting: isExporting,
      }}
    >
      <div
        className={classNames('sb-data-utility-bar-wrapper', className)}
        {...otherProps}
        {...testMetadata}
      >
        {children}
      </div>
    </DataUtilityContext.Provider>
  );
};

export default DataUtilityWrapper;
