import { useCallback, useMemo } from 'react';
import classNames from 'classnames';

// Components
import { HeaderTemplate } from '../../Templates';
import EntityGrid, { EntityGridProps } from '../../EntityGrid';
import SearchBar, { SearchBarProps } from '../../Templates/SearchBar';
import {
  DualPanelLayout,
  PrimaryPanel,
  SecondaryPanel,
} from '../../../DualPanelLayout';
import Typography from '../../../Typography';
import { BodySection } from '../../../Sections';
import { ContentArea } from '../../../PageGenerator';
import { ComponentCanvas } from '../../../Canvases';
import SurfaceGenerator from '../../SurfaceGenerator';
import { GrayRendition, WhiteRendition } from '../../../Renditions';

// Types
import FacetSection, { FacetSectionProps } from './FacetSection';

export interface SearchSurfaceProps {
  // Facet Section
  facetSection?: FacetSectionProps;
  // Search Bar Config
  search: SearchBarProps;
  // Entity Grid Config
  grid: EntityGridProps;
  // Page Name
  pageName: string;
  // Title
  title: string;
  // Display body
  displayBody?: boolean;
  // Test Metadata
  testMetaData?: {
    headerSection: TestMetadata;
    bodySection: TestMetadata;
    primaryPanel: TestMetadata;
    secondaryPanel: TestMetadata;
  };
}

// Styles
import { TestMetadata } from 'types';

import './SearchSurface.scss';

const SearchSurface: React.FC<SearchSurfaceProps> = ({
  testMetaData,
  facetSection,
  displayBody,
  pageName,
  search,
  title,
  grid,
}: SearchSurfaceProps) => {
  const dualPanelClassNames = useMemo(
    () => classNames('sb-cp-search-surface__dual-panel-layout'),
    [],
  );

  const handleOnSearch = useCallback(
    (entity, query, attribute) => {
      search?.onSearch(entity, query, attribute);
    },
    [search],
  );

  return (
    <SurfaceGenerator page="basic" pageName={pageName}>
      <ContentArea name="header">
        <HeaderTemplate title={title} />
        <SearchBar {...search} onSearch={handleOnSearch} />
      </ContentArea>
      <ContentArea name="body">
        {displayBody ? (
          <DualPanelLayout
            areaMap={
              !facetSection
                ? { primary: 'primary', secondary: 'primary' }
                : undefined
            }
            className={dualPanelClassNames}
          >
            {facetSection?.sections.length ? (
              <SecondaryPanel
                testMetadata={{ ...testMetaData?.secondaryPanel }}
                className="sb-cp-dual-panel-canvas_secondary"
              >
                <GrayRendition fullSize>
                  <BodySection padding={['normal', 'loose', 'normal', 'loose']}>
                    <FacetSection {...facetSection} />
                  </BodySection>
                </GrayRendition>
              </SecondaryPanel>
            ) : null}
            <PrimaryPanel testMetadata={{ ...testMetaData?.primaryPanel }}>
              <WhiteRendition fullSize>
                <ComponentCanvas fullSize>
                  <EntityGrid {...grid} />
                </ComponentCanvas>
              </WhiteRendition>
            </PrimaryPanel>
          </DualPanelLayout>
        ) : (
          <Typography className="sb-cp-search-surface__hint" variant="T60">
            Enter search criteria above
          </Typography>
        )}
      </ContentArea>
    </SurfaceGenerator>
  );
};

export default SearchSurface;
