// Types
import { CellRendererFunc, PillboxListColumn } from '../types';
import { ICellRendererParams } from 'ag-grid-community';

// Styles
import './PillboxListRenderer.scss';

// Components
import Badge from '../../../Badge';
import Pillbox from '../../../Pillbox';
import Tooltip from '../../../Tooltip';

// Utils
import MasterDetailCell from '../MasterDetailCell/MasterDetailCell';
import { isEmpty, uniqueId } from 'lodash';
import { getCellValue } from './utils';
import React from 'react';

const PillboxListRenderedCell: React.FC<{
  params: ICellRendererParams<string[], string[]> & PillboxListColumn;
}> = ({
  params: {
    badgeProps,
    countBadgeProps,
    data,
    groupItems = false,
    appendTo = undefined,
  },
  params,
}) => {
  const valuesCount: Array<[string, number]> = React.useMemo(() => {
    const formattedValues = getCellValue(params) as string[];
    if (!groupItems) return formattedValues.map((value) => [value, 1]);

    const count: { [value: string]: number } = {};
    formattedValues.forEach((x) => (count[x] = (count[x] || 0) + 1));
    return Object.entries(count);
  }, [params, groupItems]);

  const valuesCountTitle: string = React.useMemo(() => {
    return valuesCount
      .map(([value, count]) => `${value}${count > 1 ? `(${count})` : ''}`)
      .join(', ');
  }, [valuesCount]);

  if (isEmpty(valuesCount)) {
    console.log('Return Null');
    return null;
  }

  return (
    <Tooltip tooltipContent={valuesCountTitle} tippyProps={{ appendTo }}>
      <div
        data-testid="StandardTable_Field_Pillbox-List"
        className="sb-standard-table_cell-pillbox-list"
      >
        {valuesCount.map(([value, count], index) => (
          <Pillbox
            key={`StandardTable_Field_Pillbox-List-Item-${uniqueId()}`}
            title={valuesCountTitle}
            color="primary"
            shape="pill"
            size="small"
            light
            showClose={false}
            className="sb-standard-table_cell-pillbox-list_item"
            testMetadata={{
              'data-testid': `StandardTable_Field_Pillbox-List-Item-${index}`,
            }}
            {...(badgeProps && badgeProps(data))}
          >
            {' '}
            <span
              data-testid={`StandardTable_Field_Pillbox-List-Item-Value-${index}`}
              className="sb-standard-table_cell-pillbox-list_item-value"
            >
              {value}
            </span>
            {count > 1 && (
              <Badge
                color="secondary"
                className="sb-standard-table_cell-pillbox-list_item-count"
                shape="circle"
                size="smaller"
                testMetadata={{
                  'data-testid': `StandardTable_Field_Pillbox-List-Count-${index}`,
                }}
                {...(countBadgeProps && countBadgeProps(data))}
              >
                {count}
              </Badge>
            )}
          </Pillbox>
        ))}
      </div>
    </Tooltip>
  );
};

const PillboxListRenderer: CellRendererFunc<
  ICellRendererParams<string[], string[]> & PillboxListColumn
> = (params) => {
  const { value } = params;
  if (!value) return null;

  if (params.isMasterColumn) {
    return (
      <MasterDetailCell
        gridAPI={params.api}
        node={params.node}
        onMasterDetailCellMouseOver={params.onMasterDetailCellMouseOver}
      >
        <PillboxListRenderedCell params={params} />
      </MasterDetailCell>
    );
  }

  return <PillboxListRenderedCell params={params} />;
};

export default PillboxListRenderer;
