import { ReactChild } from 'react';

/* This component is used to mark the top level children component with the "name" prop for use in page generator. 
Although you could use any element (say a div), typescript will complain if the element doesn't have that attribute to set. */
const ContentArea = ({
  children,
}: {
  name: string;
  children: ReactChild | ReactChild[];
}): JSX.Element => {
  return <>{children}</>;
};

export default ContentArea;
