// Types
import { CellRendererFunc, LinkColumn } from '../types';
import { ICellRendererParams } from 'ag-grid-community';

// Styles
import './LinkRenderer.scss';

// Components
import Link from '../../../Link';
import MasterDetailCell from '../MasterDetailCell/MasterDetailCell';

// Utils
import { getCellValue } from './utils';

const LinkRenderedCell: React.FC<{
  params: ICellRendererParams & LinkColumn;
}> = ({ params }) => {
  const onClick = params.onClick;
  const linkTitle = params.linkTitle;

  const cellValue = getCellValue(params);

  if (!cellValue) {
    return null;
  }

  return (
    <Link
      title={linkTitle}
      className="sb-standard-table_cell-nameLink"
      onClick={() => onClick && onClick(params.data)}
      testMetadata={{
        'data-testid': 'StandardTable_Field_Link',
      }}
    >
      {cellValue}
    </Link>
  );
};

const LinkRenderer: CellRendererFunc<LinkColumn> = (params) => {
  if (params.isMasterColumn) {
    return (
      <MasterDetailCell
        gridAPI={params.api}
        node={params.node}
        onMasterDetailCellMouseOver={params.onMasterDetailCellMouseOver}
      >
        <LinkRenderedCell params={params} />
      </MasterDetailCell>
    );
  }

  return <LinkRenderedCell params={params} />;
};

export default LinkRenderer;
