import React, { useEffect } from 'react';
import classNames from 'classnames';
import type { DraggableSyntheticListeners } from '@dnd-kit/core';
import type { Transform } from '@dnd-kit/utilities';

// Components
import { Handle, Remove } from '../components';
import Container from '../../Container';

// Style
import './DndItem.scss';

export interface Props {
  /**a boolean that indicates whether the item is currently being dragged over other items */
  dragOverlay?: boolean;
  /**a string that specifies the background color of the item */
  color?: string;
  /**a boolean that indicates whether the item is disabled */
  disabled?: boolean;
  /**a boolean that indicates whether the item is currently being dragged */
  dragging?: boolean;
  /**a boolean that indicates whether the item has a handle */
  handle?: boolean;
  /**an object that contains props to be passed down to the handle component */
  // eslint-disable-next-line @typescript-eslint/no-explicit-any
  handleProps?: any;
  /**a number that specifies the height of the item */
  height?: number;
  /**a number that specifies the index of the item in the list */
  index?: number;
  /**a boolean that indicates whether the item should fade in when it is added to the list */
  fadeIn?: boolean;
  /**an object that contains information about the transform of the item during drag and drop */
  transform?: Transform | null;
  /**an object that contains event listeners for the item during drag and drop */
  listeners?: DraggableSyntheticListeners;
  /**a boolean that indicates whether the list is currently being sorted */
  sorting?: boolean;
  /**an object that contains CSS styles for the item */
  style?: React.CSSProperties;
  /**a string that specifies the CSS transition for the item */
  transition?: string | null;
  /** an object that contains CSS styles for the wrapper element of the item */
  wrapperStyle?: React.CSSProperties;
  /**the content of the item */
  value: React.ReactNode;
  /**a function that is called when the item is removed from the list */
  onRemove?(): void;
  /**a function that returns a custom render of the item */
  renderItem?(args: {
    dragOverlay: boolean;
    dragging: boolean;
    sorting: boolean;
    index: number | undefined;
    fadeIn: boolean;
    listeners: DraggableSyntheticListeners;
    ref: React.Ref<HTMLElement>;
    style: React.CSSProperties | undefined;
    transform: Props['transform'];
    transition: Props['transition'];
    value: Props['value'];
  }): React.ReactElement;
}

export const DndItem = React.memo(
  React.forwardRef<HTMLDivElement, Props>(
    (
      {
        color,
        dragOverlay,
        dragging,
        disabled,
        fadeIn,
        handle,
        handleProps,
        index,
        listeners,
        onRemove,
        renderItem,
        sorting,
        style,
        transition,
        transform,
        value,
        wrapperStyle,
        ...props
      },
      ref,
    ) => {
      useEffect(() => {
        if (!dragOverlay) {
          return;
        }

        document.body.style.cursor = 'grabbing';

        return () => {
          document.body.style.cursor = '';
        };
      }, [dragOverlay]);

      return renderItem ? (
        renderItem({
          dragOverlay: Boolean(dragOverlay),
          dragging: Boolean(dragging),
          sorting: Boolean(sorting),
          index,
          fadeIn: Boolean(fadeIn),
          listeners,
          ref,
          style,
          transform,
          transition,
          value,
        })
      ) : (
        <Container
          className={classNames('sb-dnd-item-wrapper', {
            'sb-dnd-item-wrapper__fadeIn': fadeIn,
            'sb-dnd-item-wrapper__sorting': sorting,
            'sb-dnd-item-wrapper__dragOverlay': dragOverlay,
          })}
          style={
            {
              ...wrapperStyle,
              transition: [transition, wrapperStyle?.transition]
                .filter(Boolean)
                .join(', '),
              '--translate-x': transform
                ? `${Math.round(transform.x)}px`
                : undefined,
              '--translate-y': transform
                ? `${Math.round(transform.y)}px`
                : undefined,
              '--scale-x': transform?.scaleX
                ? `${transform.scaleX}`
                : undefined,
              '--scale-y': transform?.scaleY
                ? `${transform.scaleY}`
                : undefined,
              '--index': index,
              '--color': color,
            } as React.CSSProperties
          }
          containerRef={ref}
        >
          <div
            className={classNames('sb-dnd-item', {
              'sb-dnd-item__dragging': dragging,
              'sb-dnd-item__withHandle': handle,
              'sb-dnd-item__dragOverlay': dragOverlay,
              'sb-dnd-item__disabled': disabled,
              'sb-dnd-item__color': color,
            })}
            style={style}
            data-cypress="draggable-item"
            {...(!handle ? listeners : undefined)}
            {...props}
            tabIndex={!handle ? 0 : undefined}
          >
            {value}
            <span className="sb-dnd-item__actions">
              {onRemove ? (
                <Remove className="sb-dnd-item__remove" onClick={onRemove} />
              ) : null}
              {handle ? <Handle {...handleProps} {...listeners} /> : null}
            </span>
          </div>
        </Container>
      );
    },
  ),
);
