import { FC } from 'react';

// Components
import Button from '../Button';
import Status from './Status';

// Types
import { TestMetadata } from 'types';

export interface ExpectedDowntimeProps {
  appName?: string;
  header?: string;
  message?: string;
  expectedTimeBack?: Date;
  handleRefresh?: () => void;
  footer?: boolean;
  // Test IDs for component sections
  testMetadata?: TestMetadata;
}

const ExpectedDowntime: FC<ExpectedDowntimeProps> = ({
  appName,
  expectedTimeBack,
  header,
  testMetadata,
  message = 'This application is currently down for scheduled maintenance. Please come back and try again later. We apologize for any inconvenience.',
  handleRefresh,
  footer,
}) => {
  return (
    <>
      <h2>
        {header ||
          `Sorry, ${appName ? appName : 'this application'} is currently down.`}
      </h2>
      <p>{message}</p>
      {expectedTimeBack && (
        <p {...testMetadata}>
          {`The estimated time back is: ${
            expectedTimeBack.toLocaleDateString('en-us', {
              year: 'numeric',
              month: 'long',
              day: 'numeric',
            }) +
            ' ' +
            expectedTimeBack.toLocaleTimeString('en-us', {
              hour: 'numeric',
              minute: '2-digit',
            })
          }.`}
        </p>
      )}
      {handleRefresh && (
        <Button
          className="sb-error-template_action-button"
          labelText="Refresh"
          iconType="fa-sync"
          colorType="primary"
          onClick={handleRefresh}
        >
          Refresh page
        </Button>
      )}
      {footer && (
        <p className="sb-error-template_back-message">
          Keep seeing this message? Please come back and try again later.
        </p>
      )}
      <Status statusCode={503} />
    </>
  );
};

export default ExpectedDowntime;
