import React, { useCallback, useMemo, useState, useEffect } from 'react';

// Components
import MenuListSearch from '../../../MenuListSearch';
import PopoverOverlayTemplate from '../PopoverOverlayTemplate';
import PopoverButton from '../../../PopoverButton';

// Styles
import './ActionButton.scss';

// Types
import {
  ActionState,
  ActionType,
  DataDefinition,
  Option,
  TestMetadata,
} from '../../../types';

export interface DisplayActionButtonProps {
  dataDefinition?: DataDefinition[];
  actionState?: ActionState[];
  selectId?: string;
  testMetadata?: TestMetadata;
  isDisabled?: boolean;
  showSelectedPanel?: boolean;
  onCancel?: () => void;
  onActionStateChange?: (
    type: ActionType,
    newActionStates: ActionState[],
  ) => void;
}

const DisplayActionButton: React.FC<DisplayActionButtonProps> = ({
  isDisabled,
  dataDefinition,
  actionState,
  onActionStateChange,
  onCancel,
  testMetadata,
  showSelectedPanel = false,
}) => {
  const [options, setOptions] = useState<Option[]>([]);
  const [selected, setSelected] = useState<Option[]>([]);
  const [initialSelected, setInitialSelected] = useState<Option[]>([]);
  const [open, setOpen] = useState<boolean>();

  const handleCancel = useCallback(() => {
    setSelected(initialSelected);

    onCancel && onCancel();

    setOpen(false);
  }, [onCancel, initialSelected]);

  const handleChange = useCallback((e): void => {
    setSelected(e);
  }, []);

  const handleClear = useCallback(() => setSelected([]), []);

  const getMenuListOptions = (
    dataDefinition?: DataDefinition[],
    actionState?: ActionState[],
  ): Option[] => {
    const newOptionList: Option[] = [];
    const selectedOptionList: Option[] = [];

    if (dataDefinition) {
      // Loop over the definitions and add if hide is allowed and
      dataDefinition.map((val) => {
        if (val.displayDefinition?.allowHide) {
          const newOption = {
            label: val.label,
            value: val.field,
          };

          // If the definition is marked as visible, set it to selected by default
          val.displayDefinition?.visible && selectedOptionList.push(newOption);

          // Loop over the action states looking for any others that should be selected
          !val.displayDefinition?.visible &&
            actionState?.map((action) => {
              if (action.dataKey === val.dataKey)
                selectedOptionList.push(newOption);
            });

          newOptionList.push(newOption);
        }
      });

      setSelected(selectedOptionList);
      setInitialSelected(selectedOptionList);
    }

    return newOptionList;
  };

  const menuOptions = useMemo(
    () => getMenuListOptions(dataDefinition, actionState),
    // eslint-disable-next-line react-hooks/exhaustive-deps
    [dataDefinition],
  );

  const handleApply = useCallback(() => {
    const selectedActionStates: ActionState[] = [];

    menuOptions.map((val) => {
      selectedActionStates.push({
        actionType: ActionType.Display,
        dataKey: val.value,
        dataLabel: val.label,
        hidden: !selected.some((value) => value.value === val.value),
      });
    });

    onActionStateChange &&
      onActionStateChange(ActionType.Display, selectedActionStates);

    setOpen(false);
    setInitialSelected(selected);
  }, [onActionStateChange, selected, menuOptions]);

  const handlePopoverToggle = useCallback(
    (newState: boolean) => {
      setSelected(initialSelected);
      setOpen(newState);
    },
    [initialSelected],
  );

  useEffect(() => {
    const options = menuOptions
      .sort((a, b) => (a.label > b.label ? 1 : -1))
      .map((column) => ({
        label: column.label,
        value: column.value,
      }));

    setOptions(options);
  }, [menuOptions]);

  return (
    <>
      {options.length > 0 && (
        <PopoverButton
          isOpen={open}
          handleToggle={handlePopoverToggle}
          buttonText={`Display (${selected.length})`}
          buttonProps={{
            disabled: isDisabled,
          }}
          testMetadata={{
            button: testMetadata,
          }}
        >
          <PopoverOverlayTemplate
            title="Display"
            size="standard"
            onClear={handleClear}
            onCancel={handleCancel}
            onApply={handleApply}
            elevated={false}
            flush
          >
            <MenuListSearch
              options={options}
              onSelectOptions={handleChange}
              selectedOptions={selected}
              showSelectedPanel={showSelectedPanel}
            />
          </PopoverOverlayTemplate>
        </PopoverButton>
      )}
    </>
  );
};

export default DisplayActionButton;
