import { useContext } from 'react';

// Components
import SpellbookBasicCanvas from '../../../Canvases/BasicCanvas';

// Context
import { PageGeneratorContext } from '../../../PageGenerator';

// Types
import { CanvasTemplateProps } from '../../../PageGenerator/types';

// Styles
import './BasicCanvas.scss';

const BasicCanvas: React.FC<CanvasTemplateProps> = ({ sectionNames }) => {
  const context = useContext(PageGeneratorContext);
  if (context === undefined) {
    throw new Error('PageGeneratorContext is not defined');
  }

  return (
    <SpellbookBasicCanvas
      header={<>{context.utils.renderChildren(sectionNames?.['header'])}</>}
      testMetadata={{
        standardSection: {
          'data-testid': 'PageGenerator_BasicCanvas',
        },
      }}
    >
      <>{context.utils.renderChildren(sectionNames?.['body'])}</>
    </SpellbookBasicCanvas>
  );
};

export default BasicCanvas;
