import React, { useCallback, useEffect, useRef, useState } from 'react';

// Components
import BadgedContainer from '../../../BadgedContainer';
import Badge from '../../../Badge';
import Icon from '../../../Icon';
import Button from '../../../Button';

// Types
import { IHeaderParams } from 'ag-grid-community';

// Styles
import './DataGridHeader.scss';

const DataGridHeader: React.FC<IHeaderParams> = ({
  displayName,
  showColumnMenu,
  setSort,
  enableSorting,
  column,
}) => {
  const [ascSort, setAscSort] = useState('inactive');
  const [descSort, setDescSort] = useState('inactive');
  const [noSort, setNoSort] = useState('inactive');
  const [hasActiveFilter, setHasActiveFilter] = useState(false);
  const filterMenuRef = useRef(null);

  const onMenuClicked = useCallback(
    (e) => {
      e.stopPropagation();
      filterMenuRef?.current && showColumnMenu(filterMenuRef.current);
    },
    [showColumnMenu],
  );

  const onFilterChanged = useCallback((): void => {
    setHasActiveFilter(column.isFilterActive());
  }, [setHasActiveFilter, column]);

  const onSortChanged = useCallback(() => {
    setAscSort(column.isSortAscending() ? 'active' : 'inactive');
    setDescSort(column.isSortDescending() ? 'active' : 'inactive');
    setNoSort(
      !column.isSortAscending() && !column.isSortDescending()
        ? 'active'
        : 'inactive',
    );
  }, [setAscSort, setDescSort, setNoSort, column]);

  const onAscending = useCallback(
    (e) => {
      setSort('asc', e.shiftKey);
    },
    [setSort],
  );

  const onDescending = useCallback(
    (e) => {
      setSort('desc', e.shiftKey);
    },
    [setSort],
  );

  const onRevert = useCallback(
    (e) => {
      setSort(null, e.shiftKey);
    },
    [setSort],
  );

  const switchSort = useCallback(
    (e) => {
      if (enableSorting) {
        if (noSort === 'active') {
          onAscending(e);
        } else if (ascSort === 'active') {
          onDescending(e);
        } else {
          onRevert(e);
        }
      }
    },
    [noSort, ascSort, onAscending, onDescending, onRevert, enableSorting],
  );

  useEffect(() => {
    setHasActiveFilter(column.isFilterActive());
  }, [setHasActiveFilter, column]);

  useEffect(() => {
    column.addEventListener('sortChanged', onSortChanged);
    onSortChanged();

    return () => {
      column.removeEventListener('sortChanged', onSortChanged);
    };
  }, [column, onSortChanged]);

  useEffect(() => {
    column.addEventListener('filterChanged', onFilterChanged);

    return () => {
      column.removeEventListener('filterChanged', onFilterChanged);
    };
  }, [column, onFilterChanged]);
  if (column.getColId() === 'checkbox') {
    return <></>;
  }
  return (
    <div
      className="sb-cp-standard-table_header"
      onClick={switchSort}
      onTouchEnd={switchSort}
      data-testid="StandardTable_Header_Cell"
    >
      <div className="ag-header-cell-label" role="presentation">
        <div className="ag-header-cell-text">
          {displayName}
          {enableSorting && (
            <div
              className="sb-cp-standing-table_sorting-options"
              data-testid="StandardTable_Header_Sorting"
            >
              <span
                className={`ag-header-icon ${ascSort}`}
                aria-hidden="true"
                data-testid="StandardTable_Header_Sorting_Asc"
              >
                <Icon
                  className="sb-cp-standard-table_header_menu-asc"
                  faPackage="fas"
                  iconName="fa-arrow-up"
                  iconType="fa"
                  size="smaller"
                />
              </span>
              <span
                className={`ag-header-icon ag-sort-descending-icon ${descSort}`}
                aria-hidden="true"
                data-testid="StandardTable_Header_Sorting_Desc"
              >
                <Icon
                  className="sb-cp-standard-table_header_menu-desc"
                  faPackage="fas"
                  iconName="fa-arrow-down"
                  iconType="fa"
                  size="smaller"
                />
              </span>
            </div>
          )}
        </div>
        {column.getColDef()?.filter && (
          <div
            ref={filterMenuRef}
            className="sb-cp-standard-table_header-icon"
            onClick={onMenuClicked}
            data-testid="StandardTable_Header_Filtering"
          >
            <BadgedContainer
              className="sb-cp-standard-table_header-badge"
              badges={{
                component: (
                  <Badge
                    className={
                      hasActiveFilter
                        ? 'sb-cp-standard-table_header_menu-badge'
                        : 'sb-cp-standard-table_header_menu-hide-badge'
                    }
                    color="primary"
                    shape="dot"
                    size="small"
                    testMetadata={{
                      'data-testid': 'StandardTable_Header_FilterBadge',
                    }}
                  />
                ),
                position: 'top-right',
              }}
            >
              <Button
                iconType="fa-bars-filter"
                iconPackage="fa-light"
                sizeType="smaller"
                className="sb-cp-standard-table_header_menu-icon"
                labelText="Filter"
                iconOnly
                type="button"
              />
            </BadgedContainer>
          </div>
        )}
      </div>
    </div>
  );
};

export default DataGridHeader;
